<template>
  <div class="business-type-container">
    <div>
      
    </div>
    <div>
      <div class="business-btn">
        <el-button
          type="primary"
          v-permi="'/businessType/addType'"
          @click="showAddDialog = true"
        ></el-button>
        <!-- <el-button type="danger" :disabled="!selectedItem.length" @click="delBusiness"></el-button> -->
      </div>
      <el-table
        :data="tableData"
        tooltip-effect="dark"
        style="width: 100%;margin-top: 20px;"
        @selection-change="handleSelectionChange"
        border
        :fit="true"
      >
        <el-table-column type="selection" width="55" align="center" fixed></el-table-column>
        <el-table-column label="" prop="companyName" width align="center"></el-table-column>
        <el-table-column label="" prop="createTime" width align="center"></el-table-column>
      </el-table>
      <!-- {{tableData}} -->
      <!--  -->
      <div>
        <el-pagination
          class="itemPage"
          @size-change="sizeChangePage"
          @current-change="currentChangePage"
          :current-page="pageObj.pageNum"
          :page-sizes="[10, 20, 30, 40]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="pageObj.totalSize"
        ></el-pagination>

        <!--  -->
        <add-company
          @closeDialog="showAddDialog=false"
          @sureDialogFather="getTableData()"
          :showAddDialog="showAddDialog"
        ></add-company>
      </div>
    </div>
  </div>
</template>

<script>
import API from "@/api/business";
import AddCompany from "./components/AddCompany";

export default {
  name: "company",
  components: { AddCompany },
  data() {
    return {
      // 
      showAddDialog: false,
      // 
      showEditDialog: false,
      //
      tableData: [],
      // 
      selectedItem: [],
      // 
      pageObj: {
        pageNum: 1,
        pageSize: 10,
        totalSize: null
      },
      // 
      businessDetail: {}
    };
  },
  methods: {
    // 
    handleEdit(index, row) {
      this.businessDetail = row;
      this.showEditDialog = true;
    },
    // 
    sizeChangePage(e) {
      console.log(e);
      this.pageObj.pageSize = e;
      this.getTableData();
    },
    // 
    currentChangePage(e) {
      console.log(e);
      this.pageObj.pageNum = e;
      this.getTableData();
    },
    // 
    delBusiness() {
      this.$confirm("", "", {
        distinguishCancelAndClose: true,
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(() => {
          let arr = [];
          let selectedItem = this.selectedItem;
          for (const item of selectedItem) {
            arr.push(item.id);
          }
          let params = { ids: arr.join(",") };
          API.delType(params)
            .then(res => {
              console.log(res);
              this.$message({
                type: "success",
                message: ""
              });
              this.getTableData();
            })
            .catch(err => {
              console.error(err);
              if (err.indexOf("") === -1) this.$message.warning(err);
            });
        })
        .catch(err => {
          console.error(err);
          this.$message({
            type: "error",
            message: ""
          });
        });
    },
    // 
    handleSelectionChange(e) {
      this.selectedItem = e;
    },
    // 
    getTableData() {
      let params = {
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize
      };
      API.getCompanyList(params)
        .then(res => {
          let data = res.data;
          this.pageObj.totalSize = data.total;
          this.tableData = data.list;
        })
        .catch(err => {
          console.error(err);
          this.$message({
            type: "error",
            message: err
          });
        });
    }
  },
  mounted() {
    this.getTableData();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}
</style>
