// 
import fetch from '@/utils/fetch';
import Qs from 'qs'
export default {
    // get 
    // api
    getRegion(data) {
        return fetch({
            url: `area/all`,
            method: 'get',
            params: data
        })
    },
    // 
    // getProvince(data){
    //     return fetch({
    //         url:`area/findAllProvince`,
    //         method:'get',
    //         params:data
    //     })
    // },
    // 
    // getCity(data){
    //     return fetch({
    //         url:`area/findCity/${data}`,
    //         method:'get'
    //     })
    // },

    //
    getFollowList(data) {
        return fetch({
            url: `/follow/getFollowList`,
            method: 'get',
            params: data
        })
    },

    // id
    getCustomerFileById(data) {
        return fetch({
            url: `customer/findCustomerFileById/${data}`,
            method: 'get'
        })
    },
    // 
    getCreationTime(data) {
        return fetch({
            url: `customer/findCustomerTimeCode`,
            method: 'get',
            params: data
        })
    },
    // api
    getDepartments(data) {
        return fetch({
            url: `customer/findAllDepart`,
            method: 'get',
            params: data
        })
    },
    // 
    getAllUser(data) {
        return fetch({
            url: `sea/findAllUser`,
            method: 'get',
            params: data
        })
    },
    // id
    getHead(data) {
        return fetch({
            url: `customer/findAllDepartUser/${data}`,
            method: 'get'
        })
    },
    // id
    getDepartByUserId(data) {
        return fetch({
            url: `customer/findDepartByUserId/${data}`,
            method: 'get'
        })
    },
    // id
    getLinkmanDetails(data) {
        return fetch({
            url: `linkman/findLinkmanDetails/${data}`,
            method: 'get'
        })
    },
    //  
    getCustomerByCompanyName(data) {
        return fetch({
            url: `customer/findCustomerByCompanyName`,
            method: 'get',
            params: data
        })
    },
    // 
    getLinkmanById(data) {
        return fetch({
            url: `linkman/echoLinkmanById/${data}`,
            method: 'get'
        })
    },
    // post 
    // api
    postList(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `linkman/findAllLinkman?${data1}`,
            method: 'post'
        })
    },
    // api
    postdeleteLinkman(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `linkman/deleteLinkman?${data1}`,
            method: 'post'
        })
    },
    // 
    postaddLinkman(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `customer/addLinkman?${data1}`,
            method: 'post'
        })
    },
    // 
    postupdateLinkman(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `linkman/updateLinkman?${data1}`,
            method: 'post'
        })
    },
}