<template>
  <div>
    <el-dialog title="" :visible.sync="outerVisible" width="40%" @close="closeDialog">
      <el-form :model="form" :rules="rules" ref="form">
        <el-form-item
          label=""
          :rules="[{ required: true, message: '', trigger: 'blur' }]"
          :label-width="formLabelWidth"
          prop="opinion"
        >
          <el-input
            type="textarea"
            style="width:60%"
            placeholder=""
            v-model="form.opinion"
            :autosize="{ minRows: 4, maxRows: 10}"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="sureDialog" type="success"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/customer";

// 
export default {
  name: "ReturnSales",
  props: ["isShowSales", "seletedCustomer"],
  data() {
    return {
      outerVisible: this.isShowSales,
      formLabelWidth: "100px",
      form: {
        opinion: "",
        state: ""
      },
      allState: [
        { label: "", value: 0 },
        { label: "", value: 1 },
        { label: "", value: 2 },
        { label: "", value: 3 }
      ],
      rules: {
        opinion: [
          { required: true, message: "", trigger: "blur" }
        ],
        state: [
          { required: true, message: "", trigger: "change" }
        ]
      }
    };
  },
  methods: {
    // 
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather", this.outerVisible);
    },
    // 
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;

        let params = {
          opinion: this.form.opinion,
          customerId: this.seletedCustomer.id,
          clueId: this.seletedCustomer.clueId
        };
        API.returnCustomer(params)
          .then(res => {
            this.$message({
              message: "",
              type: "success"
            });
            this.$emit("sureDialogFather");
          })
          .catch(err => {
            if (err.indexOf("") === -1) this.$message.warning(err);
          });
      });
    }
  },
  mounted() {},
  watch: {
    isShowSales(value) {
      this.outerVisible = this.isShowSales;
    }
  }
};
</script>
<style lang='scss' scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>