<template>
  <div>
    <el-dialog
      title=""
      :close-on-click-modal="false"
      :visible.sync="outerVisible"
      width="40%"
      @close="closeDialog"
    >
      <el-form :model="form">
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="content"
          :rules="[{ required: true, message: '', trigger: 'blur' }]"
        >
          <el-input v-model="form.content" placeholder=""></el-input>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="startTime"
          :rules="[{ required: true, message: '', trigger: 'change' }]"
        >
          <el-date-picker
            v-model="form.startTime"
            :picker-options="startDatePicker"
            value-format="yyyy-MM-dd HH:mm:ss"
            type="datetime"
            placeholder=""
            class="item-css"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="form.type" placeholder="">
            <el-option
              v-for="item in repetitionList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-date-picker
            v-model="form.remindTime"
            value-format="yyyy-MM-dd HH:mm:ss"
            :picker-options="startDatePicker"
            type="datetime"
            placeholder=""
            class="item-css"
          ></el-date-picker>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          :remote-method="remoteMethod"
          prop="joinName"
        >
          <el-select v-model="form.joinName" multiple filterable placeholder="" class="item-css">
            <el-option
              v-for="item in participantList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="updateTask" type="success"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/customer";
import * as filters from "@/filters/index";

// 
export default {
  name: "NewTask",
  props: ["isEditTask", "taskDetail"],
  data() {
    return {
      outerVisible: this.isEditTask,
      startDatePicker: {
        // 
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7; //-8.64e6
        }
      },

      formLabelWidth: "110px",
      form: {
        id: "", // id
        content: "", // 
        startTime: "", // 
        type: "", // 
        remindTime: "", // 
        joinName: "", // 
        linkId: "" // id
      },
      // 
      repetitionList: [
        { name: "", id: 0 },
        { name: "", id: 1 },
        { name: "", id: 2 },
        { name: "", id: 3 },
        { name: "", id: 4 }
      ],
      // 
      participantList: []
    };
  },
  methods: {
    remoteMethod(query) {
      if (query !== "") {
        this.loading = true;
        setTimeout(() => {
          this.loading = false;
          this.options = this.list.filter(item => {
            return item.label.toLowerCase().indexOf(query.toLowerCase()) > -1;
          });
        }, 200);
      } else {
        this.options = [];
      }
    },
    // 
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather");
    },
    //
    updateTask() {
      let form = this.form;
      let params = {
        id: form.id,
        content: form.content,
        startTime: form.startTime,
        type: form.type,
        remindTime: form.remindTime,
        joinName: form.joinName.join(",")
      };
      console.log(params);

      API.postupdateTask(params)
        .then(res => {
          console.log(res);

          this.$message({
            type: "success",
            message: "!"
          });
          this.$emit("sureDialogFather");
          this.closeDialog();
        })
        .catch(err => {
          if (err.indexOf("") === -1) this.$message.warning(err);
        });
    }
  },
  watch: {
    isEditTask(value) {
      this.outerVisible = this.isEditTask;
      if (!value) return;

      let taskDetail = Object.assign({}, this.taskDetail);
      console.log(taskDetail);
      let form = taskDetail;
      // for (const key in taskDetail) {
      //   if (taskDetail.hasOwnProperty(key) && form.hasOwnProperty(key)) {
      //     form[key] = taskDetail[key];
      //   }
      // }
      form.joinName = taskDetail.joinName.split(",");
      this.form = form;

      API.getAllUser().then(res => {
        let arr = [];
        for (const item of res.data) {
          arr.push({
            label: item.name,
            value: item.name
          });
        }
        this.participantList = arr;
      });
    }
  }
};
</script>
<style lang='scss' scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input {
    width: auto;
  }
  // .el-input:not(.el-input--suffix) {
  //   width: 1%;
  // }
}
</style>