import fetch from '@/utils/fetch';

export default {
	// 
	findPackage(data) {
		return fetch({
			url: "packageManage/findPackage",
			method: "get",
			params: data
		})
	},

	// 
	addPackage(data) {
		return fetch({
			url: "packageManage/addPackage",
			method: "post",
			params: data
		})
	},

	// 
	delPackage(data) {
		return fetch({
			url: "packageManage/delPackage",
			method: "get",
			params: data
		})
	},

	// 
	updatePackage(data) {
		return fetch({
			url: "packageManage/updatePackage",
			method: "post",
			params: data
		})
	},

	// 
	updateState(data) {
		return fetch({
			url: "packageManage/updateState",
			method: "get",
			params: data
		})
	},

}