<template>
  <div>
    <!-- <el-button type="primary" icon="el-icon-plus" @click="outerVisible = true"></el-button> -->
    <el-dialog
      :close-on-click-modal="false"
      title=""
      :visible.sync="outerVisible"
      @close="closeDialog"
    >
      <el-form :model="form" :rules="rules" ref="form" label-width="100px">
        <el-form-item label="" prop="name">
          <el-input placeholder="" v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="form.type" placeholder="" clearable class="item-css">
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="isFree">
          <el-select v-model="form.isFree" placeholder="" clearable class="item-css">
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button @click="sureDialog" type="success"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/ditch";

// 
export default {
  name: "EditChannel",
  props: ["isShowEdit", "channelDetail"],
  data() {
    return {
      outerVisible: this.isShowEdit,
      form: {
        name: "",
        type: "",
        isFree: ""
      },
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        type: [
          { required: true, message: "", trigger: "change" }
        ],
        isFree: [
          { required: true, message: "", trigger: "change" }
        ]
      }
    };
  },
  methods: {
    // 
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather", this.outerVisible);
    },
    // 
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        console.log(this.form);
        let params = this.form;
        API.updateChannel(params)
          .then(res => {
            console.log(res);
            this.$message({
              message: "",
              type: "success"
            });
            this.closeDialog();
            this.$emit("sureDialogFather");
          })
          .catch(err => {
            if (err.indexOf("") === -1) this.$message.warning(err);
          });
      });
    }
  },
  mounted() {},
  watch: {
    isShowEdit(newVal) {
      this.outerVisible = this.isShowEdit;
      if (!newVal) return;
      let channelDetail = Object.assign({}, this.channelDetail);
      this.$nextTick(() => {
        this.$refs.form.resetFields();
        this.form = channelDetail;
      });
    }
  }
};
</script>
<style lang='scss' scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>