<template>
  <div>
    <el-dialog
      title=""
      :close-on-click-modal="false"
      :visible.sync="dialogVisible"
      @close="closeDialog"
    >
      <el-form :model="form" label-width="120px" ref="form" :rules="rules">
        <el-form-item label="" prop="name">
          <el-input v-model="form.name" placeholder=""></el-input>
        </el-form-item>
        <!-- {{form.customerId}} -->
        <el-form-item label="" prop="customerId">
          <el-select
            v-model="form.customerId"
            placeholder=""
            :disabled="Boolean(customerDetail)"
            class="item-css"
            filterable
          >
            <el-option
              v-for="(item, index) in restaurants"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="totalPrice">
          <el-input
            @blur="$message.warning('')"
            v-model="form.totalPrice"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="signDate">
          <el-date-picker
            v-model="form.signDate"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="signNo">
          <el-input v-model="form.signNo" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="serviceId">
          <el-select v-model="form.serviceId" placeholder="" clearable>
            <el-option
              v-for="(item, i) in businessTypeList"
              :key="i"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="setMeal">
          <el-select v-model="form.setMeal" placeholder="" clearable>
            <el-option
              v-for="(item, i) in PackageList"
              :key="i"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="from">
          <el-select v-model="form.from" placeholder="" clearable>
            <el-option
              v-for="(item, i) in clientSourceList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="ladingBillTime">
          <el-date-picker
            v-model="form.ladingBillTime"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="endTime">
          <el-date-picker
            v-model="form.endTime"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select v-model="form.status" placeholder="" clearable>
            <el-option
              v-for="(item, i) in contractstatusList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="payNo">
          <el-select v-model="form.payNo" placeholder="" clearable>
            <el-option
              v-for="(item, i) in payaccountList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-upload
            ref="upload"
            :action="$store.state.baseURL+'annex/addAnnex'"
            :file-list="fileList"
            :data="uploadData"
            :auto-upload="false"
            :on-success="uploadSuccess"
            :on-error="uploadError"
          >
            <el-button class="item" type="success" icon="el-icon-plus"></el-button>
          </el-upload>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="form.nextFollowTime"
            type="datetime"
            :picker-options="startDatePicker"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
        <!-- <el-form-item label="">
          <el-button type="primary" icon="el-icon-plus"></el-button>
        </el-form-item>-->
        <el-form-item label="">
          <el-input
            style="width:60%"
            type="textarea"
            show-word-limit
            maxlength="100"
            v-model="form.remarks"
            rows="4"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.intention" placeholder="" clearable>
            <el-option
              v-for="(item, i) in intentionList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="receiptNo">
          <el-input v-model="form.receiptNo" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="isInvoice">
          <el-select v-model="form.isInvoice" placeholder="" clearable>
            <el-option label="" value="1"></el-option>
            <el-option label="" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="incomeType">
          <el-select v-model="form.incomeType" placeholder="" clearable>
            <el-option
              v-for="(item, i) in sessionTypeList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="payment">
          <el-input v-model="form.payment" @blur="$message.warning('')"></el-input>
        </el-form-item>

        <el-form-item label="" prop="receiptNumber">
          <el-input v-model="form.receiptNumber" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="departId">
          <el-select v-model="form.departId" placeholder="" clearable @change="departChange()">
            <el-option
              v-for="(item, index) in departmentsList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="userId">
          <el-select v-model="form.userId" @change="userChange" filterable placeholder="">
            <el-option
              v-for="(item,index) in headList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button @click="sureDialog" type="success"></el-button>
          <el-button @click="closeDialog"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
// 
import API from "@/api/contract";
export default {
  name: "NewContract",
  props: ["isShowContract", "customerDetail"],
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (Number(value) > Number(this.form.totalPrice)) {
        callback(new Error(""));
      } else {
        // this.$message.warning("");
        callback();
      }
    };
    return {
      startDatePicker: {
        // 
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7; //-8.64e6
        }
      },
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        customerId: [
          { required: true, message: "", trigger: "change" }
        ],
        totalPrice: [
          { required: true, message: "", trigger: "blur" }
        ],
        signDate: [
          { required: true, message: "", trigger: "change" }
        ],
        signNo: [
          { required: true, message: "", trigger: "blur" }
        ],
        serviceId: [
          { required: true, message: "", trigger: "change" }
        ],
        setMeal: [
          { required: true, message: "", trigger: "change" }
        ],

        telephone: [
          { required: true, message: "", trigger: "blur" }
        ],

        status: [
          { required: true, message: "", trigger: "change" }
        ],
        // ladingBillTime: [
        //   { required: true, message: "", trigger: "change" }
        // ],
        // endTime: [
        //   { required: true, message: "", trigger: "change" }
        // ],
        payNo: [
          { required: true, message: "", trigger: "change" }
        ],

        from: [
          { required: true, message: "", trigger: "change" }
        ],
        isInvoice: [
          { required: true, message: "", trigger: "change" }
        ],
        incomeType: [
          { required: true, message: "", trigger: "change" }
        ],
        receiptNumber: [
          { required: true, message: "", trigger: "blur" }
        ],
        departId: [
          { required: true, message: "", trigger: "change" }
        ],
        payment: [{ required: true, validator: validatePass, trigger: "blur" }],
        userId: [{ required: true, message: "", trigger: "change" }]
      },
      dialogVisible: this.isShowContract,
      restaurants: [], // 
      businessTypeList: [], // 
      PackageList: [], // 
      clientSourceList: [], // 
      contractstatusList: [], // 
      payaccountList: [], // 
      intentionList: [], // 
      sessionTypeList: [], // 
      departmentsList: [], // 
      headList: [], // 
      fileList: [], //
      uploadData: {},
      form: {
        name: "", // 
        customerId: "", // 
        totalPrice: "", // 
        signDate: "", // 
        signNo: "", // 
        serviceId: "", // 
        setMeal: "", // 
        from: "", // 
        ladingBillTime: "", // 
        endTime: "", // 
        status: "", // 
        payNo: "", // 
        nextFollowTime: "", // 
        remarks: "", // 
        intention: "", // 
        receiptNo: "", // 
        isInvoice: "", // 
        incomeType: "", // 
        payment: "", // 
        companyName: "", // 
        receiptNumber: "", // 
        departId: "", // 
        userId: "" // 
      }
    };
  },
  methods: {
    uploadSuccess(res) {
      if (res.status != 200) {
        this.$notify({
          title: "",
          message: " " + res.msg + " ",
          type: "warning"
        });
      }
      this.fileList = [];
    },
    uploadError() {
      this.$message({
        type: "error",
        message: ""
      });
    },
    // 
    departChange(flag) {
      if (flag !== "no_change") this.form.userId = null;
      let val = this.form.departId;
      if (val) {
        API.getHead(val).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      }
    },
    // id
    userChange(val) {
      let departId = this.headList.find(item => {
        return item.id === val;
      }).departId;
      this.form.departId = departId;
      this.departChange("no_change");
    },

    // 
    closeDialog() {
      this.dialogVisible = false;

      this.$emit("closeDialogFather");
    },
    // 
    sureDialog() {
      this.$refs.form.validate(vali => {
        if (!vali) return;
        let form = this.form;
        this.uploadData = {
          linkId: form.customerId,
          linkType: 1
        };
        let params = {
          name: form.name,
          customerId:form.customerId,
          totalPrice: form.totalPrice,
          signDate: form.signDate,
          signNo: form.signNo,
          serviceId: form.serviceId,
          setMeal: form.setMeal,
          from: form.from,
          telephone: form.telephone,
          ladingBillTime: form.ladingBillTime,
          endTime: form.endTime,
          status: form.status,
          payNo: form.payNo,
          nextFollowTime: form.nextFollowTime,
          remarks: form.remarks,
          intention: form.intention,
          receiptNo: form.receiptNo,
          isInvoice: form.isInvoice,
          incomeType: form.incomeType,
          payment: form.payment,
          // companyName: form.companyName,
          receiptNumber: form.receiptNumber,
          departId: form.departId,
          userId: form.userId
        };
        API.postaddContract(params).then(res => {
          this.$message({
            message: "",
            type: "success"
          });
          this.$refs.upload.submit();
          this.$refs.form.resetFields();
          this.closeDialog();
          this.$emit("sureDialogFather");
        });
      });
    }
  },
  watch: {
    isShowContract(value) {
      this.dialogVisible = this.isShowContract;
      if (!value) return;
      // this.$nextTick(() => {
      //   this.$refs.form.clearValidate();
      // });

      console.log(this.customerDetail);
      if (this.customerDetail) {
        let customerDetail = this.customerDetail;
        this.form = Object.assign(this.form, {
          isShowContract: customerDetail.serviceId,
          companyName: customerDetail.companyName,
          customerId: this.$route.query.id
        });
      }
      let userInfo = this.$store.state.userInfo;

      this.form.departId = userInfo.departId;
      this.form.userId = userInfo.id;
      this.departChange("no_change");
      // 
      API.getCustomerByCompanyName().then(res => {
        this.restaurants = res.data;
      });
      // 
      API.getType().then(res => {
        this.businessTypeList = res.data;
      });
      // 
      API.getPackageTypes().then(res => {
        this.PackageList = res.data;
      });
      //  
      API.customerFrom().then(res => {
        this.clientSourceList = res.data;
      });
      // 
      API.getContractStatus().then(res => {
        this.contractstatusList = res.data;
      });
      // 
      API.getContractPayNo().then(res => {
        this.payaccountList = res.data;
      });
      // 
      API.getContractIntention().then(res => {
        this.intentionList = res.data;
      });
      // 
      API.getContractIncomeType().then(res => {
        this.sessionTypeList = res.data;
      });
      // 
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
    }
  }
};
</script>
<style scoped lang="scss">
/deep/.el-dialog {
  width: 40%;
  .inline-input {
    width: 100%;
  }
  .item-css {
    width: 50%;
  }
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>