<template>
  <div>
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      @close="closeDialog"
    >
      <el-form :model="form" label-width="120px" :rules="rules" ref="form">
        <el-form-item
          label=""
          prop="name"
          :rules="[{ required: true, message: '', trigger: 'blur' }]"
        >
          <el-input v-model="form.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="customerId">
          <el-select
            v-model="form.customerId"
            placeholder=""
            class="item-css"
            disabled
            filterable
          >
            <el-option
              v-for="(item, index) in restaurants"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="totalPrice">
          <el-input v-model="form.totalPrice" disabled placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="signDate">
          <el-date-picker
            v-model="form.signDate"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder=""
            class="item-css"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="signNo">
          <el-input v-model="form.signNo" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="serviceId">
          <el-select v-model="form.serviceId" placeholder="" clearable class="item-css">
            <el-option
              v-for="(item, i) in businessTypeList"
              :key="i"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="setMeal">
          <el-select v-model="form.setMeal" placeholder="" clearable class="item-css">
            <el-option
              v-for="(item, i) in PackageList"
              :key="i"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="from">
          <el-select v-model="form.from" placeholder="" clearable class="item-css">
            <el-option
              v-for="(item, i) in clientSourceList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="form.ladingBillTime"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder=""
            class="item-css"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="form.endTime"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder=""
            class="item-css"
          ></el-date-picker>
        </el-form-item>

        <el-form-item label="" prop="status">
          <el-select v-model="form.status" placeholder="" clearable class="item-css">
            <el-option
              v-for="(item, i) in contractStatusList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.payNo" placeholder="" clearable class="item-css">
            <el-option
              v-for="(item, i) in payaccountList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="form.nextFollowTime"
            type="datetime"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder=""
            class="item-css"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-input
            type="textarea"
            :autosize="{ minRows: 4, maxRows: 10}"
            style="width:60%"
            maxlength="100"
            v-model="form.remarks"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.intention" placeholder="" clearable class="item-css">
            <el-option
              v-for="(item, i) in intentionList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.receiptNo" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.isInvoice" placeholder="" clearable class="item-css">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.incomeType" placeholder="" clearable class="item-css">
            <el-option
              v-for="(item, i) in sessionTypeList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input disabled v-model="form.payment" placeholder=""></el-input>
        </el-form-item>

        <el-form-item label="" prop="receiptNumber">
          <el-input v-model="form.receiptNumber" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="sureDialog" type="success"></el-button>
        <el-button @click="closeDialog"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// 
import API from "@/api/contract";
export default {
  name: "NewContractEdit",
  //          idID
  props: [
    "isShowContractEdit",
    "contractId",
    "contractStatusList",
    "customerId"
  ],
  data() {
    return {
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        customerId: [
          { required: true, message: "", trigger: "change" }
        ],
        totalPrice: [
          { required: true, message: "", trigger: "blur" }
        ],
        signDate: [
          { required: true, message: "", trigger: "change" }
        ],
        signNo: [
          { required: true, message: "", trigger: "blur" }
        ],
        serviceId: [
          { required: true, message: "", trigger: "change" }
        ],
        setMeal: [
          { required: true, message: "", trigger: "change" }
        ],
        receiptNumber: [
          { required: true, message: "", trigger: "blur" }
        ],
        from: [
          { required: true, message: "", trigger: "change" }
        ],
        telephone: [
          { required: true, message: "", trigger: "blur" }
        ],
        payNo: [
          { required: true, message: "", trigger: "change" }
        ],
        status: [
          { required: true, message: "", trigger: "change" }
        ]
      },
      dialogVisible: this.isShowContractEdit,
      formLabelWidth: "100px",
      restaurants: [], // 
      businessTypeList: [], // 
      PackageList: [], // 
      clientSourceList: [], // 
      payaccountList: [], // 
      intentionList: [], // 
      sessionTypeList: [], // 
      departmentsList: [], // 
      headList: [], // 
      fileList: [
        //
      ],
      form: {}
    };
  },
  methods: {

    // 
    depart(val) {
      this.departId = val;
      if (val != null) {
        API.getHead(val).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      }
      this.userId = null;
    },
    // 
    closeDialog() {
      this.dialogVisible = false;
      this.$emit("closeDialogFather", this.dialogVisible);
    },
    // 
    sureDialog() {
      this.$refs.form.validate(vali => {
        if (!vali) return;
        let form = this.form;

        let params = {
          id: this.contractId,
          name: form.name,
          customerId: form.customerId,
          totalPrice: form.totalPrice,
          signDate: form.signDate,
          signNo: form.signNo,
          serviceId: form.serviceId,
          setMeal: form.setMeal,
          from: form.from,
          telephone: form.telephone,
          searchWords: form.searchWords,
          ladingBillTime: form.ladingBillTime,
          endTime: form.endTime,
          status: form.status,
          payNo: form.payNo,
          nextFollowTime: form.nextFollowTime,
          remarks: form.remarks,
          intention: form.intention,
          receiptNo: form.receiptNo,
          isInvoice: form.isInvoice,
          incomeType: form.incomeType,
          payment: form.payment,
          companyName: form.companyName,
          receiptNumber: form.receiptNumber
          // departId: this.departId,
          // userId: this.userId
        };
        console.log(params);

        API.postupdateContract(params).then(res => {
          console.log(res);
          if (res.status == 200) {
            this.$message(
              {
                message: "",
                type: "success"
              },
              
            );
            this.$emit("sureDialogFather");
          }
        });
      });
    }
  },
  watch: {
    isShowContractEdit(value) {
      this.dialogVisible = this.isShowContractEdit;
      if (!value) return;
      Promise.all([
        API.getCustomerByCompanyName(),
        API.getAllUser(),
        API.getfindContractById(this.contractId)
      ]).then(resArr => {
        // 
        let restaurants = resArr[0].data;
        restaurants.map(item => {
          item.value = item.name;
        });
        this.restaurants = restaurants;

        // 
        let headList = resArr[1].data;
        this.headList = headList;

        // 
        let formData = resArr[2].data;
        formData.regionArr = [formData.province, formData.city];

        this.form = formData;
        this.$nextTick(() => {
          this.$refs.form.clearValidate();
        });
      });
      // 
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
      // 
      API.getType().then(res => {
        this.businessTypeList = res.data;
      });
      // 
      API.getPackageTypes().then(res => {
        this.PackageList = res.data;
      });
      //  
      API.customerFrom().then(res => {
        this.clientSourceList = res.data;
      });

      // 
      API.getContractPayNo().then(res => {
        this.payaccountList = res.data;
      });
      // 
      API.getContractIntention().then(res => {
        this.intentionList = res.data;
      });
      // 
      API.getContractIncomeType().then(res => {
        this.sessionTypeList = res.data;
      });
    }
  }
};
</script>
<style scoped lang="scss">
/deep/.el-dialog {
  width: 40%;
  .inline-input {
    width: 100%;
  }
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>