<template>
  <div>
    <el-dialog
      :close-on-click-modal="false"
      title=""
      :visible.sync="outerVisible"
      class="lodings"
      @close="closeDialog"
    >
      <el-scrollbar style="height:100%" ref="myScrollbar">
        <el-form label-width="110px" :model="ruleForm" :rules="rules" ref="ruleForm">
          <el-form-item label="" prop="guest">
            <el-input v-model="ruleForm.guest" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="companyName">
            <el-input v-model="ruleForm.companyName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="serviceType">
            <el-select
              v-model="ruleForm.serviceType"
              placeholder=""
              clearable
              class="item-css"
            >
              <el-option
                v-for="(item, i) in businessTypeList"
                :key="i"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" prop="grade">
            <el-select v-model="ruleForm.grade" placeholder="" clearable class="item-css">
              <el-option
                v-for="(item,i) in getCustomerLevel"
                :key="i"
                :label="item.val"
                :value="item.code"
              ></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" prop="status">
            <el-select v-model="ruleForm.status" placeholder="" clearable class="item-css">
              <el-option
                v-for="(item, index) in customerType"
                :key="index"
                :label="item.val"
                :value="item.code"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="telnum">
            <el-input v-model="ruleForm.telnum" placeholder=""></el-input>
          </el-form-item>

          <!-- <el-form-item label="" prop="registerName">
                <el-input v-model="ruleForm.registerName" placeholder=""></el-input>
          </el-form-item>-->

          <el-form-item label="" prop="nextFollwTime">
            <el-date-picker
              value-format="yyyy-MM-dd HH:mm:ss"
              v-model="ruleForm.nextFollwTime"
              :picker-options="startDatePicker"
              type="datetime"
              placeholder=""
            ></el-date-picker>
          </el-form-item>

          <el-form-item label="" prop="isWechat">
            <el-select v-model="ruleForm.isWechat" placeholder="" clearable class="item-css">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="isFace">
            <el-select v-model="ruleForm.isFace" placeholder="" clearable class="item-css">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="saleKeywords">
            <el-input v-model="ruleForm.saleKeywords" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="keywords">
            <el-input v-model="ruleForm.keywords" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model="ruleForm.email" placeholder=""></el-input>
          </el-form-item>
          <!-- <el-form-item label="QQ" prop="qq">
                <el-input v-model="ruleForm.qq" placeholder=""></el-input>
              </el-form-item>
              <el-form-item label="" prop="wechat">
                <el-input v-model="ruleForm.wechat" placeholder=""></el-input>
          </el-form-item>-->
          <el-form-item label="" prop="staffSize">
            <el-input v-model="ruleForm.staffSize" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="followDepart">
            <el-select
              v-model="ruleForm.followDepart"
              placeholder=""
              @change="departChange"
              class="item-css"
            >
              <el-option
                v-for="(item, index) in departmentsList"
                :key="index"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="followUser">
            <el-select
              v-model="ruleForm.followUser"
              no-data-text=""
              placeholder=""
              class="item-css"
            >
              <el-option
                v-for="(item, index) in headuserList"
                :key="index"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="remarks" label="">
            <el-input type="textarea" style="width:60%" v-model="ruleForm.remarks"></el-input>
          </el-form-item>
          <el-form-item prop="opinion" label="">
            <el-input type="textarea" style="width:60%" :maxlength="100" v-model="ruleForm.opinion"></el-input>
          </el-form-item>

          <el-form-item label="" prop="linkName">
            <el-input v-model="ruleForm.linkName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="linkTel">
            <el-input disabled v-model="ruleForm.linkTel" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button @click="sureDialog" type="success"></el-button>
            <el-button @click="closeDialog"></el-button>
          </el-form-item>
        </el-form>
      </el-scrollbar>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/clue";
import * as filters from "@/filters/index";

// 
export default {
  name: "customer",
  //---------- ---------
  props: ["isShowCustomer", "clueDetail"],
  data() {
    return {
      outerVisible: this.isShowCustomer,
      businessTypeList: [],
      ruleForm: {},
      rules: {
        guest: [{ required: true, message: "", trigger: "blur" }],
        companyName: [
          { required: true, message: "", trigger: "blur" }
        ],
        registerName: [
          { required: true, message: "", trigger: "blur" }
        ],
        linkName: [
          { required: true, message: "", trigger: "blur" }
        ],
        status: [
          { required: true, message: "", trigger: "change" }
        ],
        // staffSize: [
        //   { required: true, message: "", trigger: "blur" }
        // ],
        linkTel: [
          { required: true, message: "", trigger: "blur" }
        ],
        // telnum: [
        //   { required: true, message: "", trigger: "blur" }
        // ],
        serviceType: [
          { required: true, message: "", trigger: "change" }
        ],
        // isFace: [
        //   { required: true, message: "", trigger: "change" }
        // ],
        grade: [
          { required: true, message: "", trigger: "change" }
        ],
        // keywords: [
        //   { required: true, message: "", trigger: "blur" }
        // ],
        followDepart: [
          { required: true, message: "", trigger: "change" }
        ],
        followUser: [
          { required: true, message: "", trigger: "change" }
        ]
      },
      headuserList: [], // 
      getCustomerLevel: [], // 
      getRegion: [], //  
      customerSource: [], // 
      customerType: [], // 
      departmentsList: [], // 
      startDatePicker: {
        // 
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7; //-8.64e6
        }
      }
    };
  },
  methods: {
    // 
    departChange(val) {
      API.getHead(val)
        .then(res => {
          this.$set(this.ruleForm, "followUser", "");
          this.headuserList = res.data;
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 
    handldiqu(val) {
      this.ruleForm.province = val[0];
      this.ruleForm.city = val[1];
      console.log(this.ruleForm.province);
      console.log(this.ruleForm.city);
    },
    // 
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather");
    },
    // 
    sureDialog() {
      this.$refs.ruleForm.validate(valid => {
        if (!valid) return;
        let ruleForm = this.ruleForm;
        let params = {
          guest: ruleForm.guest,
          companyName: ruleForm.companyName,
          businessType: ruleForm.serviceType,
          grade: ruleForm.grade,
          opinion: ruleForm.opinion,
          status: ruleForm.status,
          telnum: ruleForm.telnum,
          // registerName: ruleForm.registerName,
          nextFollwTime: ruleForm.nextFollwTime,
          isWechat: ruleForm.isWechat,
          isFace: ruleForm.isFace,
          keyWords: ruleForm.keywords,
          saleKeywords: ruleForm.saleKeywords,
          staffSize: ruleForm.staffSize,
          email: ruleForm.email,
          followDepart: ruleForm.followDepart,
          followUser: ruleForm.followUser,
          remarks: ruleForm.remarks,
          linkName: ruleForm.linkName,
          linkTel: ruleForm.linkTel,
          clueId: ruleForm.id
        };
        // params.clueId = params.id;
        console.log(params);

        API.toCustomer(params)
          .then(res => {
            console.log(res);
            this.$message({
              message: "",
              type: "success"
            });
            this.closeDialog();
            this.$emit("sureDialogFather");
          })
          .catch(err => {
            if (err.indexOf("") === -1) this.$message.warning(err);

            console.error(err);
          });
      });
    }
  },
  watch: {
    isShowCustomer(value) {
      this.outerVisible = this.isShowCustomer;
      if (!value) return;
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
      // api
      API.getCustomerType().then(res => {
        this.customerType = res.data;
      });
      // 
      API.getCustomerLevel().then(res => {
        this.getCustomerLevel = res.data;
      });
      API.getRegion().then(res => {
        res.data.forEach(item => {
          item.value = item.label;
          if (item.children.length == "0") {
            item.children = "";
          } else {
            item.children.forEach(item1 => {
              item1.value = item1.label;
            });
          }
        });
        this.getRegion = res.data;
      });
      let clueDetail = JSON.parse(JSON.stringify(this.clueDetail));
      API.getClueInfo({
        id: this.clueDetail.id
      })
        .then(res => {
          let data = res.data;
          data.status = 1;
          data.regionArr = [data.province, data.city];
          data.linkTel = data.telnum;
          data.telnum = "";
          let userInfo = this.$store.state.userInfo;
          data.followDepart = userInfo.departId;
          data.followUser = userInfo.id;
          this.ruleForm = data;
          console.log(this.ruleForm);
          // 
          API.getType().then(res => {
            this.businessTypeList = res.data;
          });
          API.getHead(data.followDepart)
            .then(res => {
              this.headuserList = res.data;
              this.$refs.ruleForm.clearValidate();
            })
            .catch(err => {
              console.error(err);
            });
        })
        .catch(err => {
          if (err.indexOf("") === -1) this.$message.warning(err);
        });
    }
  }
};
</script>
<style scoped lang="scss">
/deep/.el-dialog {
  width: 40%;
  padding: 0 20px;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>