<template>
  <div>
    <el-form label-width="120px" v-if="flag">
      <el-row>
        <el-col :span="24" class="list">
          <el-form-item label=" : ">
            <ul>
              <li
                v-for="(item, index) in creationTime"
                :key="index"
                @click="showStyle(index, item.code)"
                :class="timeActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
              <!-- <li
                @click="showDefinedTime = true;timeActive = -1"
                class="list-li"
                v-if="!showDefinedTime"
              ></li>
              <li v-else>
                <el-date-picker
                  v-model="form.definedTime"
                  @change="timeCustom"
                  type="daterange"
                  value-format="yyyy-MM-dd"
                  range-separator=""
                  start-placeholder=""
                  end-placeholder=""
                ></el-date-picker>
              </li>-->
            </ul>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-col :span="8">
            <el-form-item label="">
              <el-select
                v-model="form.department"
                placeholder=""
                clearable
                class="item-css"
                @change="depart($event)"
              >
                <el-option
                  v-for="(item, index) in departmentsList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>

          <el-col :span="8">
            <el-form-item label="">
              <el-select
                v-model="form.followUser"
                placeholder=""
                clearable
                :no-data-text="form.department?'':''"
                class="item-css"
                @change="tableApi()"
              >
                <el-option
                  v-for="(item, index) in headList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-col>
        <el-col :span="8">
          <el-form-item label=" : ">
            <el-cascader
              :options="options"
              placeholder=""
              v-model="form.regionArr"
              class="item-css"
              clearable
              @change="tableApi()"
            ></el-cascader>
          </el-form-item>
        </el-col>

        <el-col :span="10">
          <el-form-item label="" class="input-search">
            <el-col :span="14">
              <el-input
                v-model="form.companyName"
                @clear="tableApi()"
                placeholder=""
                clearable
              ></el-input>
            </el-col>
            <el-col :span="6">
              <el-button type="primary" @click="tableApi()"></el-button>
            </el-col>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <!-- <el-row>
      <el-col :span="24" style="font-size:20px;text-align:center;">
        <i
          style="cursor: pointer;"
          :class="flag == true? 'el-icon-arrow-up' : 'el-icon-arrow-down'"
          @click="show"
        ></i>
      </el-col>
    </el-row>-->
    <el-row style="margin-top:20px;">
      <!--  -->
      <el-popover placement="top-start" width="300" trigger="click">
        <el-checkbox-group v-model="showCol" @change="colChange">
          <el-checkbox
            style="width:120px;line-height:25px"
            v-for="(item,index) in colOptions"
            :key="index"
            :label="item"
          ></el-checkbox>
        </el-checkbox-group>
        <el-button
          slot="reference"
          style="margin-right:10px;"
          type="primary"
          icon="el-icon-s-operation"
          plain
        ></el-button>
      </el-popover>
      <el-button
        type="primary"
        icon="el-icon-folder-add"
        @click="showLinkman"
        v-permi="'/customer/addLinkman'"
      ></el-button>
      <el-button
        type="danger"
        icon="el-icon-delete"
        @click="deleteLinkman"
        :disabled="!checkboxId.length"
      ></el-button>
      <el-button
        type="success"
        icon="el-icon-message"
        :disabled="!checkboxId.length"
        @click="showSend"
      ></el-button>
    </el-row>
    <el-table
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      border
      :fit="true"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" fixed></el-table-column>
      <!-- <el-table-column type="index" width="55" align="center" fixed></el-table-column> -->
      <el-table-column prop="name" v-if="showCol.indexOf('')>-1" label="" align="center"></el-table-column>
      <el-table-column
        prop="customerName"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column prop="job" v-if="showCol.indexOf('')>-1" label="" align="center"></el-table-column>
      <el-table-column prop="mobile" v-if="showCol.indexOf('')>-1" label="" align="center"></el-table-column>
      <el-table-column prop="telephone" v-if="showCol.indexOf('')>-1" label="" align="center"></el-table-column>
      <el-table-column label="" width="150" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary "
            plain
            @click="handleEdit(scope.$index, scope.row)"
            v-permi="'/linkman/updateLinkman'"
          ></el-button>
          <el-button size="mini" @click="handleView(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      class="itemPage"
      @size-change="sizeChangePage"
      @current-change="currentChangePage"
      :current-page="page"
      :page-sizes="[10, 20, 30, 40]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalSize"
    ></el-pagination>
    <new-contact
      :isShowLinkman="isShowLinkman"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-contact>
    <NewSend
      :isShowSend="isShowSend"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewSend>
    <edit-contacts
      :isShowLinkmanEdit="isShowLinkmanEdit"
      :linkmanId="linkmanId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-contacts>
  </div>
</template>

<script>
// 
import API from "@/api/linkman";
import NewContact from "@/components/element/NewContact"; // 
import NewSend from "@/components/element/NewSend"; // 
import EditContacts from "@/components/element/EditContacts"; // 
export default {
  name: "linkman",
  components: {
    NewContact,
    NewSend,
    EditContacts
  },
  data() {
    return {
      flag: true,
      isShowLinkman: false,
      isShowSend: false,
      isShowLinkmanEdit: false,
      linkmanId: "", // id
      options: [], // 
      creationTime: [], // 
      timeActive: 0,
      departmentsList: [], // 
      headList: [], // 
      tableData: [], // 
      colOptions: ["", "", "", "", ""],
      showCol: ["", "", "", "", ""],
      form: {
        regionArr: [],
        definedTime: [],
        followUser: null,
        department: null
      },
      showDefinedTime: false, // 
      page: 1, //
      rows: 10, //
      totalSize: 0, //
      checkboxId: [] // 
    };
  },
  methods: {
    colChange() {
      localStorage.setItem("linkmanTable", JSON.stringify(this.showCol));
    },
    timeCustom() {
      this.timeActive = null;
      this.form.timeCode = null;
      this.tableApi();
    },
    show() {
      this.flag = !this.flag;
    },
    // tableapi
    tableApi() {
      let form = this.form;
      form.regionArr = form.regionArr || [null, null];
      let paprams = {
        pageNum: this.page,
        pageSize: this.rows,
        province: form.regionArr[0],
        city: form.regionArr[1],
        timeCode: form.timeCode,
        startTime: form.definedTime[0],
        endTime: form.definedTime[1],
        department: form.department,
        followUser: form.followUser,
        name: form.companyName
      };
      API.postList(paprams).then(res => {
        // console.log(res.data)
        this.totalSize = res.data.total; //
        res.data.list.forEach(item => {
          item.address = item.province + item.city;
        });
        this.tableData = res.data.list;
      });
    },
    //
    sizeChangePage(val) {
      this.rows = val;
      this.tableApi(this.rows);
    },
    //
    currentChangePage(val) {
      this.page = val;
      this.tableApi(this.page);
    },
    // // 
    // headuser(e){
    //   this.;
    // },
    // 
    showStyle(index, code) {
      this.showDefinedTime = false;
      this.form.definedTime = [];
      this.timeActive = index;
      this.form.timeCode = code;
      this.tableApi();
    },
    // 
    depart(val) {
      if (val) {
        API.getHead(val).then(res => {
          // res.data.forEach(item => {
          //   item.value = item.name;
          // });
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          // res.data.forEach(item => {
          //   item.value = item.name;
          // });
          this.headList = res.data;
        });
      }
      this.form.followUser = "";
      this.tableApi();
    },
    // querySearch(queryString, cb) {
    //   var restaurants = this.headList;
    //   var results = queryString
    //     ? restaurants.filter(this.createFilter(queryString))
    //     : restaurants;
    //   //  callback 
    //   cb(results);
    // },
    // createFilter(queryString) {
    //   return restaurant => {
    //     return (
    //       restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
    //       0
    //     );
    //   };
    // },
    // id
    handleSelect(item) {
      this.followUser = item.id;
      this.tableApi(this.followUser);
      if (this.followUser != null) {
        API.getDepartByUserId(item.id).then(res => {
          this.departmentsList = res.data;
        });
      } else {
        API.getDepartments().then(res => {
          this.departmentsList = res.data;
        });
      }
    },
    handleBlurStr(val) {
      if (this.form.followUser == "") {
        API.getDepartments().then(res => {
          this.departmentsList = res.data;
        });
      }
    },

    // 
    showLinkman() {
      this.isShowLinkman = true;
    },
    // table
    handleSelectionChange(val) {
      let arr = [];
      val.map(ele => {
        arr.push(ele.id);
      });
      console.log(arr);
      this.checkboxId = arr;
    },
    // 
    deleteLinkman() {
      this.$confirm("", "", {
        distinguishCancelAndClose: true,
        confirmButtonText: "",
        cancelButtonText: ""
      }).then(() => {
        API.postdeleteLinkman({ ids: this.checkboxId.join(",") }).then(res => {
          this.$message({
            message: "",
            type: "success"
          });
          this.tableApi();
        });
      });
    },
    // 
    showSend() {
      this.isShowSend = true;
    },
    // Dialog
    getSonSure() {
      this.isShowLinkman = false;
      this.isShowSend = false;
      this.isShowLinkmanEdit = false;
      this.tableApi();
    },
    // Dialog 
    getSonCancel(val) {
      this.isShowLinkman = val;
      this.isShowSend = val;
      this.isShowLinkmanEdit = val;
    },
    // 
    handleEdit(index, row) {
      this.linkmanId = row.id;
      this.isShowLinkmanEdit = true;
    },
    // 
    handleView(index, row) {
      this.$router.push({ path: "linkmandetails", query: { id: row.id } });
    }
  },
  activated() {
    // tableapi
    this.tableApi();
  },
  mounted() {
    let showCol = JSON.parse(localStorage.getItem("linkmanTable"));
    if (showCol) this.showCol = showCol;
    // api
    API.getRegion().then(res => {
      res.data.forEach(item => {
        item.value = item.id;
        if (item.children.length == "0") {
          item.children = "";
        } else {
          item.children.forEach(item1 => {
            item1.value = item1.id;
          });
        }
      });
      this.options = res.data;
    });
    // api
    API.getCreationTime().then(res => {
      // let obj = { code: 0, name: "" };
      // res.data.unshift(obj);
      res.data.splice(2, 1);
      res.data.splice(3, 1);
      res.data.splice(4, 1);
      this.creationTime = res.data;
    });
    // api
    API.getDepartments().then(res => {
      this.departmentsList = res.data;
    });
    // 
    API.getAllUser().then(res => {
      // res.data.forEach(item => {
      //   item.value = item.name;
      // });
      this.headList = res.data;
    });
  },
  updated() {}
};
</script>

<style lang='scss' scoped>
ul {
  list-style-type: none;
  padding: 0;
}
.list {
  li {
    display: inline-block;
    padding: 0 20px;
    cursor: pointer;
  }
}
.addclass {
  background: rgb(255, 242, 233);
  color: rgb(255, 178, 121);
}
.item-css {
  width: 100%;
}

.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}

.input-search button {
  margin-left: 30px;
}
</style>
