<template>
  <div>
    <el-row>
      <el-col :span="24">
        <el-col :span="8">
          <h1>{{panoramic.companyName}}</h1>
        </el-col>
        <el-col :span="16">
          <h3>: {{head.name}}</h3>
        </el-col>
      </el-col>
    </el-row>
    <el-row>
      <el-dropdown trigger="click" @command="handleCommand1">
        <el-button type="primary" plain class="el-dropdown-link">
          {{commandName}}
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item
            v-for="(item, i) in customerStatusList"
            :key="i"
            :command="item.val"
            :val="item.code"
          >{{item.val}}</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-dropdown trigger="click" class="el-dro" @command="handleCommand2">
        <el-button type="primary" plain class="el-dropdown-link">
          
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="" v-permi="'/customer/addLinkman'"></el-dropdown-item>
          <el-dropdown-item command="" v-permi="'/customer/findChanceByCustomerId'"></el-dropdown-item>
          <el-dropdown-item command="" v-permi="'/contract/add'"></el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <!-- <el-dropdown trigger="click" class="el-dro" @command="handleCommand3">
        <el-button type="primary" plain class="el-dropdown-link">
          
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command=""></el-dropdown-item>
          <el-dropdown-item command=""></el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>-->

      <el-button
        type="primary"
        class="el-dro"
        v-permi="'/customer/toOther'"
        @click="removeUser"
      ></el-button>
      <el-button
        type="primary"
        class="el-dro"
        v-permi="'/customer/updateCustomer'"
        @click="isShowEdit=true"
      ></el-button>
      <el-button
        type="warning"
        class="el-dro"
        v-permi="'/customer/returnCustomer'"
        v-show="panoramic.clueId"
        @click="isShowSales=true"
      ></el-button>
      <el-button type="warning" @click="isShowSend=true"></el-button>
    </el-row>

    <el-tabs type="card" @tab-click="tabClick">
      <el-tab-pane label="" class="quanjing">
        <el-card>
          <div slot="header" class="clearfix">
            <span></span>
          </div>

          <div class="text">
            <p></p>
            <p>{{customerGrade&&customerGrade.name}}</p>
          </div>
          <div class="text">
            <p></p>
            <p>{{panoramic.remarks}}</p>
          </div>
          <div class="text">
            <p></p>
            <p>{{panoramic.telephone}}</p>
          </div>
          <div class="text">
            <p></p>
            <p>{{channel&&channel.name}}</p>
          </div>
          <div class="text">
            <p></p>
            <p>{{panoramic.customerFromName}}</p>
          </div>
          <div class="text">
            <p></p>
            <p>{{panoramic.type}}</p>
          </div>
          <div class="text">
            <p></p>
            <p>{{panoramic.isWechat}}</p>
          </div>
          <div class="text">
            <p></p>
            <p>{{panoramic.isFace}}</p>
          </div>
          <div class="text">
            <p></p>
            <p>{{panoramic.searchKeywords}}</p>
          </div>
          <div class="text">
            <p></p>
            <p>{{panoramic.saleKeywords}}</p>
          </div>
        </el-card>
        <el-card>
          <div slot="header" class="clearfix">
            <span></span>
            <el-button
              style="float: right; padding: 0;font-size: 30px;"
              type="text"
              @click="isShowLinkman=true"
            >
              <i class="el-icon-plus"></i>
            </el-button>
          </div>
          <div v-for="(item, i) in panoramic.linkmens" :key="i">
            <div class="text">
              <p></p>
              <p>{{item.name}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{item.mobile}}</p>
            </div>
            <el-divider></el-divider>
          </div>
        </el-card>
        <el-card class>
          <div slot="header" class="clearfix">
            <span></span>
            <el-button
              style="float: right; padding: 0;font-size: 30px;"
              type="text"
              @click="isShowChance=true"
            >
              <i class="el-icon-plus"></i>
            </el-button>
          </div>
          <div v-for="(item, i) in panoramic.chances" :key="i">
            <div class="text">
              <p></p>
              <p>{{item.name}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{item.expectPrice}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{item.expectDate}}</p>
            </div>
            <el-divider></el-divider>
          </div>
        </el-card>
        <el-card>
          <div slot="header" class="clearfix">
            <span></span>
            <el-button
              style="float: right; padding: 0;font-size: 30px;"
              type="text"
              @click="isShowContract=true"
            >
              <i class="el-icon-plus"></i>
            </el-button>
          </div>
          <div v-for="(item, i) in panoramic.contracts" :key="i">
            <div class="text">
              <p></p>
              <p>{{item.name}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{item.totalPrice}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{item.signDate}}</p>
            </div>
            <el-divider></el-divider>
          </div>
        </el-card>
        <el-card>
          <div slot="header" class="clearfix">
            <span></span>
          </div>
          <div>
            <div class="text">
              <p></p>
              <p>{{totalPrice}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{panoramic.totalContractsPrice}}</p>
            </div>
            <!-- <div class="text">{{}}</div> -->
          </div>
        </el-card>
        <follow-up-history
          class="genjin"
          @writeFollow="isShowFollow= true"
          :customerId="Number(customerId)"
          :type="1"
          ref="followUpHistory"
        ></follow-up-history>
      </el-tab-pane>
      <el-tab-pane label="">
        <el-card style="width: 100%;">
          <div slot="header" class="clearfix">
            <span></span>
          </div>
          <div class="font14">
            <div class="title"></div>
            <el-row>
              <el-col :span="8">
                <div>{{customerData.companyName}}</div>
              </el-col>
              <el-col :span="8">
                <div>{{customerData.telephone}}</div>
              </el-col>
              <el-col :span="8">
                <div>{{customerData.area}}</div>
              </el-col>
              <el-col :span="8">
                <div>{{customerData.customerGrade}}</div>
              </el-col>
              <el-col :span="8">
                <div>{{customerData.email}}</div>
              </el-col>
              <el-col :span="8">
                <div>{{customerData.from}}</div>
              </el-col>
              <el-col :span="8">
                <div>{{customerData.type}}</div>
              </el-col>
            </el-row>
            <el-divider></el-divider>
            <div class="title"></div>
            <el-row>
              <el-col :span="8">
                <div>{{customerData.status}}</div>
              </el-col>
              <el-col :span="8">
                <div>{{ customerData.staffSize}}</div>
              </el-col>
              <el-col :span="8">
                <div>{{customerData.isWechat}}</div>
              </el-col>
              <el-col :span="8">
                <div>{{customerData.nextFollowTime}}</div>
              </el-col>
              <el-col :span="8">
                <div>{{customerData.isFace}}</div>
              </el-col>
              <el-col :span="8">
                <div>{{customerData.customerFrom}}</div>
              </el-col>
              <el-col :span="8">
                <div>{{customerData.search_keywords}}</div>
              </el-col>
              <el-col :span="8">
                <div class="remark">
                  
                  <span>{{customerData.remarks}}</span>
                </div>
              </el-col>
            </el-row>
            <el-divider></el-divider>
            <div class="title"></div>
            <el-row>
              <el-col :span="8">
                <div>{{customerData.followUser}}</div>
              </el-col>
              <el-col :span="8">
                <div>{{customerData.createTime}}</div>
              </el-col>
              <el-col :span="8">
                <div>{{customerData.followDepart}}</div>
              </el-col>
              <el-col :span="8">
                <div>{{customerData.updateTime}}</div>
              </el-col>
            </el-row>
          </div>
        </el-card>
      </el-tab-pane>
      <el-tab-pane label="">
        <tab-linkman ref="tabLinkman" :customerId="customerId"></tab-linkman>
      </el-tab-pane>
      <el-tab-pane label="">
        <tab-chance ref="tabChance" :customerName="panoramic.companyName" :customerId="customerId"></tab-chance>
      </el-tab-pane>
      <el-tab-pane label="">
        <tab-contract ref="tabContract" :panoramic="panoramic" :customerId="customerId"></tab-contract>
      </el-tab-pane>
      <el-tab-pane label="">
        <tab-task ref="tabTask" :customerId="customerId"></tab-task>
      </el-tab-pane>
      <el-tab-pane label="">
        <tab-annex ref="tabAnnex" :customerId="customerId"></tab-annex>
      </el-tab-pane>
      <el-tab-pane label="">
        <tab-log ref="tabLog" :customerId="customerId"></tab-log>
      </el-tab-pane>
    </el-tabs>
    <edit-customer
      :isShowEdit="isShowEdit"
      :customerId="customerId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-customer>
    <Potential
      :isShowPotential="isShowPotential"
      :PotentialId="PotentialId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></Potential>
    <NewSend
      :isShowSend="isShowSend"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewSend>
    <new-contact
      :isShowLinkman="isShowLinkman"
      :customerId="customerId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-contact>
    <new-chance
      :customerId="customerId"
      :customerName="panoramic.companyName"
      :isShowChance="isShowChance"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-chance>
    <new-contract
      :isShowContract="isShowContract"
      :customerId="customerId"
      :customerDetail="panoramic"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-contract>
    <Followup
      :isShowFollow="isShowFollow"
      :customerId="customerId"
      @sureDialogFather="$refs.followUpHistory.getFollowData()"
      @closeDialogFather="getSonCancel"
    ></Followup>
    <!--  -->
    <return-sales
      :isShowSales="isShowSales"
      :seletedCustomer="panoramic"
      @sureDialogFather="$router.go(-1)"
      @closeDialogFather="getSonCancel"
    ></return-sales>
  </div>
</template>

<script>
import API from "@/api/customer";
import FollowUpHistory from "@/components/element/FollowUpHistory"; // 
import Potential from "../components/Potential"; // 
import EditCustomer from "@/components/element/EditCustomer"; // 
import NewSend from "@/components/element/NewSend"; // 
import NewContact from "@/components/element/NewContact"; // 
import NewChance from "@/components/element/NewChance"; // 
import NewContract from "@/components/element/NewContract"; // 
import ReturnSales from "../components/ReturnSales"; // 
import Followup from "@/components/element/Followup"; // 
import EditContacts from "@/components/element/EditContacts"; // 
import EditChance from "@/components/element/EditChance"; // 
import EditContract from "@/components/element/EditContract"; // 
import TabTask from "@/components/element/TabTask";
import TabAnnex from "@/components/element/TabAnnex";
import TabChance from "@/components/element/TabChance";
import TabLog from "@/components/element/TabLog";
import TabContract from "@/components/element/TabContract";
import TabLinkman from "@/components/element/TabLinkman";

export default {
  name: "customerdetails",
  components: {
    Potential,
    FollowUpHistory,
    EditCustomer,
    ReturnSales,
    NewSend,
    TabLinkman,
    TabLog,
    TabChance,
    TabTask,
    TabAnnex,
    TabContract,
    NewContact,
    NewChance,
    NewContract,
    Followup,
    EditContacts,
    EditChance,
    EditContract
  },
  data() {
    return {
      customerStatusList: [], // 
      isShowPotential: false, // 
      PotentialId: "", // id
      isShowEdit: false, // 
      isShowSales: false, // 
      isShowSend: false, // 
      isShowLinkman: false, // 
      linkmanId: "", // id
      isShowChance: false, // 
      opportunityId: "", // id
      isShowContract: false, // 
      contractId: "", // id
      isShowFollow: false, // 
      isShowUpload: false, // 
      taskDetail: "", // 
      commandName: "", // 
      panoramic: {}, // 
      head: {}, // 
      customerGrade: {}, // 
      channel: {}, // 
      totalPrice: "", // 
      customerData: {}, // 
      linkmanTableData: [], // 
      chanceTableData: [], // 
      contractTableData: [], // 
      tabName: "", // tab
      customerId: null, // id
      contractStatusList: [] // 
    };
  },
  methods: {
    // 
    handleCommand1(command, val) {
      // console.log(command, val.$attrs.val)
      let data = {
        id: this.customerId,
        status: val.$attrs.val
      };
      API.postchangeCustomerStatus(data).then(res => {
        this.$message({
          type: "success",
          message: "!"
        });
        this.commandName = command;
      });
    },
    handleCommand2(command) {
      if (command == "") {
        this.isShowLinkman = true;
      } else if (command == "") {
        this.isShowChance = true;
      } else if (command == "") {
        this.isShowContract = true;
      }
    },
    removeUser(command) {
      // let id = this.customerId;
      // if (command == "") {
      this.PotentialId = this.customerId;
      this.isShowPotential = true;
      // } else if (command == "") {
      //   this.$confirm(", ?", "", {
      //     confirmButtonText: "",
      //     cancelButtonText: "",
      //     type: "warning"
      //   })
      //     .then(() => {
      //       API.posttoPublic({ ids: id }).then(res => {
      //         if (res.status == 200) {
      //           this.$message({
      //             type: "success",
      //             message: "!"
      //           });
      //         }
      //       });
      //     })
      //     .catch(() => {
      //       this.$message({
      //         type: "error",
      //         message: ""
      //       });
      //     });
      // }
    },
    // Dialog
    getSonSure() {
      this.getSonCancel();
      this.getCustomerData();
      this.tabClick(this.tabName);
    },
    // Dialog
    getSonCancel() {
      this.isShowPotential = false;
      this.isShowEdit = false;
      this.isShowSend = false;
      this.isShowLinkman = false;
      this.isShowSales = false;
      this.isShowChance = false;
      this.isShowContract = false;
      this.isShowFollow = false;
      this.isShowUpload = false;
    },

    // 
    handleView1(index, row) {
      this.$router.push({ path: "linkmandetails", query: { id: row.id } });
    },

    // 
    handleView2(index, row) {
      this.$router.push({ path: "opportunitydetails", query: { id: row.id } });
    },

    // 
    handleView3(index, row) {
      this.$router.push({
        path: "contractdetails",
        query: { id: row.id, paymentTotal: row.paymentTotal }
      });
    },

    // 
    getLinkmanTable() {
      API.getCustomerLinkman(this.customerId).then(res => {
        this.linkmanTableData = res.data;
      });
    },
    // 
    getContractTable() {
      API.getContractByCustomerId(this.customerId).then(res => {
        let data = res.data;
        data.map(tableEle => {
          this.contractStatusList.map(listEle => {
            if (tableEle.status == listEle.code) {
              tableEle.status = listEle.val;
            }
          });
        });
        this.contractTableData = data;
      });
    },
    getChanceTable() {
      API.getChanceByCustomerId(this.customerId).then(res => {
        this.chanceTableData = res.data;
      });
    },
    // tab 
    tabClick(tab) {
      let id = this.customerId;
      let label = typeof tab === "object" ? tab.label : tab;
      this.tabName = label;
      if (label == "") {
        this.getCustomerData();
      } else if (label == "") {
        API.getCustomerInformationVoById(id).then(res => {
          this.customerData = res.data;
        });
      } else if (tab.label == "") {
        this.$refs.tabLinkman.getLinkmanTable();
      } else if (tab.label == "") {
        this.$refs.tabChance.getChanceTable();
      } else if (tab.label == "") {
        this.$refs.tabContract.getContractTable();
      } else if (label == "") {
        this.$refs.tabTask.getTaskTable();
      } else if (label == "") {
        this.$refs.tabAnnex.getAnnexTable();
      } else if (label == "") {
        this.$refs.tabLog.getLogTable();
      }
    },
    getCustomerData() {
      let id = this.$route.query.id;
      // api
      API.getCustomerDetails(id)
        .then(res => {
          // console.log(res.data);
          // 
          if (res.data.isWechat == 1) {
            res.data.isWechat = "";
          } else {
            // } else if(res.data.isWechat == 0){
            res.data.isWechat = "";
          }
          // 
          if (res.data.isFace == 1) {
            res.data.isFace = "";
          } else {
            // } else if (res.data.isFace == 0){
            res.data.isFace = "";
          }
          let totalPrice = 0; // 
          res.data.contracts.forEach(item => {
            totalPrice += item.totalPrice;
          });
          this.totalPrice = totalPrice;
          this.panoramic = res.data;
          this.head = res.data.user;
          this.customerGrade = res.data.customerGrade;
          this.channel = res.data.channel;
        })
        .catch(err => {
          console.error(err);
        });
    }
  },
  mounted() {
    let id = this.$route.query.id;
    this.customerId = id;
    this.getCustomerData();
    API.getCustomerType().then(res => {
      // 
      let customerStatusList = res.data;
      API.getfindCustomerStatusById(id).then(res => {
        customerStatusList.forEach(item => {
          if (res.data == item.code) {
            this.commandName = item.val;
          }
        });
      });
      this.customerStatusList = customerStatusList;
    });
    // 
    API.getContractStatus().then(res => {
      this.contractStatusList = res.data;
    });
  }
};
</script>
<style lang='scss' scoped>
.el-dropdown-link {
  cursor: pointer;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.el-dro {
  margin-left: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
/deep/ .el-card {
  width: 48%;
  margin-bottom: 20px;
}
/deep/ .el-tab-pane {
  position: relative;
}
.genjin {
  position: absolute;
  top: 0;
  right: 0;
}
span {
  font-size: 18px;
}
div.text {
  font-size: 14px;
  margin-bottom: 18px;
  display: flex;
  & > :first-child {
    width: 100px;
    // text-align: right;
    // margin-right:10px;
  }
  & > :last-child {
    flex: 1;
    word-break: break-all;
    word-wrap: break-word;
  }
}
.font14 {
  font-size: 14px;
  line-height: 24px;
  & > div.title {
    font-weight: bold;
    font-size: 16px;
  }
}
.remark {
  padding-right: 20px;
  box-sizing: border-box;
  span {
    // display: line-block;
    word-break: break-all;
    word-wrap: break-word;
    font-size: 14px;
  }
}
</style>