<template>
  <div>
    <el-dialog
      title=""
      :close-on-click-modal="false"
      :visible.sync="outerVisible"
      @close="closeDialog"
    >
      <el-form :model="form" label-width="120px" :rules="rules" ref="form">
        <el-form-item label="" prop="companyName">
          <el-input v-model="form.companyName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="grade">
          <el-select v-model="form.grade" placeholder="" clearable class="item-css">
            <el-option
              v-for="(item, index) in customerLevel"
              :key="index"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="telephone">
          <el-input v-model="form.telephone" maxlength="12" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="form.type" disabled placeholder="" clearable class="item-css">
            <el-option
              v-for="(item, index) in customerTypeList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="from">
          <el-select v-model="form.from" placeholder="" clearable class="item-css">
            <el-option
              v-for="(item, index) in channelSourceList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="customerFrom">
          <el-select v-model="form.customerFrom" placeholder="" clearable class="item-css">
            <el-option
              v-for="(item, index) in clientSourceList"
              :key="index"
              :value="item.code"
              :label="item.val"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="" prop="registerName">
          <el-input v-model="form.registerName" placeholder=""></el-input>
        </el-form-item>-->
        <el-form-item label="" prop="regionArr">
          <el-cascader
            v-model="form.regionArr"
            :options="region"
            placeholder=""
            clearable
            class="item-css"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="followDepart">
          <el-select
            v-model="form.followDepart"
            placeholder=""
            clearable
            class="item-css"
            @change="departChange($event)"
          >
            <el-option
              v-for="(item, index) in departmentsList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="followUser">
          <el-select v-model="form.followUser" @change="userChange" filterable placeholder="">
            <el-option
              v-for="(item,index) in headList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.saleKeywords" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.searchKeywords" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select v-model="form.status" placeholder="" clearable class="item-css">
            <el-option
              v-for="(item, index) in customerType"
              :key="index"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.email" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model.number="form.staffSize" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="form.nextFollwTime"
            type="datetime"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder=""
            :picker-options="startDatePicker"
            class="item-css"
            default-time="00:00:00"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="form.remarks"
            style="width:60%"
            type="textarea"
            :rows="4"
            maxlength="100"
            show-word-limit
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.isWechat" placeholder="" clearable class="item-css">
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.isFace" placeholder="" clearable class="item-css">
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button @click="sureDialog" type="success"></el-button>
          <el-button @click="closeDialog"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/customer";
export default {
  name: "EditCustomer",
  props: ["isShowEdit", "customerId"],
  data() {
    var validateTel = (rule, value, callback) => {
      var reg = /0\d{2}-\d{7,8}$/;
      var reg2 = /^\d{11}$/;
      if (!value || reg.test(value) || reg2.test(value)) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    return {
      startDatePicker: {
        // 
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7; //-8.64e6
        }
      },
      outerVisible: this.isShowEdit,
      customerLevel: [], // 
      customerTypeList: [], // 
      channelSourceList: [], // 
      region: [], // 
      departmentsList: [], // 
      headList: [], // 
      customerType: [], // 
      clientSourceList: [], // 
      form: {}, // 
      province: "", // 
      city: "", // 
      followDepart: null, // id
      followUser: null, // id
      rules: {
        companyName: [
          { required: true, message: "", trigger: "blur" }
        ],
        grade: [
          { required: true, message: "", trigger: "change" }
        ],
        telephone: [
          { required: false, validator: validateTel, trigger: "blur" }
        ],
        type: [
          { required: true, message: "", trigger: "change" }
        ],
        from: [
          { required: true, message: "", trigger: "change" }
        ],
        customerFrom: [
          { required: true, message: "", trigger: "change" }
        ],
        registerName: [
          { required: true, message: "", trigger: "blur" }
        ],
        regionArr: [
          {
            type: "array",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        followDepart: [
          { required: true, message: "", trigger: "change" }
        ],
        followUser: [
          { required: true, message: "", trigger: "change" }
        ],
        status: [
          { required: true, message: "", trigger: "change" }
        ],
        linkUserName: [
          { required: true, message: "", trigger: "blur" }
        ],
        linkUserPhone: [
          { required: true, message: "", trigger: "blur" }
        ]
      }
    };
  },
  methods: {
    // 
    departChange(flag) {
      if (flag !== "no_change") this.form.followUser = null;
      let val = this.form.followDepart;
      if (val) {
        API.getHead(val).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      }
    },
    // id
    userChange(val) {
      let departId = this.headList.find(item => {
        return item.id === val;
      }).departId;
      this.form.followDepart = departId;
      this.departChange("no_change");
    },

    // 
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let form = this.form;
        let data = {
          id: this.customerId,
          companyName: form.companyName,
          grade: form.grade,
          telephone: form.telephone,
          type: form.type,
          from: form.from,
          registerName: form.registerName,
          saleKeywords: form.saleKeywords,
          province: form.regionArr[0],
          city: form.regionArr[1],
          followDepart: form.followDepart,
          customerFrom: form.customerFrom,
          followUser: form.followUser,
          searchKeywords: form.searchKeywords,
          status: form.status,
          email: form.email,
          staffSize: form.staffSize,
          nextFollwTime: form.nextFollwTime,
          remarks: form.remarks,
          isWechat: form.isWechat,
          isFace: form.isFace
        };
        API.postUpdate(data).then(res => {
          console.log(res);
          if (res && res.status == 200) {
            this.$message({
              message: "",
              type: "success"
            });
            this.closeDialog();
            this.$emit("sureDialogFather");
          } else {
            this.$message.error("");
          }
        });
      });
    },
    // 
    closeDialog() {
      this.outerVisible = false;
      this.$refs.form.resetFields();

      this.$emit("closeDialogFather", this.outerVisible);
    }
  },
  watch: {
    isShowEdit(val) {
      this.outerVisible = this.isShowEdit;
      if (!val) return;
      // api
      API.getCustomerLevel().then(res => {
        this.customerLevel = res.data;
      });
      // api
      API.getType().then(res => {
        this.customerTypeList = res.data;
      });
      // api
      API.getCustomerSource().then(res => {
        this.channelSourceList = res.data;
      });
      // 
      API.customerFrom().then(res => {
        this.clientSourceList = res.data;
      });
      // api
      API.getRegion().then(res => {
        res.data.forEach(item => {
          item.value = item.id;
          if (item.children.length == "0") {
            item.children = "";
          } else {
            item.children.forEach(item1 => {
              item1.value = item1.id;
            });
          }
        });
        this.region = res.data;
      });
      // api
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });

      // api
      API.getCustomerType().then(res => {
        this.customerType = res.data;
      });
      // 
      API.getByid(this.customerId).then(res => {
        let data = res.data;
        console.log(data);

        return new Promise((resolve, reject) => {
          // 
          if (data.followDepart) {
            API.getHead(data.followDepart).then(resHead => {
              resHead.data.forEach(item => {
                item.value = item.name;
              });
              this.headList = resHead.data;
              resolve();
            });
          } else {
            // 
            API.getAllUser().then(res => {
              res.data.forEach(item => {
                item.value = item.name;
              });
              this.headList = res.data;
              resolve();
            });
          }
        }).then(() => {
          data.regionArr = [data.province, data.city];
          this.form = data;
          this.$nextTick(() => {
            this.$refs.form.clearValidate();
          });
        });
      });
    }
  }
};
</script>
<style lang='scss' scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>