// api
import fetch from '@/utils/fetch';
import Qs from 'qs'
export default {
    // get 
    // 
    getCustomerLevel(data) {
        return fetch({
            url: `customer/grade`,
            method: 'get',
            params: data
        })
    },
    // api
    getRegion(data) {
        return fetch({
            url: `area/all`,
            method: 'get',
            params: data
        })
    },
    // 
    getType(data) {
        return fetch({
            url: `customer/service/type`,
            method: 'get',
            params: data
        })
    },
    // 
    getCreationTime(data) {
        return fetch({
            url: `customer/findCustomerTimeCode`,
            method: 'get',
            params: data
        })
    },
    // 
    getAllUser(data) {
        return fetch({
            url: `customer/findAllUser`,
            method: 'get',
            params: data
        })
    },
    // id
    getByid(data) {
        return fetch({
            url: `customer/findById/${data}`,
            method: 'get'
        })
    },
    // api
    getCustomerSource(data) {
        return fetch({
            url: `customer/channel`,
            method: 'get',
            params: data
        })
    },
    // api
    getDepartments(data) {
        return fetch({
            url: `customer/findAllDepart`,
            method: 'get',
            params: data
        })
    },
    // id
    getHead(data) {
        return fetch({
            url: `customer/findAllDepartUser/${data}`,
            method: 'get'
        })
    },
    // id
    getDepartByUserId(data) {
        return fetch({
            url: `customer/findDepartByUserId/${data}`,
            method: 'get'
        })
    },
    // 
    getCustomerType(data) {
        return fetch({
            url: `customer/status`,
            method: 'get',
            params: data
        })
    },
    // 
    getfindSeaCustomerById(data) {
        return fetch({
            url: `sea/findSeaCustomerById/${data}`,
            method: 'get'
        })
    },
    // id
    getCustomerFileById(data) {
        return fetch({
            url: `customer/findCustomerFileById/${data}`,
            method: 'get'
        })
    },
    // id
    getCustomerFileDownload(data) {
        return fetch({
            url: `customer/customerFileDownload/${data}`,
            method: 'get'
        })
    },
    // id
    getdeleteCustomerFile(data) {
        return fetch({
            url: `customer/deleteCustomerFile/${data}`,
            method: 'get'
        })
    },
    // post 
    // api
    postList(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `sea/findAllSeaCustomerVo?${data1}`,
            method: 'post'
        })
    },
    //
    getFollowList(data) {
        return fetch({
            url: `/follow/getFollowList`,
            method: 'get',
            params: data
        })
    },
    // api
    posttoCustomer(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `sea/toCustomer?${data1}`,
            method: 'post'
        })
    },
    // api
    postdeleteSeaCustomer(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `sea/deleteSeaCustomer?${data1}`,
            method: 'post'
        })
    },
    // api
    postUpdate(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `customer/updateCustomer?${data1}`,
            method: 'post'
        })
    },
}