<template>
  <div class="combo-container">
    <div class="business-btn">
      <el-button
        type="primary"
        v-permi="'/packageManage/addPackage'"
        @click="isAddCombo = true"
      ></el-button>
      <el-button type="warning" @click="changeState(0)" :disabled="!selectedItem.length"></el-button>
      <el-button type="success" @click="changeState(1)" :disabled="!selectedItem.length"></el-button>
      <el-button type="danger" :disabled="!selectedItem.length" @click="delCombo"></el-button>
    </div>

    <el-table
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      @selection-change="handleSelectionChange"
      border
      :fit="true"
    >
      <el-table-column type="selection" width="55" align="center" fixed></el-table-column>
      <el-table-column label="" prop="name" min-width="250" align="center"></el-table-column>
      <el-table-column label="" prop="type" align="center">
        <template slot-scope="scope">{{scope.row.type|taxType}}</template>
      </el-table-column>
      <el-table-column label="" prop="money" align="center"></el-table-column>
      <el-table-column label="" prop="moneyGift" align="center"></el-table-column>
      <el-table-column label="" prop="months" align="center">
        <template slot-scope="scope">{{scope.row.months+''}}</template>
      </el-table-column>
      <el-table-column label="" prop="cycle" align="center">
        <template slot-scope="scope">
          <span v-if="scope.row.state===1" style="color:#67C23A"></span>
          <span v-else style="color:#E6A23C"></span>
        </template>
      </el-table-column>
      <el-table-column label="" prop="description" min-width="250" align="center"></el-table-column>
      <el-table-column label="" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            v-permi="'/packageManage/updatePackage'"
            type="primary"
            plain
            @click="handleEdit(scope.$index, scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <div>
      <el-pagination
        class="itemPage"
        @size-change="sizeChangePage"
        @current-change="currentChangePage"
        :current-page="pageObj.pageNum"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pageObj.totalSize"
      ></el-pagination>
    </div>

    <!--  -->
    <add-combo
      :isAddCombo="isAddCombo"
      @sureDialogFather="getTableData()"
      @closeDialogFather="isAddCombo=false"
    ></add-combo>

    <!--  -->
    <edit-combo
      @closeDialogFather="isEditCombo=false"
      :isEditCombo="isEditCombo"
      :detailData="detailData"
      @sureDialogFather="getTableData()"
    ></edit-combo>
  </div>
</template>

<script>
import API from "@/api/combo";
import AddCombo from "./components/AddCombo";
import EditCombo from "./components/EditCombo";
export default {
  name: "combo",
  components: { AddCombo, EditCombo },
  data() {
    return {
      // 
      isAddCombo: false,
      // 
      isEditCombo: false,
      // 
      detailData: null,
      // 
      selectedItem: [],
      // 
      tableData: [],
      pageObj: {
        totalSize: null,
        pageSize: 10,
        pageNum: 1
      }
    };
  },
  methods: {
    // 
    changeState(state) {
      let handle = state == 1 ? "" : "";
      this.$confirm("" + handle + "", "" + handle, {
        distinguishCancelAndClose: true,
        confirmButtonText: handle,
        cancelButtonText: ""
      })
        .then(() => {
          let arr = [];
          for (const item of this.selectedItem) {
            arr.push(item.id);
          }
          let params = {
            ids: arr.join(","),
            state
          };
          API.updateState(params)
            .then(res => {
              console.log(res);
              this.getTableData();
            })
            .catch(err => {
              console.error(err);
              this.$message({
                message: err,
                type: "error"
              });
            });
        })
        .catch(err => {
          if (err.indexOf("") === -1) this.$message.warning(err);

          console.error(err);
        });
    },
    // 
    currentChangePage(e) {
      this.pageObj.pageNum = e;
      this.getTableData();
    },
    // 
    sizeChangePage(e) {
      this.pageObj.pageSize = e;
      this.getTableData();
    },
    // 
    delCombo() {
      this.$confirm("", "", {
        distinguishCancelAndClose: true,
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(() => {
          let arr = [];
          for (const item of this.selectedItem) {
            arr.push(item.id);
          }
          let params = {
            ids: arr.join(",")
          };
          API.delPackage(params)
            .then(res => {
              console.log(res);
              this.$message({
                type: "success",
                message: ""
              });
              this.getTableData();
            })
            .catch(err => {
              console.error(err);
              if (err.indexOf("") === -1) this.$message.warning(err);
            });
        })
        .catch(err => {
          console.error(err);
          if (err.indexOf("") === -1) this.$message.warning(err);
        });
    },
    // 
    handleEdit(e, row) {
      this.detailData = row;
      this.isEditCombo = true;
    },
    // 
    handleSelectionChange(e) {
      this.selectedItem = e;
    },
    // 
    getTableData() {
      let params = {
        pageSize: this.pageObj.pageSize,
        pageNum: this.pageObj.pageNum
      };
      API.findPackage(params)
        .then(res => {
          let data = res.data;
          this.pageObj.totalSize = data.total;
          this.tableData = data.list;
        })
        .catch(err => {
          console.error(err);
          if (err.indexOf("") === -1) this.$message.warning(err);
        });
    }
  },
  mounted() {
    this.getTableData();
  },
  filters: {
    taxType(value) {
      if (!value) return;
      switch (value) {
        case 1:
          return "";
          break;
        case 2:
          return "";
          break;
        case 3:
          return "";
          break;
      }
    }
  }
};
</script>
<style lang='scss' scoped>
.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}
</style>