<template>
  <div>
    <!--  -->
    <el-dialog title="" :visible.sync="outerVisible" @close="closeDialog" append-to-body>
      <el-form :model="form" label-width="120px" :rules="rules" ref="form">
        <el-form-item label="" prop="customerId">
          <el-select v-model="form.customerId" placeholder="" disabled>
            <el-option
              v-for="(item, index) in clientList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="contractId">
          <el-select v-model="form.contractId" placeholder="" disabled>
            <el-option
              v-for="(item, index) in contractList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="price">
          <el-input v-model="form.price" placeholder="" style="width:50%"></el-input>
        </el-form-item> 
        <!-- <el-form-item label="">
          <el-input v-model="form.residue"  placeholder="" style="width:50%"></el-input>
        </el-form-item>-->
        <el-form-item label="" prop="opeDate">
          <el-date-picker v-model="form.opeDate" type="date" value-format="yyyy-MM-dd HH:mm:ss"></el-date-picker>
        </el-form-item>
    

        <el-form-item>
          <el-button type="success" @click="sureDialog"></el-button>

          <el-button type="info" @click="closeDialog"></el-button>
        </el-form-item>

        <!-- <el-divider v-show="form.showTable"></el-divider> -->
        <!-- v-if="form.showTable" -->
        <el-table
          v-if="false"
          :data="tableData"
          style="width: 100%"
          highlight-current-row
          border
          stripe
        >
          <el-table-column type="index" label="" width="65" align="center"></el-table-column>

          <el-table-column prop="date" label="" align="center">
            <template slot-scope="scope">
              <el-date-picker
                :disabled="!scope.row.edit"
                :picker-options="scope.row.startDatePicker"
                v-model="scope.row.opeDate"
                value-format="yyyy-MM-dd HH:mm:ss"
                type="date"
                placeholder=""
              ></el-date-picker>
            </template>
          </el-table-column>

    
          <el-table-column prop="price" width="130" label="" align="center">
            <template slot-scope="scope">
              <el-input
                class
                v-model="scope.row.price"
                :disabled="!scope.row.edit"
                placeholder=""
              ></el-input>
            </template>
          </el-table-column>
          <el-table-column prop="residue" width="140" label="" align="center">
            <template slot-scope="scope">
              <div
                v-if="form.editTable"
              >{{scope.$index == tableData.length - 1?editBalance(scope.$index, scope.row): scope.row.residue}}</div>
              <div v-else>{{echoBalance(scope.$index, scope.row)}}</div>
            </template>
          </el-table-column>
          <el-table-column label="" width="100" align="center">
            <template slot-scope="scope">
              <div v-if="!scope.row.edit">
                <el-button
                  type="text"
                  icon="el-icon-edit"
                  :title="scope.row.isOver?'':null"
                  :disabled="Boolean(scope.row.isOver)"
                  @click="handleEdit(scope.$index, scope.row)"
                ></el-button>
              </div>
              <el-button v-else @click="finish(scope.$index,scope.row)" type="success"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <el-form-item v-if="tableData.length">
          <el-button
            :disabled="tableData[tableData.length-1].residue!==0"
            style="margin: 20px 20px 0 0"
            type="success"
            @click="sureDialog"
          ></el-button>
          <el-button type="info" @click="closeDialog"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/payment";
/**
 *
 *  
 *  
 *  ,
 *  html
 *  computed
 *  echoBalance editBalance
 *  editBalance./NewReceivableplan.vuecomputed
 *  
 */
// 
export default {
  name: "EditReceivableplan",
  props: {
    isEditReceivableplan: {
      type: Boolean,
      default: false
    },
    editPlanData: {
      type: Object,
      default: {}
    }
  },
  data() {
    return {
      rules: {
        customerId: [
          { required: true, message: "", trigger: "change" }
        ],
        contractId: [
          { required: true, message: "", trigger: "change" }
        ],
        departId: [
          { required: true, message: "", trigger: "change" }
        ],
        userId: [
          { required: true, message: "", trigger: "change" }
        ],
        price: [
          { required: true, message: "", trigger: "blur" }
        ],
        opeDate: [
          { required: true, message: "", trigger: "change" }
        ]
      },
      outerVisible: this.isEditReceivableplan,
      form: {
        value: "",
        contractId: "",
        totalPrice: "",
        signDate: "",
        // periodsNum: 1,
        customerId: "",
        departId: "",
        showTable: false, //  
        userId: "",
        editTable: false
      },
      tableData: [],
      clientList: [], // 
      contractList: [], // 
      departmentsList: [], // 
      userList: [] // 
    };
  },
  methods: {
    // 
    handleEdit(index, row) {
      this.$confirm("", "", {
        distinguishCancelAndClose: true,
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(() => {
          // 1 echoBalance
          // editBalancetableData
          let residue = this.form.residue;
          // 2 tableData
          for (let i = 0; i < this.tableData.length; i++) {
            // 3 n
            let num = 0;
            // 4 
            for (let j = 0; j <= i; j++) {
              num += this.tableData[j].price;
            }
            // 5 
            // 
            // 
            this.tableData[i].residue = residue - num;
          }
          // 6 
          this.tableData.splice(index);
          // 7 htmleditBalance
          this.form.editTable = true;
          // 8 editBalance
          this.addPeriods();
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 
    addPeriods() {
      let residue, startDatePicker;
      let length = this.tableData.length;
      // 
      if (length) {
        residue = this.tableData[length - 1].residue;
        let that = this;
        startDatePicker = {
          // 
          disabledDate(time) {
            if (that.tableData[length - 1]) {
              return (
                time.getTime() <
                new Date(that.tableData[length - 1].opeDate).getTime()
              ); //-8.64e6
            }
          }
        };
      } else {
        // 
        residue = this.form.residue;
        startDatePicker = {
          // 
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7; //-8.64e6
          }
        };
      }
      this.tableData.push({
        edit: true,
        price: residue, // 
        opeDate: "", // 
        residue, // 
        startDatePicker
      });
    },
    // 
    finish(index, row) {
      let { price, opeDate } = row;
      // 
      let residue = row.residue - row.price;
      // 
      if (!price || !opeDate || residue < 0) {
        this.$message.info("");
        return;
      }
      // 
      this.tableData[index].residue = residue;
      // 
      if (row.residue > 0) {
        this.addPeriods();
      }
      this.tableData[index].edit = false;
    },

    // 
    userChange(item) {
      if (item) {
        this.form.departId = item.departId;
      }
    },
    departChange() {
      let val = this.form.departId;
      if (val) {
        API.getHead(val).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.userList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.userList = res.data;
        });
      }
      this.form.userId = null;
    },

    userSearch(queryString, cb) {
      var restaurants = this.userList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    sureDialog() {
      let form = this.form;
      let userId = this.userList.find(ele => ele.value === form.userId).id;
      // let arr = [];
      // this.tableData.map(ele => {
      //   arr.push({
      //     price: ele.price,
      //     opeDate: ele.opeDate,
      //     id: ele.id
      //   });
      // });
      let params = {
        contractId: form.contractId,
        departId: form.departId,
        customerId: form.customerId,
        userId,
        opeDate: form.opeDate,
        price: form.price,
        id: form.id
        // planPayments: JSON.stringify(arr)
      };
      console.log(params);
      API.updateContractPaymentPlan(params)
        .then(res => {
          console.log(res);
          this.$message({
            type: "success",
            message: ""
          });
          this.$emit("sureDialogFather");
          this.closeDialog();
        })
        .catch(err => {
          console.error(err);
        });
    },
    // 
    clearCustormer() {
      this.form.contractId = "";
      this.contractList = [];
    },
    // 
    clearFollowUser() {
      if (this.form.departId) {
        this.departChange();
      }
    },

    // 
    closeDialog() {
      this.outerVisible = false;
      this.$refs.form.resetFields();
      this.form = {
        value: "",
        contractId: "",
        totalPrice: "",
        signDate: "",
        customerId: "",
        departId: "",
        editTable: false,
        showTable: false, //  
        userId: ""
      };
      this.tableData = [];
      this.$emit("closeDialogFather");
    }
  },
  watch: {
    isEditReceivableplan(val) {
      this.outerVisible = this.isEditReceivableplan;
      if (!val) return;
      let data = Object.assign({}, this.editPlanData);
      console.log(data);
      this.form = {
        customerId: data.customerId,
        contractId: data.contractId,
        departId: data.departId,
        userId: data.userName,
        opeDate: data.opeDate,
        price: data.price,
        id: data.id
      };
      // 
      // API.getPaymentListByContractId({ contractId: data.contractId }).then(
      //   res => {
      //     console.log(res.data);
      //     this.tableData = res.data;
      //   }
      // );

      // 
      API.getContractByCustomerId(data.customerId)
        .then(res => {
          this.contractList = res.data;          
          // let item = res.data.find(ele => ele.id === data.contractId);
          // this.form.totalPrice = item.totalPrice;
          // this.form.signDate = item.signDate;
          // this.form.payment = item.payment;
          // this.form.residue = item.totalPrice - item.payment;
          // this.form.showTable = true;
        })
        .catch(err => {
          console.error(err);
        });
      // 
      API.getCustomerByCompanyName().then(res => {
        res.data.forEach(item => {
          item.value = item.name;
        });
        this.clientList = res.data;
      });
      // 
      API.getAllUser().then(res => {
        res.data.forEach(item => {
          item.value = item.name;
        });
        this.userList = res.data;
      });
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
    }
  },
  computed: {
    //  
    editBalance() {
      // 
      return (index, row) => {
        let residue;
        let length = this.tableData.length;
        if (length > 1) {
          residue = this.tableData[length - 2].residue;
        } else {
          residue = this.form.residue;
        }

        let result = residue - this.tableData[length - 1].price;
        if (result < 0) {
          return "";
        } else {
          return result;
        }
      };
    },
    // 
    echoBalance() {
      return (index, row) => {
        // 1 
        let residue = this.form.residue;
        // 2 
        let num = 0;
        // 3 
        for (let i = 0; i < index; i++) {
          num += this.tableData[i].price;
        }
        // 4 
        return residue - num - row.price;
      };
    }
  }
};
</script>
<style scoped lang="scss">
/deep/.el-dialog {
  width: 40%;
  // .el-input:not(.el-input--suffix) {
  //   width: 50%;
  // }
}
</style>