<template>
  <div>
    <el-dialog
      title=""
      :visible.sync="dialogFormVisible"
      class="dialog"
      @close="$emit('closeDialog')"
    >
      <el-form :model="form" label-width="120px" :rules="rules" ref="form">
        <el-form-item label="" prop="name">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" prop="pid">
          <el-select v-model="form.pid" placeholder="">
            <el-option :value="0" label=""></el-option>
            <el-option
              v-for="(item, index) in departmentList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-cascader v-model="value" :options="treeData" @change="handleChange"></el-cascader> -->
        <!-- <el-form-item label="">
          <el-select v-model="form.region" placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>-->
        <el-form-item label="" prop="level">
          <el-select v-model="form.level" placeholder="110">
            <el-option v-for="(item,index) in 10" :key="index" :label="item" :value="item"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <!-- <el-input type="textarea" v-model="form.remarks"></el-input> -->
          <el-button type="success" @click="sureDialog"> </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/admin";

export default {
  name: "EditDepartment",
  props: [
    "isEditDepartment", // 
    "departmentList", // 
    "departmentDetail" // 
  ],
  data() {
    return {
      dialogFormVisible: this.isEditDepartment,
      form: {},
      treeData: [],
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        pid: [{ required: true, message: "", trigger: "change" }],
        level: [
          { required: true, message: "", trigger: "change" }
        ]
      }
    };
  },
  methods: {
    handleChange() {},
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let params = this.form;
        delete params.createTime;
        console.log(params);
        API.updateDepart(params)
          .then(res => {
            console.log(res);
            this.$message({
              message: "",
              type: "success"
            });
            this.$emit("sureDialogFather");
            this.$emit("closeDialog");
          })
          .catch(err => {
            console.error(err);
            if (err.indexOf("") === -1) this.$message.warning(err);
          });
      });
    },
    // 
    getTree(data) {
      data.forEach(function(item) {
        delete item.children;
        item.label = item.name;
        item.value = item.id;
      });
      //   id  KEY  map 
      var map = {};
      data.forEach(function(item) {
        map[item.id] = item;
      });
      var val = [];
      data.forEach(function(item) {
        // pid,mapid
        var parent = map[item.pid];
        // ,
        if (parent) {
          (parent.children || (parent.children = [])).push(item);
        } else {
          //mapID, item val
          val.push(item);
        }
      });
      return val;
    }
  },
  watch: {
    isEditDepartment(value) {
      this.dialogFormVisible = this.isEditDepartment;
      if (!value) return;
      // this.form.pid = this.editChildDepart || 0;
      this.form = Object.assign({}, this.departmentDetail);
      console.log(this.form);
    }
  }
};
</script>

<style lang="scss" scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>
