<template>
  <div>
    <el-dialog title="" :visible.sync="outerVisible" @close="closeDialog">
      <el-form :model="form" :label-width="formLabelWidth" :rules="rules" ref="form">
        <el-form-item label="" prop="name">
          <el-input placeholder="" v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="money">
          <el-input-number v-model="form.money" :precision="2" :min="0" :step="100"></el-input-number>
        </el-form-item>
        <el-form-item label="" prop="moneyGift">
          <el-input-number v-model="form.moneyGift" :precision="2" :min="0" :step="100"></el-input-number>
        </el-form-item>
        <el-form-item label="" prop="months">
          <el-input-number v-model="form.months" :step="1" :min="0"></el-input-number>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input type="textarea" :rows="4" placeholder="" v-model="form.description"></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="form.type" placeholder="" clearable class="item-css">
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="2"></el-option>
            <el-option label="" :value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button @click="sureDialog" type="success"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/combo";

// 
export default {
  name: "EditChannel",
  props: ["isEditCombo", "detailData"],
  data() {
    return {
      outerVisible: this.isEditCombo,
      formLabelWidth: "100px",
      form: {
        name: "", // 
        months: "", // 
        moneyGift: "", // 
        money: "", // 
        type: "", // 
        description: "" // 
      },
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        money: [{ required: true, message: "", trigger: "blur" }],
        moneyGift: [
          { required: true, message: "", trigger: "blur" }
        ],
        months: [
          { required: true, message: "", trigger: "blur" }
        ],
        description: [
          { required: true, message: "", trigger: "blur" }
        ],
        type: [{ required: true, message: "", trigger: "change" }]
      }
    };
  },
  methods: {
    // 
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather", this.outerVisible);
    },
    // 
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let params = this.form;
        console.log(params);

        API.updatePackage(params)
          .then(res => {
            this.$message({
              message: "",
              type: "success"
            });
            this.form = {
              name: "", // 
              months: "", // 
              moneyGift: "", // 
              money: "", // 
              type: "", // 
              description: "" // 
            };
            this.closeDialog();
            this.$emit("sureDialogFather");
          })
          .catch(err => {
            if (err.indexOf("") === -1) this.$message.warning(err);
          });
      });
    }
  },
  mounted() {},
  watch: {
    isEditCombo(value) {
      this.outerVisible = this.isEditCombo;
      if (!value) return;
      this.form = Object.assign({}, this.detailData);
      this.$nextTick(() => {
        this.$refs.form.clearValidate();
      });
    }
  }
};
</script>
<style lang='scss' scoped>
.item-css {
  width: 100%;
}
</style>