<template>
  <div>
    <!--  -->
    <el-dialog title="" :visible.sync="outerVisible" width="40%" @close="closeDialog">
      <el-form :model="form" label-width="120px" ref="form" :rules="rules">
        <el-form-item label="" prop="opDate">
          <el-date-picker
            v-model="form.opeDate"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss "
            placeholder=""
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="customerId">
          <el-select v-model="form.customerId" placeholder="" disabled>
            <el-option
              v-for="(item, index) in clientList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="contractId">
          <el-select
            v-model="form.contractId"
            :no-data-text="form.customerId?'':''"
            placeholder=""
            disabled
            @change="contractChange"
          >
            <el-option
              v-for="(item, index) in contractList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="payType">
          <el-select v-model="form.payType" placeholder="" clearable>
            <el-option
              v-for="(item, i) in payTypeList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" v-if="form.payType==3" prop="planpayId">
          <el-select
            v-model="form.planpayId"
            :no-data-text="form.contractId?'':''"
            placeholder=""
            clearable
          >
            <el-option
              v-for="(item, index) in periodList"
              :key="index"
              :label="item.opeDate.slice(0,10)"
              :value="item.id"
            >
              <span style="float: right; color: #8492a6; font-size: 13px">{{ item.price }}</span>
              <span style="float: left;">{{ item.opeDate.slice(0,10) }}</span>
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="price">
          <el-input v-model="form.price" disabled placeholder=""></el-input>
        </el-form-item>

        <el-form-item label="" v-if="form.payType!==1" prop="isOver">
          <el-select v-model="form.isOver" placeholder="" clearable>
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="0"></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="payNo">
          <el-select v-model="form.payNo" placeholder="">
            <el-option
              v-for="(item, i) in payaccountList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="">
          <el-input
            style="width:60%"
            v-model="form.remarks"
            placeholder=""
            type="textarea"
            :autosize="{ minRows: 2, maxRows: 10}"
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="sureDialog" type="success"></el-button>
          <el-button @click="closeDialog"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
// 
import API from "@/api/payment";
export default {
  name: "EditReceivablerecord",
  props: ["isEditReceivablerecord", "editRecordData"],
  data() {
    return {
      outerVisible: this.isEditReceivablerecord,
      departmentsList: [], // 
      clientList: [], // 
      contractList: [], //
      periodList: [], // 
      payaccountList: [], // 
      payTypeList: [], // 
      userList: [], // 
      form: {
        opeDate: "",
        price: "",
        customerId: "",
        contractId: "",
        planpayId: "",
        isOver: "",
        payNo: "",
        payType: "",
        departId: "",
        userId: "",
        remarks: ""
      },
      rules: {
        opeDate: [
          { required: true, message: "", trigger: "change" }
        ],
        customerId: [
          { required: true, message: "", trigger: "change" }
        ],
        contractId: [
          { required: true, message: "", trigger: "change" }
        ],
        payType: [
          { required: true, message: "", trigger: "change" }
        ],
        price: [{ required: true, message: "", trigger: "blur" }],
        isOver: [
          { required: true, message: "", trigger: "change" }
        ],
        planpayId: [
          { required: true, message: "", trigger: "change" }
        ],
        payNo: [
          { required: true, message: "", trigger: "change" }
        ],
        departId: [
          { required: true, message: "", trigger: "change" }
        ],
        userId: [{ required: true, message: "", trigger: "change" }]
      },
      contractNameId: "" // id
    };
  },
  methods: {
    // 
    userChange(item) {
      if (item) {
        this.form.departId = item.departId;
      }
    },
    // 
    clearFollowUser() {
      if (this.form.departId) {
        this.departChange();
      }
    },
    departChange() {
      let val = this.form.departId;
      if (val) {
        API.getHead(val).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.userList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.userList = res.data;
        });
      }
      this.form.userId = null;
    },
    // 
    clientSearch(queryString, cb) {
      var restaurants = this.clientList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },

    customerSelect(e) {
      // console.log(item.id)
      API.getContractByCustomerId(e.id)
        .then(res => {
          this.$nextTick(() => {
            this.contractList = res.data;
          });
        })
        .catch(err => {
          console.error("");
        });
    },
    contractChange(e) {
      console.log(e);
      API.getPaymentListByContractId({ contractId: e }).then(res => {
        console.log(res);
        this.periodList = res.data;
      });
    },
    // 
    sureDialog() {
      let form = this.form;
      // let userId = this.userList.find(ele => ele.value === form.userId).id;
      // let payNo = this.payaccountList.find(ele => ele.val === form.payNo).code;
      // let payType = this.payTypeList.find(ele => ele.val === form.payType).code;
      let params = {
        opeDate: form.opeDate,
        price: form.price,
        customerId: form.customerId,
        departId: form.departId,
        id: form.id,
        contractId: form.contractId,
        planpayId: form.payType == 1 ? null : form.planpayId,
        isOver: form.payType == 1 ? 1 : form.isOver,
        payNo: form.payNo,
        payType: form.payType,
        userId: form.userId,
        remarks: form.remarks
      };
      console.log(params);

      API.updateContractPaymentPlan(params)
        .then(res => {
          console.log(res);
          this.$message({
            message: "",
            type: "success"
          });
          this.$emit("sureDialogFather");
          this.closeDialog();
        })
        .catch(err => {
          if (err.indexOf("") === -1) this.$message.warning(err);
        });
    },
    userSearch(queryString, cb) {
      var restaurants = this.userList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      cb(results);
    },
    clearCustormer() {
      this.form.contractId = "";
      this.contractList = [];
    },
    // 
    closeDialog() {
      this.$refs.form.resetFields();
      this.outerVisible = false;
      this.$emit("closeDialogFather");
    }
  },
  watch: {
    isEditReceivablerecord(val) {
      this.outerVisible = this.isEditReceivablerecord;
      if (!val) return;
      // let detail = this.editRecordData;
      let form = Object.assign({}, this.editRecordData);
      form.userId = form.departId;
      form.payNo = form.payNoCode || form.payNo;
      form.payType = form.payTypeCode || form.payType;
      this.form = form;

      API.getPaymentListByContractId({ contractId: form.contractId }).then(
        res => {
          this.periodList = res.data;
        }
      );
      API.getContractByCustomerId(this.editRecordData.customerId)
        .then(res => {
          this.$nextTick(() => {
            this.contractList = res.data;
          });
        })
        .catch(err => {
          if (err.indexOf("") === -1) this.$message.warning(err);
        });

      // 
      API.getCustomerByCompanyName().then(res => {
        res.data.forEach(item => {
          item.value = item.name;
        });
        this.clientList = res.data;
      });
      // 
      API.getfindPaymentType().then(res => {
        this.payTypeList = res.data;
      });
      // 
      API.getContractPayNo().then(res => {
        this.payaccountList = res.data;
      });
      // 
      API.getAllUser().then(res => {
        res.data.forEach(item => {
          item.value = item.name;
        });
        this.userList = res.data;
      });
      // 
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
    }
  }
};
</script>
<style lang='scss' scoped>
/deep/.el-dialog {
  width: 40%;
  .el-input {
    width: auto;
  }
  // .el-input:not(.el-input--suffix) {
  //   width: 1%;
  // }
}
</style>