<template>
  <div class="business-type-container">
    <div class="business-btn">
      <el-button type="primary" v-permi="'/businessType/addType'" @click="showAddDialog = true"></el-button>
      <el-button type="danger" :disabled="!selectedItem.length" @click="delBusiness"></el-button>
    </div>
    <el-table
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      @selection-change="handleSelectionChange"
      border
      :fit="true"
    >
      <el-table-column type="selection" width="55" align="center" fixed></el-table-column>
      <el-table-column label="" prop="name" width align="center"></el-table-column>
      <el-table-column label="" prop="cycle" width align="center">
        <template slot-scope="scope">{{scope.row.cycle==='1'?"":""}}</template>
      </el-table-column>
      <el-table-column label="" width="150" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            plain
            v-permi="'/businessType/updateType'"
            @click="handleEdit(scope.$index, scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- {{tableData}} -->
    <!--  -->
    <div>
      <el-pagination
        class="itemPage"
        @size-change="sizeChangePage"
        @current-change="currentChangePage"
        :current-page="pageObj.pageNum"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pageObj.totalSize"
      ></el-pagination>

      <!--  -->
      <new-business
        @closeDialog="showAddDialog=false"
        @sureDialogFather="getTableData()"
        :showAddDialog="showAddDialog"
      ></new-business>
      <!--  -->
      <edit-business
        @closeDialog="showEditDialog=false"
        @sureDialogFather="getTableData()"
        :showEditDialog="showEditDialog"
        :businessDetail="businessDetail"
      ></edit-business>
    </div>
  </div>
</template>

<script>
import API from "@/api/business";
import NewBusiness from "./components/NewBusiness";
import EditBusiness from "./components/EditBusiness";
export default {
  name: "businessType",
  components: { NewBusiness, EditBusiness },
  data() {
    return {
      // 
      showAddDialog: false,
      // 
      showEditDialog: false,
      //
      tableData: [],
      // 
      selectedItem: [],
      // 
      pageObj: {
        pageNum: 1,
        pageSize: 10,
        totalSize: null
      },
      // 
      businessDetail: {}
    };
  },
  methods: {
    // 
    handleEdit(index, row) {
      this.businessDetail = row;
      this.showEditDialog = true;
    },
    // 
    sizeChangePage(e) {
      console.log(e);
      this.pageObj.pageSize = e;
      this.getTableData();
    },
    // 
    currentChangePage(e) {
      console.log(e);
      this.pageObj.pageNum = e;
      this.getTableData();
    },
    // 
    delBusiness() {
      this.$confirm("", "", {
        distinguishCancelAndClose: true,
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(() => {
          let arr = [];
          let selectedItem = this.selectedItem;
          for (const item of selectedItem) {
            arr.push(item.id);
          }
          let params = { ids: arr.join(",") };
          API.delType(params)
            .then(res => {
              console.log(res);
              this.$message({
                type: "success",
                message: ""
              });
              this.getTableData();
            })
            .catch(err => {
              console.error(err);
              if (err.indexOf("") === -1) this.$message.warning(err);
            });
        })
        .catch(err => {
          console.error(err);
          this.$message({
            type: "error",
            message: ""
          });
        });
    },
    // 
    handleSelectionChange(e) {
      this.selectedItem = e;
    },
    // 
    getTableData() {
      let params = {
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize
      };
      API.findType(params)
        .then(res => {
          let data = res.data;
          this.pageObj.totalSize = data.total;
          this.tableData = data.list;
        })
        .catch(err => {
          console.error(err);
          this.$message({
            type: "error",
            message: err
          });
        });
    }
  },
  mounted() {
    this.getTableData();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}
</style>
