<template>
  <div>
    <el-row>
      <el-col :span="24">
        <el-col :span="18">
          <h1>{{message.companyName}}</h1>
        </el-col>
      </el-col>
    </el-row>
    <!--  -->
    <el-row class="el-dro">
      <el-button type="primary" @click="isShowCustomer=true"></el-button>
      <el-button type="primary" @click="isShowEdit=true"></el-button>
      <el-button type="danger" @click="deletehigh"></el-button>
      <el-button type="warning" @click="showSend"></el-button>
    </el-row>

    <el-tabs type="card" @tab-click="handleClick">
      <el-tab-pane label="" class="quanjing">
        <el-card class="box-card">
          <div>
            <div class="text"></div>
            <div class="text">
              <p></p>
              <p>{{message.serviceName}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.companyName}}</p>
            </div>
            <el-divider></el-divider>
            <div class="text"></div>
            <div class="text">
              <p></p>
              <p>{{message.telephone}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.wechat}}</p>
            </div>
            <div class="text">
              <p>QQ</p>
              <p>{{message.qq}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.email}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.serviceName}}</p>
            </div>
            <el-divider></el-divider>
            <div class="text"></div>
            <div class="text">
              <p></p>
              <p>{{message.statusStr}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.channelName}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.nextFollowTime}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.remarks}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.gradeStr}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.searchKeywords}}</p>
            </div>
            <el-divider></el-divider>
            <div class="text"></div>
            <div class="text">
              <p></p>
              <p>{{message.toPublicTime}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.customerServiceName}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.createUser}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.followName}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.followDepart}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.followDepart}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.createTime}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.updateTime}}</p>
            </div>
          </div>
        </el-card>
        <follow-up-history
          class="genjin box-card"
          @writeFollow="isShowFollow= true"
          :customerId="Number(highId)"
          :type="1"
          ref="followUpHistory"
        ></follow-up-history>
      </el-tab-pane>

      <el-tab-pane label="">
        <tab-task ref="tabTask" :customerId="highId"></tab-task>
      </el-tab-pane>
      <el-tab-pane label="">
        <tab-annex ref="tabAnnex" :customerId="highId"></tab-annex>
      </el-tab-pane>
      <el-tab-pane label="">
        <el-table :data="tableData" tooltip-effect="dark" style="width: 100%;" border :fit="true">
          <el-table-column prop="name" label="" width align="center"></el-table-column>
          <el-table-column prop="name" label="" width align="center"></el-table-column>
          <el-table-column prop="name" label="" width align="center"></el-table-column>
          <el-table-column prop="name" label="" width align="center"></el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
    <edit-customer
      :isShowEdit="isShowEdit"
      :customerId="highId"
      :customerLevel="customerLevel"
      :customerTypeList="customerTypeList"
      @sureDialogFather="getSonSure"
      @closeDialogFather="isShowEdit=false"
    ></edit-customer>

    <NewSend
      :isShowSend="isShowSend"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewSend>

    <Followup
      :isShowFollow="isShowFollow"
      :customerId="highId"
      @sureDialogFather="$refs.followUpHistory.getFollowData()"
      @closeDialogFather="getSonCancel"
    ></Followup>
    <!--  -->
    <move-client
      :followUserList="followUserList"
      :checkboxId="[highId]"
      :isShowCustomer="isShowCustomer"
      @closeDialogFather="isShowCustomer=false"
      @sureDialogFather="$router.go(-1)"
    ></move-client>
  </div>
</template>

<script>
// 
import API from "@/api/highseas";
import TabTask from "@/components/element/TabTask";
import TabAnnex from "@/components/element/TabAnnex";
import EditCustomer from "@/components/element/EditCustomer"; // 
import FollowUpHistory from "@/components/element/FollowUpHistory"; // 
import NewSend from "@/components/element/NewSend"; // 
import Followup from "@/components/element/Followup"; // 
import MoveClient from "./components/MoveClient"; // 
export default {
  name: "highSeasdetails",
  components: {
    EditCustomer,
    NewSend,
    Followup,
    FollowUpHistory,
    MoveClient,
    TabAnnex,
    TabTask
  },
  data() {
    return {
      isShowCustomer: false,
      newfuzeren: "", // 
      isShowEdit: false,
      customerLevel: [], // 
      customerTypeList: [], // 
      followUserList: [], // 
      isShowSend: false,
      isShowFollow: false,
      highId: null, // id
      message: {}, // 
      tableData: []
    };
  },
  methods: {
    // 
    save() {
      let obj = {
        ids: this.highId,
        userId: this.newfuzeren
      };
      API.posttoCustomer(obj).then(res => {
        if (res.status == 200) {
          this.$router.push({ path: "highSeas" });
          this.$message(
            {
              message: "",
              type: "success"
            },
            1000
          );
          this.isShowCustomer = false;
        }
      });
    },

    // 
    deletehigh() {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(() => {
          API.postdeleteSeaCustomer({ ids: this.highId }).then(res => {
            this.$router.push({ path: "highSeas" });
            this.$message({
              message: "",
              type: "success"
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    //  
    showSend() {
      this.isShowSend = true;
    },
    // 
    showFollow() {
      this.isShowFollow = true;
    },
    // Dialog
    getSonSure() {
      this.getSonCancel();
      this.getDetailData();
    },
    // Dialog
    getSonCancel() {
      this.isShowEdit = false;
      this.isShowSend = false;
      this.isShowFollow = false;
    },
    // tab 
    handleClick(tab, event) {
      if (tab.label == "") {
      } else if (tab.label == "") {
        this.$refs.tabAnnex.getAnnexTable();
      } else if (tab.label == "") {
        this.$refs.tabTask.getTaskTable();
      } else if (tab.label == "") {
        // console.log('')
      }
    },
    getDetailData() {
      API.getfindSeaCustomerById(this.highId).then(res => {
        this.message = res.data;
      });
    }
  },

  mounted() {
    this.highId = this.$route.query.id;
    this.getDetailData();
    // api
    API.getCustomerLevel().then(res => {
      let obj = { code: 0, val: "" };
      res.data.unshift(obj);
      this.customerLevel = res.data;
    });
    // api
    API.getType().then(res => {
      let obj = { id: 0, name: "" };
      res.data.unshift(obj);
      this.customerTypeList = res.data;
    });
    // 
    API.getAllUser().then(res => {
      this.followUserList = res.data;
    });
  }
};
</script>
<style lang='scss' scoped>
.el-dropdown-link {
  cursor: pointer;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.el-dro {
  margin-top: 20px;
  margin-bottom: 20px;
}

.text {
  font-size: 14px;
  font-size: 14px;
  margin-bottom: 18px;
  display: flex;
  & > :first-child {
    width: 100px;
    // text-align: right;
    // margin-right:10px;
  }
  & > :last-child {
    flex: 1;
    word-break: break-all;
    word-wrap: break-word;
  }
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.box-card {
  width: 48%;
}
.genjin {
  position: absolute;
  top: 0;
  right: 0;
}
.quanjing {
  position: relative;
}
span {
  font-size: 18px;
}
</style>