<template>
  <div>
    <el-dialog
      title=""
      :visible.sync="outerVisible"
      :modal-append-to-body="false"
      class="lodings2"
      @close="closeDialog"
    >
      <div>
        1.
        <el-link type="success" @click="downfile"></el-link>
      </div>
      <div></div>
      <div>1</div>
      <div>2</div>
      <div>310000</div>
      <div>2</div>
      <el-upload
        class="upload-demo"
        ref="upload"
        :action="$store.state.baseURL+'clue/uploadFile'"
        :file-list="fileList"
        :auto-upload="false"
        :on-success="uploadSuccess"
        :on-error="uploadError"
      >
        <el-button slot="trigger" size="small" type="primary">+ </el-button>
      </el-upload>
      <div style="text-align:left;margin-top:40px">
        <el-button type="success" @click="$refs.upload.submit();"></el-button>
        <el-button type="info" @click="closeDialog"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  props: ["uploadDialog"],
  data() {
    return {
      outerVisible: this.uploadDialog,
      fileList: []
    };
  },
  methods: {
    // 
    uploadSuccess(res) {
      if (res.status !== 200) {
        this.$message({
          type: "info",
          message: res.msg
        });
        return;
      }
      this.fileList = [];
      // this.$refs.form.resetFields();
      this.$message({
        type: "success",
        message: ""
      });
      this.$emit("sureDialogFather");
      this.closeDialog();
    },
    // 
    uploadError(res) {
      res = res.toString().slice(7);
      let msg = JSON.parse(res).message;
      this.$message({
        type: "error",
        message: "" + msg
      });
    },
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialog");
    },
    // 
    downfile() {
      window.location.href = this.$store.state.baseURL + "clue/downFile";
    }
  },
  watch: {
    uploadDialog(newVal) {
      this.outerVisible = newVal;
      if (!newVal) return;
    }
  }
};
</script>

<style lang="scss" scoped>
/deep/.el-dialog {
  width: 40%;
  div {
    padding-left: 20px;
    margin-bottom: 20px;
  }
}
</style>
