<template>
  <div>
    <el-dialog
      title=""
      :close-on-click-modal="false"
      :visible.sync="dialogVisible"
      @close="closeDialog"
    >
      <el-form :model="form" :rules="rules" label-width="120px" ref="form">
        <el-form-item label="" prop="name">
          <el-input v-model="form.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="customerId">
          <el-select
            v-model="form.customerId"
            @change="customerChange"
            placeholder=""
            class="item-css"
            filterable
          >
            <el-option
              v-for="(item, index) in restaurants"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="linkmanId">
          <el-select v-model="form.linkmanId" placeholder="">
            <el-option
              v-for="(item, i) in LinkmenList"
              :key="i"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="serviceId">
          <el-select v-model="form.serviceId" placeholder="">
            <el-option
              v-for="(item, i) in BusinessTypes"
              :key="i"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="expectPrice">
          <el-input v-model="form.expectPrice" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=" ">
          <el-date-picker
            v-model="form.expectDate"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder=""
            class
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="stage">
          <el-select v-model="form.stage" placeholder="" clearable class>
            <el-option v-for="(item, i) in SaleStage" :key="i" :label="item.val" :value="item.code"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.type" placeholder="" clearable class>
            <el-option
              v-for="(item, i) in ChanceType"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="">
          <el-date-picker
            v-model="form.createTime"
            type="datetime"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder=""
            class
          ></el-date-picker>
        </el-form-item>-->
        <el-form-item label="" prop="source">
          <el-select v-model="form.source" placeholder="" clearable class>
            <el-option
              v-for="(item, i) in clientSourceList"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="form.nextTime"
            type="datetime"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder=""
            class
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-input
            v-model="form.remarks"
            style="width:60%;"
            maxlength="100"
            show-word-limit
            type="textarea"
            :autosize="{ minRows: 4, maxRows: 10}"
            placeholder=""
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="sureDialog" type="success"></el-button>
        <el-button @click="closeDialog"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// 
import API from "@/api/chance";
export default {
  name: "EditChance",
  props: ["isShowChanceEdit", "opportunityId", "customerId"],
  data() {
    return {
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        customerId: [
          { required: true, message: "", trigger: "change" }
        ],
        linkmanId: [
          { required: true, message: "", trigger: "change" }
        ],
        serviceId: [
          { required: true, message: "", trigger: "change" }
        ],
        stage: [
          { required: true, message: "", trigger: "change" }
        ],
        expectPrice: [
          { required: true, message: "", trigger: "change" }
        ],
        type: [
          { required: true, message: "", trigger: "change" }
        ],
        source: [
          { required: true, message: "", trigger: "change" }
        ],
        followDepart: [
          { required: true, message: "", trigger: "change" }
        ],
        followUser: [
          { required: true, message: "", trigger: "change" }
        ]
      },
      dialogVisible: this.isShowChanceEdit,
      restaurants: [], // 
      LinkmenList: [], // 
      BusinessTypes: [], // 
      SaleStage: [], // 
      ChanceType: [], // 
      clientSourceList: [], // 
      departmentsList: [], // 
      headList: [], // 
      form: {},
      followDepart: null, // id
      followUser: null // id
    };
  },
  methods: {
    // 
    closeDialog() {
      this.dialogVisible = false;
      this.$emit("closeDialogFather");
    },
    // 
    sureDialog() {
      let form = this.form;
      console.log(form);
      let params = {
        id: this.opportunityId,
        name: form.name,
        customerId: form.customerId,
        linkmanId: form.linkmanId,
        serviceId: form.serviceId,
        expectPrice: form.expectPrice,
        expectDate: form.expectDate,
        stage: form.stage,
        type: form.type,
        getTime: form.getTime,
        source: form.source,
        nextTime: form.nextTime,
        followDepart: form.followDepart,
        followUser: form.followUser,
        // probability: form.probability,
        remarks: form.remarks
      };
      API.postupdateChance(params).then(res => {
        console.log(res);
        if (res.status == 200) {
          this.$message({
            message: "",
            type: "success"
          });
          this.$emit("sureDialogFather");
          this.closeDialog();
        }
      });
    },
    customerChange(val) {
      this.form.linkmanId = null
      API.getCustomerLinkman(val).then(res => {
        this.LinkmenList = res.data;
      });
    }
  },
  watch: {
    isShowChanceEdit(value) {
      this.dialogVisible = this.isShowChanceEdit;
      if (!value) return;

      Promise.all([
        API.getCustomerByCompanyName(),
        API.getAllUser(),
        API.findChanceById(this.opportunityId)
      ]).then(resArr => {
        console.log(resArr);
        this.restaurants = resArr[0].data;

        // 
        this.headList = resArr[1].data;

        // 
        let formData = resArr[2].data;
        // 
        this.customerChange(formData.customerId);
        formData.regionArr = [formData.province, formData.city];
        this.form = formData;
        this.$nextTick(() => {
          this.$refs.form.clearValidate();
        });
      });
      // 
      API.getType().then(res => {
        this.BusinessTypes = res.data;
      });
      // 
      API.getSaleStage().then(res => {
        this.SaleStage = res.data;
      });
      // 
      API.getChanceType().then(res => {
        this.ChanceType = res.data;
      });
      //  
      API.customerFrom().then(res => {
        this.clientSourceList = res.data;
      });
      // 
      // API.getchannel().then(res => {
      //   this.clientSourceList = res.data;
      // });
      // 
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
    }
  }
};
</script>
<style scoped lang="scss">
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>