<template>
  <div>
    <el-form :model="form" label-width="100px">
      <el-row>
        <el-row :span="24">
          <el-form-item label="">
            <ul>
              <li
                v-for="(item, index) in getAllSupport"
                :key="index"
                class="list-li"
                @click="actives(index,item.code)"
                :class="{'pote':ins == index}"
              >{{ item.val }}</li>
            </ul>
          </el-form-item>
        </el-row>
        <el-row :span="24">
          <el-form-item label="">
            <ul>
              <li
                v-for="(item, index) in getAllstate"
                :key="index"
                class="list-li"
                @click="actives1(index,item.code)"
                :class="{'pote':ins1 == index}"
              >{{ item.val}}</li>
            </ul>
          </el-form-item>
        </el-row>
        <el-row :span="24">
          <el-form-item label="">
            <ul>
              <li
                v-for="(item, index) in getAlltime"
                :key="index"
                class="list-li"
                @click="actives2(index,item.code)"
                :class="{'pote':ins2 == index}"
              >{{ item.name }}</li>
              <li
                @click="showDefinedDate = true;ins2 = -1"
                class="list-li"
                v-if="!showDefinedDate"
              ></li>
              <li v-else>
                <el-date-picker
                  :unlink-panels="true"
                  v-model="definedDate"
                  type="daterange"
                  @change="actives2('defined')"
                  range-separator=""
                  start-placeholder=""
                  end-placeholder=""
                ></el-date-picker>
              </li>
            </ul>
          </el-form-item>
        </el-row>
        <div class="slide"  :style="{'height':showMore?'170px':0}">
          <el-row :span="24">
            <el-col :span="8">
              <el-form-item label="">
                <el-cascader
                  :options="getRegion"
                  placeholder=""
                  clearable
                  class="item-css"
                  @change="handleChange"
                ></el-cascader>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="">
                <el-select
                  v-model="formdata.department"
                  placeholder=""
                  clearable
                  class="item-css"
                  @change="departs($event)"
                >
                  <el-option
                    v-for="(item, index) in getDepartments"
                    :key="index"
                    :label="item.name"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="">
                <el-select
                  v-model="formdata.headuser"
                  placeholder=""
                  clearable
                  :no-data-text="formdata.department?'':''"
                  class="item-css"
                  @change="tableApi()"
                >
                  <el-option
                    v-for="(item, index) in followUser"
                    :key="index"
                    :label="item.name"
                    :value="item.name"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :span="24">
            <el-col :span="8">
              <el-form-item label="">
                <el-select
                  v-model="formdata.CustomerLevel"
                  placeholder=""
                  clearable
                  class="item-css"
                  @change="tableApi"
                >
                  <el-option
                    v-for="(item, index) in getCustomerLevel"
                    :key="index"
                    :label="item.val"
                    :value="item.code"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="">
                <el-select
                  v-model="formdata.CustomerSource"
                  placeholder=""
                  clearable
                  class="item-css"
                  @change="tableApi"
                >
                  <el-option
                    v-for="(item, index) in getCustomerSource"
                    :key="index"
                    :label="item.name"
                    :value="item.name"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row :span="24">
            <el-col :span="8">
              <el-form-item label="">
                <el-select
                  v-model="formdata.searchType"
                  placeholder=""
                  clearable
                  class="item-css"
                >
                  <el-option
                    v-for="(item, index) in getsearchType"
                    :key="index"
                    :label="item.name"
                    :value="item.code"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="10">
              <el-form-item label="" class="input-search">
                <el-col :span="14">
                  <el-input
                    v-model="formdata.search"
                    @keydown.enter.native="tableApi"
                    placeholder=""
                    clearable
                  ></el-input>
                </el-col>
                <el-col :span="6">
                  <el-button type="primary" @click="tableApi"></el-button>
                </el-col>
              </el-form-item>
            </el-col>
          </el-row>
        </div>
        <p class="show-more-button">
          <el-button
            @click="showMore=!showMore"
            plain
            :icon="showMore?'el-icon-caret-top':'el-icon-caret-bottom'"
            type="primary"
            circle
            size="mini"
          ></el-button>
        </p>
      </el-row>
    </el-form>

    <el-row>
      <el-popover placement="top-start" width="300" trigger="click">
        <el-checkbox-group v-model="showCol" @change="colChange">
          <el-checkbox
            style="width:120px;line-height:25px"
            v-for="(item,index) in colOptions"
            :key="index"
            :label="item"
          ></el-checkbox>
        </el-checkbox-group>
        <el-button
          slot="reference"
          style="margin-right:10px;"
          type="primary"
          icon="el-icon-s-operation"
          plain
        ></el-button>
      </el-popover>
      <el-button
        type="primary"
        icon="el-icon-folder-add"
        v-permi="'/clue/addClue'"
        @click="addClueDialog = true"
      ></el-button>
      <el-button
        type="primary"
        icon="el-icon-money"
        v-permi="'/clue/uploadFile'"
        @click="uploadDialog = true"
      ></el-button>
      <!-- <el-button type="success" @click="publicClue" icon="el-icon-document-copy"></el-button> -->
      <!-- <el-button
        type="primary"
        @click="transferType='transfer';isShowClueTransfer=multipleSelection.length>0"
        icon="el-icon-ship"
        :disabled="!multipleSelection.length"
      ></el-button>-->
      <el-button
        type="primary"
        @click="transferType='copyClue';isShowClueTransfer=multipleSelection.length>0"
        icon="el-icon-circle-plus-outline"
        :disabled="multipleSelection.length!==1"
      ></el-button>
      <el-button
        type="danger"
        @click="transferType='remove';isShowClueTransfer=multipleSelection.length===1"
        icon="el-icon-remove-outline"
        :disabled="multipleSelection.length!==1"
      ></el-button>
      <!-- <el-button
        type="success"
        @click="isShowCustomer = true"
        :disabled="multipleSelection.length!==1"
        v-permi="'/clue/toCustomer'"
        icon="el-icon-s-fold"
      ></el-button>-->
      <el-dropdown trigger="click" @command="clueTransition">
        <el-button type="warning" :disabled="multipleSelection.length!==1" style="margin: 0 10px;">
          
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item v-permi="'/clue/toCustomer'" command=""></el-dropdown-item>
          <el-dropdown-item
            :disabled="multipleSelection[0]&&multipleSelection[0].isChange==1"
            command=""
          ></el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-button
        type="danger"
        :disabled="!multipleSelection.length"
        @click="deleteClue"
        icon="el-icon-delete"
      ></el-button>
      <el-button
        type="warning"
        icon="el-icon-message"
        :disabled="!multipleSelection.length"
        @click="sendMessageDialog = true"
      ></el-button>
      <!-- <el-button type="success" icon="el-icon-message" @click="exportTable" class="item"></el-button> -->
      <el-dropdown trigger="click" @command="exportTable">
        <el-button type="warning" style="margin-left:10px;">
          
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item
            v-for="(item,index) in excNames"
            :key="index"
            :command="item"
          >{{item.name}}</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-row>
    <el-table
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      @selection-change="handleSelectionChange"
      :row-class-name="tableRowClassName"
      border
      :fit="true"
    >
      <el-table-column type="selection" width="50" align="center" fixed></el-table-column>
      <el-table-column
        v-if="showCol.indexOf('')>-1"
        label=""
        prop="registerName"
        align="center"
      ></el-table-column>
      <el-table-column v-if="showCol.indexOf('')>-1" align="center" prop="guest" label=""></el-table-column>
      <el-table-column
        v-if="showCol.indexOf('')>-1"
        align="center"
        prop="linkName"
        label=""
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('')>-1"
        prop="telnum"
        label=""
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column align="center" v-if="showCol.indexOf('')>-1" prop="region" label=""></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('')>-1"
        prop="status"
        sortable
        label=""
      ></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('')>-1"
        prop="clueFrom"
        label=""
      ></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('')>-1"
        prop="userName"
        label=""
      ></el-table-column>
      <!-- <el-table-column  align="center" prop="contents" label=""></el-table-column> -->
      <el-table-column
        align="center"
        v-if="showCol.indexOf('')>-1"
        prop="nextTime"
        width="120"
        label=""
      ></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('')>-1"
        prop="createTime"
        label=""
      ></el-table-column>
      <el-table-column
        align="center"
        v-if="showCol.indexOf('')>-1"
        prop="level"
        label=""
        width="180px"
      ></el-table-column>
      <el-table-column align="center" v-if="showCol.indexOf('')>-1" prop="keyWords" label=""></el-table-column>
      <el-table-column label="" width="150" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            plain
            @click="handleEdit(scope.$index, scope.row)"
            v-permi="'/clue/updateClue'"
          ></el-button>
          <el-button size="mini" @click="handleView(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <div>
      <el-pagination
        class="itemPage"
        @size-change="sizeChangePage"
        @current-change="currentChangePage"
        :current-page="formdata.page"
        :page-sizes="[10, 20, 30, 40]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="totalSize"
      ></el-pagination>
    </div>
    <!--  -->
    <to-high-seas
      @closeDialogFather="isShowHighSeas=false"
      :isShowHighSeas="isShowHighSeas"
      @sureDialogFather="tableApi()"
      :clueDetail="multipleSelection[0]"
    ></to-high-seas>

    <!--  -->
    <add-clue
      :dialogFormVisible="addClueDialog"
      @closeDialogFather="addClueDialog=false"
      @sureDialogFather="tableApi()"
      :getDepartments="getDepartments"
    ></add-clue>

    <!--  -->
    <edit-clue
      :clueDetail="editData"
      :dialogFormVisible="showEditClue"
      @closeDialogFather="showEditClue=false"
      @sureDialogFather="tableApi()"
    ></edit-clue>

    <!--  -->
    <upload-clue
      :uploadDialog="uploadDialog"
      @sureDialogFather="tableApi()"
      @closeDialog="uploadDialog=false"
    ></upload-clue>

    <!--  -->
    <clue-transfer
      :isShowClueTransfer="isShowClueTransfer"
      :clueList="multipleSelection"
      :transferType="transferType"
      @closeDialogFather="isShowClueTransfer=false"
      @sureDialogFather="tableApi()"
    ></clue-transfer>

    <!--  -->
    <Customer
      :isShowCustomer="isShowCustomer"
      @closeDialogFather="isShowCustomer=false"
      :clueDetail="multipleSelection[0]"
      @sureDialogFather="tableApi()"
      :customerSource="getCustomerSource"
    ></Customer>

    <!--  -->
    <send-message
      :multipleSelection="multipleSelection"
      :isShowSend="sendMessageDialog"
      @closeDialogFather="sendMessageDialog=false"
    ></send-message>
  </div>
</template>

<script>
import API from "@/api/clue";
import * as filters from "@/filters/index";
import { filter } from "minimatch";
import ToHighSeas from "../components/ToHighSeas";
import ClueTransfer from "../components/ClueTransfer";
import Customer from "../components/Customer";
import EditClue from "../components/EditClue";
import SendMessage from "../components/SendMessage";
import AddClue from "../components/AddClue";
import UploadClue from "../components/UploadClue";
export default {
  name: "clue",
  components: {
    ClueTransfer,
    ToHighSeas,
    Customer,
    EditClue,
    SendMessage,
    UploadClue,
    AddClue
  },
  data() {
    return {
      isShowClueTransfer: false, // 
      isShowCustomer: false, // 
      showMore: false, // 
      isShowHighSeas: false, // 
      startDatePicker: {
        // 
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7; //-8.64e6
        }
      },
      transferType: null, // 
      showDefinedDate: false, // 
      definedDate: "", // 
      ins: 0,
      ins1: 0,
      ins2: 0,
      addClueDialog: false,
      uploadDialog: false,
      showEditClue: false, // 
      sendMessageDialog: false,
      getAllSupport: [], //
      getAllstate: [], //
      getAlltime: [], //
      getDepartments: [],
      followUser: [],
      getCustomerLevel: [],
      getCustomerSource: [],
      getsearchType: [
        { name: "", code: 1 },
        { name: "", code: 2 },
        { name: "", code: 3 },
        { name: "", code: 7 }

        // { name: "", code: 4 },
        // { name: "", code: 5 },
        // { name: "QQ", code: 6 }
      ],
      form: {
        name: "",
        region: "",
        date1: "",
        date2: "",
        delivery: false,
        type: [],
        resource: "",
        desc: ""
      },
      // 
      ruleForm: {
        registerName: null, // 
        guest: null, // 
        linkName: null, // 
        telnum: null, // 
        contents: null, // 
        nextFollwTime: null, // 
        clueFrom: null, // 
        status: null, // 
        remarks: null, // 
        level: null, // 
        keyWords: null, // 
        departName: null, // 
        followUserName: null, // 
        wechat: null, // 
        qq: null, // qq
        province: null, //
        city: null //
      },
      editData: {},
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        region: [
          { required: true, message: "", trigger: "change" }
        ]
      },
      totalSize: 0, //
      formLabelWidth: "120px",
      showCol: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      colOptions: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      // 
      formdata: {
        page: 1, //
        rows: 10, //
        timeCode: 0, //  0
        codeType: 3, // 3
        codeLevel: 4, // 4
        codeTime: 0, //
        searchType: null, //
        startTime: null, // 
        endTime: null, // 
        province: null, //
        city: null, //
        department: null, //
        CustomerSource: null, //
        headuser: null, //
        CustomerLevel: null, //
        search: null
      },
      tableData: [],
      multipleSelection: [], // 
      allCustomer: {
        arrlist: [],
        desc: "",
        arrlists: []
      },
      getRegion: [],
      excNames: [
        { api: "exportClueExc", name: "" },
        { api: "exportDataExc", name: "" },
        { api: "exportChannelExc", name: "" }
      ] // 
    };
  },

  methods: {
    clueTransition(name) {
      switch (name) {
        case "":
          this.isShowHighSeas = true;
          break;
        case "":
          this.isShowCustomer = true;
          break;

        default:
          break;
      }
    },
    colChange(col) {
      localStorage.setItem("clueTable", JSON.stringify(this.showCol));
    },
    tableRowClassName({ row, rowIndex }) {
      if (row.isChange == 1) {
        return "success-row";
      }
      return "";
    },
    // 
    deleteClue() {
      // 
      this.$confirm("", "", {
        distinguishCancelAndClose: true,
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(() => {
          let arr = [];
          for (const item of this.multipleSelection) {
            arr.push(item.id);
          }
          // ,
          API.delClue({ ids: arr.join(",") })
            .then(res => {
              this.$message({
                type: "success",
                message: ""
              });
              this.tableApi();
            })
            .catch(err => {
              if (err.indexOf("") === -1) this.$message.warning(err);
            });
        })
        .catch(action => {
          this.$message({
            type: "error",
            message: ""
          });
        });
    },
    // 
    sizeChangePage(e) {
      this.formdata.rows = e;
      this.tableApi();
    },
    // 
    currentChangePage(e) {
      this.formdata.page = e;
      this.tableApi();
    },
    multipleSelections(val) {
      console.log(val);
      if (this.allCustomer.arrlists.indexOf(val.id) == -1) {
        this.allCustomer.arrlists.push(val);
      } else {
        this.allCustomer.arrlists.splice(
          this.allCustomer.arrlists.indexOf(val),
          1
        );
      }
    },

    exportTable(obj) {
      let formdata = this.formdata;
      let params = {
        pageNum: formdata.page,
        pageSize: formdata.rows,
        clueType: formdata.codeType,
        status: formdata.codeLevel,
        startTime: formdata.startTime,
        endTime: formdata.endTime,
        timeCode: formdata.timeCode,
        province: formdata.province,
        city: formdata.city,
        departName: formdata.department,
        userName: formdata.headuser,
        customerGrade: formdata.CustomerLevel,
        fromName: formdata.CustomerSource,
        searchType: formdata.searchType,
        searchName: formdata.search
      };
      API[obj.api](params)
        .then(res => {
          var blob = new Blob([res], {
            type: "application/vnd.ms-excel;charset=utf-8"
          }); //vnd.ms-excel
          var downloadElement = document.createElement("a");
          var href = window.URL.createObjectURL(blob); //
          downloadElement.href = href;
          document.body.appendChild(downloadElement);
          downloadElement.setAttribute("download", obj.name + ".xls");
          downloadElement.click(); //
          document.body.removeChild(downloadElement); //
          window.URL.revokeObjectURL(href); //blob
        })
        .catch(err => {
          console.error(err);
        });
    },
    //
    actives(index, code) {
      this.ins = index;
      console.log(code);

      this.formdata.codeType = code;
      console.log("", this.formdata);
      this.tableApi();
    },
    actives1(index, code) {
      this.ins1 = index;
      this.formdata.codeLevel = code;
      console.log("", this.formdata);
      this.tableApi();
    },
    actives2(index, code) {
      // 
      if (index === "defined" && this.definedDate) {
        let startTime = this.definedDate[0];
        startTime = filters.parseTime(startTime);
        let endTime = this.definedDate[1];
        endTime = filters.parseTime(endTime);

        this.formdata.startTime = startTime;
        this.formdata.endTime = endTime;
        this.timeCode = null; // 
        this.tableApi();
      } else if (index !== "defined") {
        // 
        this.formdata.startTime = null; // 
        this.formdata.endTime = null;
        this.showDefinedDate = false;
        this.ins2 = index;
        this.formdata.timeCode = code;
        console.log("code", this.formdata.timeCode);
        console.log("", this.formdata);
        this.tableApi();
      } else {
        this.ins2 = -1;
      }
    },
    // 
    publicClue() {
      if (!this.multipleSelection.length) return;
      // 
      this.$confirm("", "", {
        distinguishCancelAndClose: true,
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(() => {
          let arr = [];
          for (const item of this.multipleSelection) {
            arr.push(item.id);
          }
          // ,
          API.toPublic({ ids: arr.join(",") })
            .then(res => {
              if (res.status !== 200) {
                throw new Error("");
              }
              this.$message({
                type: "success",
                message: ""
              });
              this.tableApi();
            })
            .catch(err => {
              if (err.indexOf("") === -1) this.$message.warning(err);
            });
        })
        .catch(action => {
          console.log(action);

          this.$message({
            type: "error",
            message: ""
          });
        });
    },
    //
    addClue() {
      let params = this.ruleForm;
      console.log(params);
      params.followTime = filters.parseTime(new Date(params.nextFollwTime));
      API.addClue(params)
        .then(res => {
          this.$message({
            message: "",
            type: "success"
          });
          this.addClueDialog = false;
          // 
          this.tableApi();
          // 
          this.ruleForm = {
            registerName: null, // 
            guest: null, // 
            linkName: null, // 
            telnum: null, // 
            contents: null, // 
            nextFollwTime: null, // 
            clueFrom: null, // 
            status: null, // 
            remarks: null, // 
            level: null, // 
            keyWords: null, // 
            departName: null, // 
            followUserName: null, // 
            wechat: null, // 
            qq: null, // qq
            province: null, //
            city: null //
          };
        })
        .catch(err => {
          console.log("add", err);
          if (err.indexOf("") === -1) this.$message.warning(err);
        });
    },
    //
    tableApi() {
      let params = {
        pageNum: this.formdata.page,
        pageSize: this.formdata.rows,
        clueType: this.formdata.codeType,
        status: this.formdata.codeLevel,
        startTime: this.formdata.startTime,
        endTime: this.formdata.endTime,
        timeCode: this.formdata.timeCode,
        province: this.formdata.province,
        city: this.formdata.city,
        departName: this.formdata.department,
        userName: this.formdata.headuser,
        customerGrade: this.formdata.CustomerLevel,
        fromName: this.formdata.CustomerSource,
        searchType: this.formdata.searchType,
        searchName: this.formdata.search
      };
      console.log("", params);
      API.getAll(params).then(res => {
        this.totalSize = res.data.total;
        let list = res.data.list;
        list.forEach(item => {
          switch (item.status) {
            case "0":
              item.status = "";
              break;
            case "1":
              item.status = "";
              break;
            case "2":
              item.status = "";
              break;
            case "3":
              item.status = "";
              break;

            default:
              item.status = "";
              break;
          }
        });
        this.tableData = list;
        console.log("", this.tableData);
      });
    },
    //
    handleChange(val) {
      this.formdata.province = val[0];
      this.formdata.city = val[1];
      this.tableApi();
    },
    // 
    handldiqu(val) {
      this.ruleForm.province = val[0];
      this.ruleForm.city = val[1];
      console.log(this.ruleForm.province);
      console.log(this.ruleForm.city);
    },
    //

    // 
    departs(val) {
      if (val != "") {
        API.getHead(val).then(res => {
          this.followUser = res.data;
        });
      }
      this.formdata.headuser = "";
      this.tableApi();
    },
    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;
      // console.log(this.multipleSelection);
    },

    // 
    handleEdit(index, row) {
      this.editData = row;
      this.showEditClue = true;
    },
    // 
    // handleDelete(index, row) {
    //   console.log(index, row);
    // },
    // 
    handleView(index, row) {
      this.$router.push({
        path: "/clue/message",
        query: { id: row.id, userName: row.userName }
      });
    }
  },
  activated() {
    this.tableApi();
  },
  mounted() {
    API.getAllSupport().then(res => {
      this.getAllSupport = res.data;
      this.getAllSupport.unshift({
        val: "",
        code: 3
      });
    });
    // local  
    let showCol = JSON.parse(localStorage.getItem("clueTable"));
    if (showCol) this.showCol = showCol;
    // 
    API.getAllstate().then(res => {
      this.getAllstate = res.data;
      this.getAllstate.unshift({
        val: "",
        code: 4
      });
    });
    // 
    API.getAlltime().then(res => {
      this.getAlltime = res.data.slice(0, -3);
    });
    // 
    API.getRegion().then(res => {
      res.data.forEach(item => {
        item.value = item.label;
        if (item.children.length == "0") {
          item.children = "";
        } else {
          item.children.forEach(item1 => {
            item1.value = item1.label;
          });
        }
      });
      this.getRegion = res.data;
    });
    //
    API.getDepartments().then(res => {
      this.getDepartments = res.data;
    });
    //
    API.getCustomerLevel().then(res => {
      this.getCustomerLevel = res.data;
    });
    //
    API.getCustomerSource().then(res => {
      this.getCustomerSource = res.data;
    });
    // if (this.$store.state.userInfo) return;
    // console.log("");

    // this.getUserInfo();
  }
};
</script>

