<template>
  <div>
    <el-dialog
      :title="''+departmentDetail.name"
      :visible.sync="dialogFormVisible"
      @close="$emit('closeDialog')"
    >
      <div class="container">
        <el-form :model="form" class="role-serch" label-width="auto">
          <el-form-item label="">
            <el-select
              clearable
              v-model="form.roleName"
              placeholder=""
              @change="findSysMemberAndDepMember()"
            >
              <el-option
                v-for="(item,index) in roleList"
                :key="index"
                :label="item.name"
                :value="item.name"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select
              clearable
              v-model="form.positionName"
              placeholder=""
              @change="findSysMemberAndDepMember()"
            >
              <el-option
                v-for="(item, index) in positionList"
                :key="index"
                :label="item.name"
                :value="item.name"
              ></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div class="formlist">
          <div>
            <el-transfer
              style="text-align: center;"
              v-model="departList"
              filterable
              @change="listChange"
              :titles="['', departmentDetail.name+'']"
              :button-texts="['', '']"
              :format="{
                            noChecked: '${total}',
                            hasChecked: '${checked}/${total}'
                        }"
              :data="allMemberList"
            >
              <span slot-scope="{ option }">{{ option.label }}</span>
            </el-transfer>
          </div>
        </div>
        <!-- <div style="text-align:right;margin-top:30px;">
        <el-button type="success" @click="sureDialog"></el-button>
        </div>-->
      </div>
    </el-dialog>
  </div>
</template>

<script>
import API from "@/api/admin";

export default {
  name: "AddMember",
  props: ["isAddMember", "roleList", "departmentDetail"],
  data() {
    return {
      dialogFormVisible: false,
      form: {
        roleName: "",
        position: ""
      },
      value4: [],
      data: [],
      allMemberList: [], // 
      departList: [], // 
      positionList: [], // 
      removeMember: [], // 
      addMember: [] // 
    };
  },
  methods: {
    // 
    sureDialog() {
      let addArr = new Set(this.addMember);
      let removeArr = new Set(this.removeMember);
      console.log(...addArr);
      console.log(...removeArr);
    },
    // 
    listChange(handleList, handle, ids) {
      /*   **
      let arr = [];
      // 
      if (handle === "right") {
        let allMemberList = this.allMemberList;
        for (let i = 0; i < ids.length; i++) {
          for (let j = 0; j < allMemberList.length; j++) {
            if (
              ids[i] === allMemberList[j].key &&
              allMemberList[j].departName
            ) {
              let userName = allMemberList[j].label;
              let departName = allMemberList[j].departName;
              arr.push({ userName, departName });
            }
          }
        }
      }
      // 
      if (arr.length) {
        let strArr = [];
        for (const item of arr) {
          strArr.push(
            ": " +
              item.userName +
              " : " +
              item.departName +
              " "
          );
        }
        let str = strArr.join("<br/> ") + "<br/> ";
        this.$alert(str, "", {
          confirmButtonText: "",
          type: "warning",
          dangerouslyUseHTMLString: true
        });
      } else {
      }
        */
      handle = handle === "left" ? "deleteMember" : "addMember";
      let params = {
        ids: ids.join(","),
        id: this.departmentDetail.id
      };
      API[handle](params)
        .then(res => {
          console.log(res);
          this.findSysMemberAndDepMember();
          this.$message({
            type: "success",
            message: ""
          });
        })
        .catch(err => {
          if (err.indexOf("") === -1) this.$message.warning(err);

          console.error(err);
        });
    },
    // 
    findSysMemberAndDepMember() {
      let params = {
        roleName: this.form.roleName, // 
        postName: this.form.positionName, // 
        id: this.departmentDetail.id // id
      };
      API.findSysMemberAndDepMember(params)
        .then(res => {
          console.log(res);
          let allData = res.data[0];
          let departData = res.data[1];
          let allArr = [];
          let departArr = [];

          for (const item of allData) {
            allArr.push({
              key: item.id,
              label:
                item.name + (item.departName ? "--" + item.departName : ""),
              departName: item.departName
            });
          }
          for (const item of departData) {
            departArr.push(item.id);
          }
          this.allMemberList = allArr;
          this.departList = departArr;
        })
        .catch(err => {
          console.error(err);
        });
    }
  },
  watch: {
    isAddMember(value) {
      this.dialogFormVisible = value;
      if (!value) return;
      // 
      this.findSysMemberAndDepMember();
      // 
      if (this.positionList.length) return;
      API.findPositionsInfo()
        .then(res => {
          this.positionList = res.data;
        })
        .catch(err => {
          console.error(err);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
