<template>
  <div>
    <el-dialog
      title=""
      :close-on-click-modal="false"
      :visible.sync="outerVisible"
      @close="closeDialog"
    >
      <el-form :model="form" :rules="rules" ref="form" label-width="120px">
        <el-form-item label="" prop="companyName">
          <el-input v-model="form.companyName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="grade">
          <el-select v-model="form.grade" placeholder="" clearable class="item-css">
            <el-option
              v-for="(item, index) in customerLevel"
              :key="index"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="telephone">
          <el-input v-model="form.telephone" maxlength="12" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="form.type" placeholder="" clearable class="item-css">
            <el-option
              v-for="(item, index) in customerTypeList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="from">
          <el-select v-model="form.from" placeholder="" clearable class="item-css">
            <el-option
              v-for="(item, index) in channelSourceList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="" prop="customerFrom">
          <el-select v-model="form.customerFrom" placeholder="" clearable class="item-css">
            <el-option
              v-for="(item, index) in clientSourceList"
              :key="index"
              :value="item.code"
              :label="item.val"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="" prop="registerName">
              <el-input v-model="form.registerName" placeholder=""></el-input>
        </el-form-item>-->
        <el-form-item label="" prop="regionArr">
          <el-cascader
            :options="region"
            v-model="form.regionArr"
            placeholder=""
            clearable
            class="item-css"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="followDepart">
          <el-select
            v-model="form.followDepart"
            placeholder=""
            clearable
            class="item-css"
            @change="departChange()"
          >
            <el-option
              v-for="(item, index) in departmentsList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="followUser">
          <el-select
            v-model="form.followUser"
            @change="userChange"
            filterable
            placeholder=""
          >
            <el-option
              v-for="(item,index) in headList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="saleKeywords">
          <el-input v-model="form.saleKeywords" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="searchKeywords">
          <el-input v-model="form.searchKeywords" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="status">
          <el-select v-model="form.status" placeholder="" clearable class="item-css">
            <el-option
              v-for="(item, index) in customerType"
              :key="index"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="form.email" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="staffSize">
          <el-input v-model="form.staffSize" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="nextFollwTime">
          <el-date-picker
            v-model="form.nextFollwTime"
            type="datetime"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder=""
            :picker-options="startDatePicker"
            class="item-css"
            default-time="00:00:00"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="remarks">
          <el-input
            v-model="form.remarks"
            style="width:60%"
            type="textarea"
            :rows="4"
            show-word-limit
            maxlength="100"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="isWechat">
          <el-select v-model="form.isWechat" placeholder="" clearable class="item-css">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="isFace">
          <el-select v-model="form.isFace" placeholder="" clearable class="item-css">
            <el-option label="" value="1"></el-option>
            <el-option label="" value="0"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="wechat">
          <el-input v-model="form.wechat" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="QQ" prop="qq">
          <el-input v-model="form.qq" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="linkUserName">
          <el-input v-model="form.linkUserName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="linkUserPhone">
          <el-input v-model="form.linkUserPhone " maxlength="11" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button @click="sureDialog" type="success"></el-button>
          <el-button @click="closeDialog"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
// 
import API from "@/api/customer";
export default {
  name: "newcustomer",
  props: ["isShowCreate"],
  data() {
    var validateTel = (rule, value, callback) => {
      var reg = /0\d{2}-\d{7,8}$/;
      var reg2 = /^\d{11}$/;
      if (!value || reg.test(value) || reg2.test(value)) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    var validateMobile = (rule, value, callback) => {
      if (/^\d{11}$/.test(value)) {
        callback();
      } else {
        callback(new Error(""));
      }
    };
    return {
      startDatePicker: {
        // 
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7; //-8.64e6
        }
      },
      rules: {
        companyName: [
          { required: true, message: "", trigger: "blur" }
        ],
        grade: [
          { required: true, message: "", trigger: "change" }
        ],
        telephone: [
          { required: false, validator: validateTel, trigger: "blur" }
        ],
        type: [
          { required: true, message: "", trigger: "change" }
        ],
        from: [
          { required: true, message: "", trigger: "change" }
        ],
        customerFrom: [
          { required: true, message: "", trigger: "change" }
        ],
        registerName: [
          { required: true, message: "", trigger: "blur" }
        ],
        regionArr: [
          {
            type: "array",
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        followDepart: [
          { required: true, message: "", trigger: "change" }
        ],
        followUser: [
          { required: true, message: "", trigger: "change" }
        ],
        status: [
          { required: true, message: "", trigger: "change" }
        ],
        linkUserName: [
          { required: true, message: "", trigger: "blur" }
        ],
        linkUserPhone: [
          { required: true, validator: validateMobile, trigger: "blur" }
        ]
      },
      outerVisible: this.isShowCreate,
      customerLevel: [], // 
      customerTypeList: [], // 
      channelSourceList: [], // 
      region: [], // 
      departmentsList: [], // 
      clientSourceList: [], // 
      headList: [], // 
      customerType: [], // 
      form: {
        companyName: "", // 
        grade: null, // 
        telephone: "", // 
        type: null, // 
        from: null, // 
        customerFrom: null, // 
        registerName: "", // 
        regionArr: [],
        followDepart: "", // 
        followUser: "", // 
        searchKeywords: "", // 
        status: "", // 
        email: "", // 
        staffSize: "", // 
        nextFollwTime: "", // 
        remarks: "", // 
        isWechat: "", // 
        isFace: "", // 
        wechat: "", // 
        qq: "", // qq
        linkUserName: "", // 
        linkUserPhone: "" // 
      }
    };
  },
  methods: {
    // 
    clearFollowUser() {
      if (this.form.followDepart) {
        this.departChange();
      }
    },
    // id
    departChange(flag) {
      if (flag !== "no_change") this.form.followUser = null;
      let val = this.form.followDepart;
      if (val) {
        API.getHead(val).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      }
    },

    // id
    userChange(val) {
      let departId = this.headList.find(item => {
        return item.id === val;
      }).departId;
      this.form.followDepart = departId;
      this.departChange("no_change");
    },
    // 
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let followUser = this.form.followUser;
        let userInfo = this.$store.state.userInfo;
        let params = {
          companyName: this.form.companyName,
          grade: this.form.grade,
          telephone: this.form.telephone,
          type: this.form.type,
          from: this.form.from,
          customerFrom: this.form.customerFrom,
          registerName: this.form.registerName,
          province: this.form.regionArr[0],
          city: this.form.regionArr[1],
          followDepart: this.form.followDepart,
          followUser,
          saleKeywords: this.form.saleKeywords,
          searchKeywords: this.form.searchKeywords,
          status: this.form.status,
          email: this.form.email,
          staffSize: this.form.staffSize,
          nextFollwTime: this.form.nextFollwTime,
          remarks: this.form.remarks,
          isWechat: this.form.isWechat,
          isFace: this.form.isFace,
          wechat: this.form.wechat,
          qq: this.form.qq,
          linkUserName: this.form.linkUserName,
          linkUserPhone: this.form.linkUserPhone,
          createId: userInfo.id
        };
        API.postAdd(params).then(res => {
          console.log(res);
          this.$message({
            message: "",
            type: "success"
          });
          this.$refs.form.resetFields();
          this.$emit("sureDialogFather");
        });
      });
    },
    // 
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather", this.outerVisible);
    }
  },
  watch: {
    isShowCreate(val) {
      this.outerVisible = this.isShowCreate;
      if (!val) return;
      // 
      API.customerFrom().then(res => {
        this.clientSourceList = res.data;
      });
      // api
      API.getCustomerLevel().then(res => {
        this.customerLevel = res.data;
      });
      // api
      API.getType().then(res => {
        this.customerTypeList = res.data;
      });
      // api
      API.getCustomerSource().then(res => {
        this.channelSourceList = res.data;
      });
      // api
      API.getRegion().then(res => {
        res.data.forEach(item => {
          item.value = item.id;
          if (item.children.length == "0") {
            item.children = "";
          } else {
            item.children.forEach(item1 => {
              item1.value = item1.id;
            });
          }
        });
        this.region = res.data;
      });
      // api
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
      // 
      API.getAllUser().then(res => {
        res.data.forEach(item => {
          item.value = item.name;
        });
        this.headList = res.data;
      });
      // api
      API.getCustomerType().then(res => {
        this.customerType = res.data;
      });
    }
  }
};
</script>
<style scoped lang='scss'>
/deep/.el-dialog {
  width: 40%;
  .el-input:not(.el-input--suffix) {
    width: 50%;
  }
}
</style>