<template>
  <!--  -->
  <div>
    <el-tabs type="card" @tab-click="tabChange" v-model="tabName">
      <el-tab-pane label="" name="plan">
        <el-row>
          <el-form :model="chooseObj.plan" label-width="120px">
            <el-col :span="24" class="list">
              <el-form-item label="">
                <ul>
                  <li
                    v-for="(item, index) in timeList"
                    :key="index"
                    @click="dateChange(index, item.code)"
                    :class="chooseObj.plan.timeActive == index ? 'addclass' : '' "
                  >{{item.name}}</li>
                  <li
                    @click="chooseObj.plan.showDefinedTime=true;chooseObj.plan.timeActive = -1"
                    class="list-li"
                    v-if="!chooseObj.plan.showDefinedTime"
                  ></li>
                  <li v-else>
                    <el-date-picker
                      v-model="chooseObj.plan.createTime"
                      type="daterange"
                      value-format="yyyy-MM-dd"
                      range-separator=""
                      start-placeholder=""
                      end-placeholder=""
                      @change="tableApi"
                    ></el-date-picker>
                  </li>
                </ul>
              </el-form-item>
            </el-col>
            <el-col :span="24">
              <el-col :span="8">
                <el-form-item label="">
                  <el-select
                    v-model="chooseObj.plan.departId"
                    placeholder=""
                    clearable
                    class="item-css"
                    @change="departChange"
                  >
                    <el-option
                      v-for="(item, i) in departList"
                      :key="i"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="">
                  <el-select
                    v-model="chooseObj.plan.userId"
                    placeholder=""
                    clearable
                    class="item-css"
                    @change="tableApi"
                    :no-data-text="chooseObj.plan.departId?'':''"
                  >
                    <el-option
                      v-for="(item, index) in followUserList"
                      :key="index"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-col>
          </el-form>
        </el-row>

        <el-row>
          <!--  -->
          <el-popover placement="top-start" width="300" trigger="click">
            <el-checkbox-group v-model="showPlanCol" @change="planColChange">
              <el-checkbox
                style="width:120px;line-height:25px"
                v-for="(item,index) in planColOptions"
                :key="index"
                :label="item"
              ></el-checkbox>
            </el-checkbox-group>
            <el-button
              slot="reference"
              style="margin-right:10px;"
              type="primary"
              icon="el-icon-s-operation"
              plain
            ></el-button>
          </el-popover>
          <el-button
            type="primary"
            icon="el-icon-folder-add"
            @click="isNewReceivableplan=true"
            v-permi="'/payment/addContractPaymentPlan'"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            :disabled="!statisticsObj.plan.checkboxId.length"
            @click="deletePlan"
          ></el-button>
        </el-row>
        <el-row style="height:60px;line-height:60px;font-size:14px">
          <div>{{statisticsObj.plan.planAmount}}{{statisticsObj.plan.realPayAmount}}</div>
        </el-row>
        <el-table
          :data="planTableData"
          tooltip-effect="dark"
          style="width: 100%;"
          border
          stripe
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" align="center" fixed></el-table-column>
          <el-table-column
            prop="contractName"
            v-if="showPlanCol.indexOf('')>-1"
            label=""
            align="center"
          ></el-table-column>
          <el-table-column
            prop="customerName"
            v-if="showPlanCol.indexOf('')>-1"
            label=""
            align="center"
          ></el-table-column>
          <el-table-column
            prop="price"
            v-if="showPlanCol.indexOf('')>-1"
            label=""
            align="center"
          ></el-table-column>

          <el-table-column
            prop="realPay"
            v-if="showPlanCol.indexOf('')>-1"
            label=""
            align="center"
          ></el-table-column>

          <el-table-column
            prop="opeDate"
            v-if="showPlanCol.indexOf('')>-1"
            label=""
            align="center"
          >
            <template slot-scope="scope">{{scope.row.opeDate&&scope.row.opeDate.slice(0,11)}}</template>
          </el-table-column>
          <el-table-column
            prop="realPayDate"
            v-if="showPlanCol.indexOf('')>-1"
            label=""
            align="center"
          >
            <template
              slot-scope="scope"
            >{{scope.row.realPayDate&&scope.row.realPayDate.slice(0,11)}}</template>
          </el-table-column>
          <el-table-column
            prop="overDays"
            v-if="showPlanCol.indexOf('')>-1"
            label=""
            align="center"
          ></el-table-column>
          <el-table-column
            prop="userName"
            v-if="showPlanCol.indexOf('')>-1"
            label=""
            align="center"
          ></el-table-column>
          <el-table-column
            prop="departName"
            v-if="showPlanCol.indexOf('')>-1"
            label=""
            align="center"
          ></el-table-column>
          <el-table-column label="" align="center" fixed="right">
            <template slot-scope="scope">
              <!-- <el-button
                size="mini"
                :disabled="Boolean(scope.row.isOver)"
                plain
                type="primary"
                @click="handleEditplan(scope.$index, scope.row)"
              ></el-button>-->
              <el-button size="mini" @click="handleViewplan(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination
          class="itemPage"
          @size-change="sizeChangePage"
          @current-change="currentChangePage"
          :current-page="pageObj.plan.pageNum"
          :page-sizes="[10, 20, 30, 40]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="pageObj.plan.total"
        ></el-pagination>
      </el-tab-pane>
      <el-tab-pane label="" name="record">
        <el-row>
          <el-form :model="chooseObj.record" label-width="120px">
            <el-col :span="24" class="list">
              <el-form-item label="">
                <ul>
                  <li
                    v-for="(item, index) in paymentTypeList"
                    :key="index"
                    @click="paymentTypeChange(index, item.code)"
                    :class="chooseObj.record.paymentActive == index ? 'addclass' : '' "
                  >{{item.val}}</li>
                </ul>
              </el-form-item>
            </el-col>
            <el-col :span="24" class="list">
              <el-form-item label="">
                <ul>
                  <li
                    v-for="(item, index) in contimeList"
                    :key="index"
                    @click="dateChange(index, item.code)"
                    :class="chooseObj.record.timeActive == index ? 'addclass' : '' "
                  >{{item.name}}</li>
                  <li
                    @click="chooseObj.record.showDefinedTime=true;chooseObj.record.timeActive = -1"
                    class="list-li"
                    v-if="!chooseObj.record.showDefinedTime"
                  ></li>
                  <li v-else>
                    <el-date-picker
                      v-model="chooseObj.record.createTime"
                      type="daterange"
                      value-format="yyyy-MM-dd"
                      range-separator=""
                      start-placeholder=""
                      end-placeholder=""
                      @change="tableApi"
                    ></el-date-picker>
                  </li>
                </ul>
              </el-form-item>
            </el-col>
            <el-col :span="24">
              <el-col :span="8">
                <el-form-item label="">
                  <el-select
                    v-model="chooseObj.record.departId"
                    placeholder=""
                    clearable
                    class="item-css"
                    @change="departChange"
                  >
                    <el-option
                      v-for="(item, i) in departList"
                      :key="i"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="8">
                <el-form-item label="">
                  <el-select
                    v-model="chooseObj.record.userId"
                    placeholder=""
                    clearable
                    class="item-css"
                    :no-data-text="chooseObj.record.departId?'':''"
                    @change="tableApi"
                  >
                    <el-option
                      v-for="(item, index) in followUserList"
                      :key="index"
                      :label="item.name"
                      :value="item.id"
                    ></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
            </el-col>
          </el-form>
        </el-row>

        <el-row>
          <!--  -->
          <el-popover placement="top-start" width="300" trigger="click">
            <el-checkbox-group v-model="showRecordCol" @change="planRecordChange">
              <el-checkbox
                style="width:120px;line-height:25px"
                v-for="(item,index) in recordColOptions"
                :key="index"
                :label="item"
              ></el-checkbox>
            </el-checkbox-group>
            <el-button
              slot="reference"
              style="margin-right:10px;"
              type="primary"
              icon="el-icon-s-operation"
              plain
            ></el-button>
          </el-popover>
          <el-button
            type="primary"
            icon="el-icon-folder-add"
            @click="isNewReceivablerecord=true"
            v-permi="'/payment/addRealContractPayment'"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            :disabled="!statisticsObj.record.checkboxId.length"
            @click="deletePlan"
          ></el-button>
        </el-row>
        <el-row style="height:60px;line-height:60px;font-size:14px">
          <div>{{TotalPrice}}</div>
        </el-row>
        <el-table
          :data="recordTableData"
          tooltip-effect="dark"
          style="width: 100%;"
          border
          :fit="true"
          stripe
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" align="center" fixed></el-table-column>
          <el-table-column
            prop="opeDate"
            v-if="showRecordCol.indexOf('')>-1"
            label=""
            align="center"
          ></el-table-column>
          <el-table-column
            prop="price"
            v-if="showRecordCol.indexOf('')>-1"
            label=""
            align="center"
          ></el-table-column>
          <el-table-column
            prop="companyName"
            v-if="showRecordCol.indexOf('')>-1"
            label=""
            align="center"
          ></el-table-column>
          <el-table-column
            prop="contractName"
            v-if="showRecordCol.indexOf('')>-1"
            label=""
            align="center"
          ></el-table-column>
          <el-table-column
            prop="payNo"
            v-if="showRecordCol.indexOf('')>-1"
            label=""
            align="center"
          ></el-table-column>
          <el-table-column
            prop="payType"
            v-if="showRecordCol.indexOf('')>-1"
            label=""
            align="center"
          ></el-table-column>
          <el-table-column
            prop="userName"
            v-if="showRecordCol.indexOf('')>-1"
            label=""
            align="center"
          ></el-table-column>
          <el-table-column
            prop="returnStatus"
            v-if="showRecordCol.indexOf('')>-1"
            label=""
            align="center"
          ></el-table-column>
          <el-table-column
            prop="days"
            v-if="showRecordCol.indexOf('')>-1"
            label=""
            align="center"
          ></el-table-column>
          <el-table-column label="" align="center" fixed="right">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                plain
                v-permi="'/payment/updateContractPaymentPlan'"
                @click="handleEditrecord(scope.$index, scope.row)"
              ></el-button>
              <el-button size="mini" @click="handleViewrecord(scope.$index, scope.row)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination
          class="itemPage"
          @size-change="sizeChangePage"
          @current-change="currentChangePage"
          :current-page="pageObj.record.pageNum"
          :page-sizes="[10, 20, 30, 40]"
          layout="total, sizes, prev, pager, next, jumper"
          :total="pageObj.record.total"
        ></el-pagination>
      </el-tab-pane>
    </el-tabs>

    <NewReceivableplan
      :isNewReceivableplan="isNewReceivableplan"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewReceivableplan>

    <edit-receivableplan
      :isEditReceivableplan="isEditReceivableplan"
      :editPlanData="editPlanData"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-receivableplan>

    <NewReceivablerecord
      :isNewReceivablerecord="isNewReceivablerecord"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewReceivablerecord>

    <edit-receivablerecord
      :isEditReceivablerecord="isEditReceivablerecord"
      :editRecordData="editRecordData"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-receivablerecord>
  </div>
</template>

<script>
// 
import API from "@/api/payment";
import EditReceivableplan from "../components/EditReceivableplan"; // 
import NewReceivableplan from "../components/NewReceivableplan"; // 
import NewReceivablerecord from "../components/NewReceivablerecord"; // 
import EditReceivablerecord from "../components/EditReceivablerecord"; // 
export default {
  name: "receivable",
  components: {
    NewReceivableplan,
    EditReceivableplan,
    NewReceivablerecord,
    EditReceivablerecord
  },
  data() {
    return {
      // 
      chooseObj: {
        // 
        plan: {
          userId:"",
          showDefinedTime: false, // 
          timeCode: null, // 
          timeActive: 0, // 
          createTime: []
        },
        // 
        record: {
          showDefinedTime: false, // 
          timeActive: 0, // 
          createTime: [],
          paymentActive: 0,
          payType: null // 
        }
      },
      // 
      showPlanCol: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      // 
      planColOptions: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      // 
      showRecordCol: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      // 
      recordColOptions: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      // 
      pageObj: {
        plan: {
          pageNum: 1, //
          pageSize: 10, //
          total: 0 //
        },
        record: {
          pageNum: 1, //
          pageSize: 10, //
          total: 0 //
        }
      },
      planTableData: [], // 
      recordTableData: [], // 
      showDefinedTime: false,
      tabName: "plan", // 
      timeList: [], // 
      paymentTypeList: [], // 
      contimeList: [], // 
      departList: [], // 
      followUserList: [], // 
      statisticsObj: {
        plan: {
          planAmount: "", // 
          realPayAmount: "", // 
          checkboxId: [] // 
        },
        record: {
          planAmount: "", // 
          realPayAmount: "", // 
          checkboxId: [] // 
        }
      },
      TotalPrice: "", // 
      isNewReceivableplan: false,
      isEditReceivableplan: false,
      isNewReceivablerecord: false,
      isEditReceivablerecord: false,
      editPlanData: {}, // 
      editRecordData: {} // 
    };
  },
  methods: {
    planColChange() {
      localStorage.setItem("planTable", JSON.stringify(this.showPlanCol));
    },
    planRecordChange() {
      localStorage.setItem("recordTable", JSON.stringify(this.showRecordCol));
    },
    // tableapi
    tableApi() {
      // 
      let obj = this.chooseObj[this.tabName];
      // 
      let page = this.pageObj[this.tabName];
      let { pageNum, pageSize } = page;
      let startTime = obj.createTime && obj.createTime[0];
      let endTime = obj.createTime && obj.createTime[1];
      if (this.tabName === "plan") {
        // 
        let params = {
          pageNum,
          pageSize,
          startTime,
          endTime,
          timeCode: obj.timeCode,
          departId: obj.departId,
          userId: obj.userId
        };
        API.findPlanPaymentList(params).then(res => {
          let data = res.data;
          this.planTableData = data.paymentList.list;
          page.total = data.paymentList.total;
          this.statisticsObj.plan.planAmount = data.planAmount;
          this.statisticsObj.plan.realPayAmount = data.realPayAmount;
        });
      } else {
        // 
        let params = {
          pageNum,
          pageSize,
          startTime,
          endTime,
          payType: obj.payType,
          timeCode: obj.timeCode,
          departId: obj.departId,
          userId: obj.userId
        };
        this.getTotalPrice();
        API.postListcon(params).then(res => {
          let data = res.data;
          this.recordTableData = data.list;
          page.total = data.total;
        });
      }
    },
    //
    sizeChangePage(val) {
      this.pageObj[this.tabName].pageSize = val;
      this.tableApi(this.rows);
    },
    //
    currentChangePage(val) {
      this.pageObj[this.tabName].pageNum = val;
      this.tableApi(this.page);
    },
    // 
    dateChange(index, code) {
      let obj = this.chooseObj[this.tabName];
      obj.showDefinedTime = false;
      obj.timeActive = index;
      obj.timeCode = code;
      obj.createTime = [];
      this.tableApi();
    },
    // 
    departChange(val) {
      this.chooseObj[this.tabName].userId = "";
      let obj = this.chooseObj[this.tabName];
      // this.tableApi(this.departId);
      if (val) {
        API.getHead(obj.departId).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.followUserList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.followUserList = res.data;
        });
      }
      this.tableApi();
    },
    // querySearch(queryString, cb) {
    //   var restaurants = this.followUserList;
    //   var results = queryString
    //     ? restaurants.filter(this.createFilter(queryString))
    //     : restaurants;
    //   cb(results);
    // },
    // createFilter(queryString) {
    //   return restaurant => {
    //     return (
    //       restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
    //       0
    //     );
    //   };
    // },
    // 
    handleSelect(item) {
      this.userId = item.id;
      this.tableApi(this.userId);
      if (this.userId !== null) {
        API.getDepartByUserId(item.id).then(res => {
          this.departList = res.data;
        });
      }
      this.userId = null;
    },

    // table
    handleSelectionChange(val) {
      let arr = [];
      val.map(ele => {
        arr.push(ele.id);
      });
      this.statisticsObj[this.tabName].checkboxId = arr;
    },
    // 
    deletePlan() {
      let name, delApi;
      name = this.tabName === "plan" ? "" : "";
      this.$confirm("" + name, "" + name, {
        distinguishCancelAndClose: true,
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(() => {
          let Params = {
            ids: this.statisticsObj[this.tabName].checkboxId.join(",")
          };
          API.postdeletePaymentPlans(Params).then(res => {
            this.$message({
              message: "",
              type: "success"
            });
            this.tableApi();
          });
        })
        .catch(() => {
          console.error(err);
        });
    },
    // 
    handleEditplan(index, row) {
      this.editPlanData = row;
      this.isEditReceivableplan = true;
    },
    // 
    handleViewplan(index, row) {
      this.$router.push({
        path: "receivablePlandetails",
        query: { id: row.id }
      });
    },
    // tab
    tabChange(val) {
      if (val.label == "") {
        this.tabName = "plan";
      } else if (val.label == "") {
        this.tabName = "record";
      }
      this.tableApi();
    },

    // 
    paymentTypeChange(index, code) {
      let obj = this.chooseObj.record;
      obj.paymentActive = index;
      obj.payType = code;
      this.tableApi();
    },

    handleBlurcon() {
      if (this.form.userIdcon == "") {
        API.getDepartments().then(res => {
          this.departList = res.data;
        });
      }
    },

    // 
    handleEditrecord(index, row) {
      console.log(row);
      this.editRecordData = row;
      this.isEditReceivablerecord = true;
    },
    // 
    handleViewrecord(index, row) {
      this.$router.push({
        path: "receivableRecorddetails",
        query: { id: row.id }
      });
    },
    // 
    getTotalPrice() {
      API.getfindPaymentTotalPrice().then(res => {
        this.TotalPrice = res.data;
      });
    },
    // Dialog
    getSonSure() {
      this.getSonCancel();
      this.tableApi();
    },
    // Dialog 
    getSonCancel() {
      this.isNewReceivableplan = false;
      this.isNewReceivablerecord = false;
      this.isEditReceivablerecord = false;
      this.isEditReceivableplan = false;
    }
  },
  mounted() {
    let showPlanCol = JSON.parse(localStorage.getItem("planTable"));
    if (showPlanCol) this.showPlanCol = showPlanCol;
    let showRecordCol = JSON.parse(localStorage.getItem("recordTable"));
    if (showRecordCol) this.showRecordCol = showRecordCol;
    // 
    API.getCreationTime().then(res => {
      // let obj = { code: 0, name: "" };
      // res.data.unshift(obj);
      this.timeList = res.data;

      this.contimeList = res.data.slice(0, -3);
    });
    // 
    API.getDepartments().then(res => {
      this.departList = res.data;
    });
    // 
    // API.getAllUser().then(res => {
    //   res.data.forEach(item => {
    //     item.value = item.name;
    //   });
    //   this.followUserList = res.data;
    // });
    // 
    API.getPaymentType().then(res => {
      // console.log(res.data)
      let obj = { code: 0, val: "" };
      res.data.unshift(obj);
      this.paymentTypeList = res.data;
    });
  },
  activated() {
    // 
    this.tableApi();
  }
};
</script>
<style lang='scss' scoped>
ul {
  list-style-type: none;
  padding: 0;
}
.list {
  li {
    display: inline-block;
    padding: 0 20px;
    cursor: pointer;
  }
}
.addclass {
  background: rgb(255, 242, 233);
  color: rgb(255, 178, 121);
}
.item-css {
  width: 100%;
}
.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}
</style>