<template>
  <div>
    <!--  -->
    <el-form :model="form" label-width="100px">
      <el-row :span="24" class="list">
        <el-form-item label="">
          <ul>
            <li
              v-for="(item, index) in customerType"
              :key="index"
              @click="changeClientType(index, item.code)"
              :class="typeActive === index ? 'addclass' : '' "
            >{{item.val}}</li>
          </ul>
        </el-form-item>
      </el-row>
      <el-row :span="24" class="list">
        <el-form-item label="">
          <ul>
            <li
              v-for="(item, index) in customerLevel"
              :key="index"
              @click="changeClientGrade(index, item.code)"
              :class="gradeActive == index ? 'addclass' : '' "
            >{{item.val}}</li>
          </ul>
        </el-form-item>
      </el-row>
      <el-row :span="24" class="list">
        <el-form-item label="">
          <ul>
            <li
              v-for="(item, index) in customerTypeList"
              :key="index"
              @click="changeService(index, item.id)"
              :class="serviceActive == index ? 'addclass' : '' "
            >{{item.name}}</li>
          </ul>
        </el-form-item>
      </el-row>
      <div class="slide"  :style="{'height':showMore?'225px':0}">
        <el-row :span="24" class="list">
          <el-form-item label="">
            <ul>
              <li
                v-for="(item, index) in creationTime"
                :key="index"
                @click="changeCreateTime(index, item.code)"
                :class="timeActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
              <li
                @click="showDefinedDate = true;timeActive = -1"
                class="list-li"
                v-if="!showDefinedDate"
              ></li>
              <li v-else>
                <el-date-picker
                  @change="timeCustom"
                  v-model="definedDate"
                  type="daterange"
                  value-format="yyyy-MM-dd"
                  range-separator=""
                  start-placeholder=""
                  end-placeholder=""
                ></el-date-picker>
              </li>
            </ul>
          </el-form-item>
        </el-row>
        <el-row :span="24">
          <el-col :span="8">
            <el-form-item label="">
              <el-cascader
                :options="region"
                @change="regionChange"
                placeholder=""
                clearable
                class="item-css"
              ></el-cascader>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-select
                v-model="form.source"
                placeholder=""
                class="item-css"
                clearable
                @change="tableApi()"
              >
                <el-option
                  v-for="(item, index) in customerSourceList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :span="24">
          <el-col :span="8">
            <el-form-item label="">
              <el-select
                v-model="form.department"
                placeholder=""
                clearable
                class="item-css"
                @change="departChange($event)"
              >
                <el-option
                  v-for="(item, index) in departmentsList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="8">
            <el-form-item label="">
              <el-select
                v-model="form.followUser"
                placeholder=""
                clearable
                class="item-css"
                :no-data-text="form.department?'':''"
                @change="tableApi()"
              >
                <el-option
                  v-for="(item, index) in headList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :span="24">
          <el-col :span="8">
            <el-form-item label="">
              <el-select v-model="form.searchType" placeholder="" clearable class="item-css">
                <el-option
                  v-for="(item, index) in getsearchType"
                  :key="index"
                  :label="item.name"
                  :value="item.code"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item label="" class="input-search">
              <el-col :span="14">
                <el-input
                  v-model="form.searchName"
                  @keydown.enter.native="tableApi"
                  placeholder=""
                  class="input-with-select"
                  clearable
                ></el-input>
              </el-col>
              <el-col :span="6">
                <el-button type="primary" @click="tableApi()"></el-button>
              </el-col>
            </el-form-item>
          </el-col>
        </el-row>
      </div>
      <p class="show-more-button">
        <el-button
          @click="showMore=!showMore"
          plain
          :icon="showMore?'el-icon-caret-top':'el-icon-caret-bottom'"
          type="primary"
          circle
          size="mini"
        ></el-button>
      </p>
    </el-form>
    <!--  -->
    <!-- <el-row>
      <el-col :span="24" style="font-size:20px;text-align:center;">
        <i
          style="cursor: pointer;"
          :class="flag == true? 'el-icon-arrow-up' : 'el-icon-arrow-down'"
          @click="show"
        ></i>
      </el-col>
    </el-row>-->
    <!--  -->
    <el-row style="margin-top:20px">
      <!--  -->
      <el-popover placement="top-start" width="300" trigger="click">
        <el-checkbox-group v-model="showCol" @change="colChange">
          <el-checkbox
            style="width:120px;line-height:25px"
            v-for="(item,index) in colOptions"
            :key="index"
            :label="item"
          ></el-checkbox>
        </el-checkbox-group>
        <el-button
          slot="reference"
          style="margin-right:10px;"
          type="primary"
          icon="el-icon-s-operation"
          plain
        ></el-button>
      </el-popover>
      <el-button
        type="primary"
        icon="el-icon-folder-add"
        @click="isShowCreate = true"
        v-permi="'/customer/add'"
      ></el-button>
      <el-button
        type="primary"
        icon="el-icon-money"
        v-permi="'/customer/batchUploadCustomer'"
        @click="isShowClues = true"
        class="item"
      ></el-button>
      <el-button
        type="warning"
        icon="el-icon-s-fold"
        v-permi="'/customer/returnCustomer'"
        @click="returnSalesBtn"
        :disabled="multipleSelection.length!==1"
        class="item"
      ></el-button>
      <el-button
        type="warning"
        icon="el-icon-document-copy"
        @click="isShowPotential=true"
        class="item"
        :disabled="!multipleSelection.length"
      ></el-button>
      <!-- <el-button
        type="info"
        icon="el-icon-ship"
        class="item"
        :disabled="!multipleSelection.length"
        @click="showHigh"
      ></el-button>-->
      <el-button
        type="success"
        icon="el-icon-message"
        @click="isShowSend=true"
        :disabled="!multipleSelection.length"
        class="item"
      ></el-button>
      <!-- <el-button type="success" icon="el-icon-message" @click="exportTable" class="item"></el-button> -->
      <el-dropdown trigger="click" @command="exportTable">
        <el-button type="warning" style="margin-left:10px;">
          
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item
            v-for="(item,index) in excNames"
            :key="index"
            :command="item"
          >{{item.name}}</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </el-row>
    <!--  -->
    <el-table
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      border
      :fit="true"
      @selection-change="handleSelectionChange"
      highlight-current-row
    >
      <el-table-column type="selection" width="55" align="center" fixed></el-table-column>
      <el-table-column
        prop="companyName"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="address"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column
        prop="service.name"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="customerGrade.name"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column prop="telephone" v-if="showCol.indexOf('')>-1" label="" align="center"></el-table-column>
      <el-table-column
        prop="customerStatus.name"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="createTime"
        width="170"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="follow[0].contents"
        v-if="showCol.indexOf('')>-1"
        label=""
        show-overflow-tooltip
        align="center"
      >
        <!-- <template slot-scope="scope">
          <p>
            {{scope.row.follow[0]&&scope.row.follow[0].contents.slice(0,20)+'...'}}
          </p>
        </template>-->
      </el-table-column>
      <el-table-column
        prop="depart.name"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="channel.name"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column label="" width="160" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row)"
            type="primary"
            v-permi="'/customer/updateCustomer'"
            plain
          ></el-button>
          <el-button size="mini" @click="handleView(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      class="itemPage"
      @size-change="sizeChangePage"
      @current-change="currentChangePage"
      :current-page="pageObj.pageNum"
      :page-sizes="[10, 20, 30, 40]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pageObj.total"
    ></el-pagination>

    <!--  -->
    <new-customer
      :isShowCreate="isShowCreate"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-customer>
    <!--  -->
    <upload-client
      :isShowClues="isShowClues"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></upload-client>

    <!--  -->
    <return-sales
      :isShowSales="isShowSales"
      :seletedCustomer="multipleSelection[0]"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></return-sales>

    <!--  -->
    <Potential
      :isShowPotential="isShowPotential"
      :PotentialId="selectIds"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></Potential>

    <!--  -->
    <NewSend
      :isShowSend="isShowSend"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewSend>

    <!--  -->
    <edit-customer
      :isShowEdit="isShowEdit"
      :customerId="customerId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-customer>
  </div>
</template>

<script>
// 
import API from "@/api/customer";
// import { watch } from 'fs'
import NewCustomer from "../components/NewCustomer"; // 
import UploadClient from "../components/UploadClient"; // 
import ReturnSales from "../components/ReturnSales"; // 
import Potential from "../components/Potential"; // 
import NewSend from "@/components/element/NewSend"; // 
import EditCustomer from "@/components/element/EditCustomer"; // 
import { constants } from "crypto";
export default {
  name: "custormer",
  components: {
    NewCustomer,
    UploadClient,
    ReturnSales,
    Potential,
    NewSend,
    EditCustomer
  },
  data() {
    return {
      showMore: false, // 
      customerType: [], // 
      customerLevel: [], // 
      creationTime: [], // 
      getsearchType: [
        { name: "", code: 1 },
        { name: "", code: 2 },
        { name: "", code: 3 },
        { name: "", code: 7 }
      ],
      showCol: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      colOptions: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      excNames: [
        { api: "exportCustomerExc", name: "" },
        { api: "exportSignCustomerExc", name: "" }
      ], // 
      typeActive: 0,
      gradeActive: 0,
      timeActive: 0,
      serviceActive: 0,
      region: [], // 
      departmentsList: [], // 
      headList: [], // 
      customerSourceList: [], // 
      customerTypeList: [], // 
      definedDate: null,
      isShowCreate: false, // 
      isShowClues: false, // 
      isShowSales: false, // 
      isShowPotential: false, // 
      isShowSend: false, // 
      isShowEdit: false, // 
      customerId: "", // id
      form: {
        department: "", // 
        followUser: "", // 
        source: "", // 
        searchName: "", // 
        searchType: "" //  
      },
      tableData: [], // 
      showDefinedDate: false, // 
      pageObj: {
        pageNum: 1, //
        pageSize: 10, //
        total: 0 //
      },
      codeType: null, // code
      codeLevel: null, // code
      codeTime: null, // code
      codeService: null, // code
      startTime: null, // 
      endTime: null, // 
      province: null, // 
      city: null, // 
      multipleSelection: [] // 
    };
  },
  methods: {
    colChange() {
      localStorage.setItem("customerTable", JSON.stringify(this.showCol));
    },
    // tableapi
    tableApi() {
      let params = {
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize,
        customerStatus: this.codeType,
        customerGrade: this.codeLevel,
        timeCode: this.codeTime,
        startTime: this.startTime,
        endTime: this.endTime,
        province: this.province,
        city: this.city,
        service: this.codeService,
        department: this.form.department,
        searchType: this.form.searchType,
        followUser: this.form.followUser,
        source: this.form.source,
        searchName: this.form.searchName
      };
      API.postList(params).then(res => {
        this.pageObj.total = res.data.total; //
        res.data.list.forEach(item => {
          if (item.cityStr == null) {
            item.address = item.provinceStr;
          } else {
            item.address = item.provinceStr + item.cityStr;
          }
        });
        this.tableData = res.data.list;
      });
    },
    // 
    sizeChangePage(val) {
      this.pageObj.pageSize = val;
      this.tableApi(this.rows);
    },
    // 
    currentChangePage(val) {
      this.pageObj.pageNum = val;
      this.tableApi();
    },
    // 
    changeClientType(index, code) {
      this.typeActive = index; //
      this.codeType = code;
      this.tableApi(this.codeType);
    },
    // 
    changeClientGrade(index, code) {
      this.gradeActive = index; //
      this.codeLevel = code;
      this.tableApi(this.codeLevel);
    },
    // 
    changeCreateTime(index, code) {
      this.startTime = this.endTime = "";
      this.definedDate = null;
      this.showDefinedDate = false;
      this.timeActive = index; //
      this.codeTime = code;
      this.tableApi();
    },
    // 
    timeCustom(val) {
      if (val != null) {
        this.startTime = val[0];
        this.endTime = val[1];
        this.timeCode = null;
        this.tableApi();
      }
    },
    // 
    regionChange(value) {
      this.province = value[0];
      this.city = value[1];
      this.tableApi(this.province, this.city);
    },
    changeService(index, code) {
      this.serviceActive = index;
      this.codeService = code;
      this.tableApi();
    },
    // 
    departChange(val) {
      this.form.followUser = null;
      this.tableApi();
      if (!val) {
        this.headList = [];
        return;
      }
      // ids
      API.getHead(val).then(res => {
        res.data.forEach(item => {
          item.value = item.name;
        });
        this.headList = res.data;
      });
    },
    querySearch(queryString, cb) {
      var restaurants = this.headList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      //  callback 
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    exportTable(obj) {
      let params = {
        pageNum: this.pageObj.pageNum,
        pageSize: this.pageObj.pageSize,
        customerStatus: this.codeType,
        customerGrade: this.codeLevel,
        timeCode: this.codeTime,
        startTime: this.startTime,
        endTime: this.endTime,
        province: this.province,
        city: this.city,
        service: this.codeService,
        department: this.form.department,
        searchType: this.form.searchType,
        followUser: this.form.followUser,
        source: this.form.source,
        searchName: this.form.searchName
      };
      API[obj.api](params)
        .then(res => {
          console.log(res);

          var blob = new Blob([res], {
            type: "application/vnd.ms-excel;charset=utf-8"
          }); //vnd.ms-excel
          var downloadElement = document.createElement("a");
          var href = window.URL.createObjectURL(blob); //
          downloadElement.href = href;
          document.body.appendChild(downloadElement);
          downloadElement.setAttribute("download", obj.name + ".xls");
          downloadElement.click(); //
          document.body.removeChild(downloadElement); //
          window.URL.revokeObjectURL(href); //blob
        })
        .catch(err => {
          console.error(err);
        });
    },
    // table
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 
    // showHigh() {
    //   this.$confirm(
    //     "",
    //     "",
    //     {
    //       confirmButtonText: "",
    //       cancelButtonText: "",
    //       type: "warning"
    //     }
    //   )
    //     .then(() => {
    //       let data = {
    //         ids: this.selectIds
    //       };
    //       API.posttoPublic(data)
    //         .then(res => {
    //           // console.log(res)
    //           this.$message({
    //             type: "success",
    //             message: "!"
    //           });
    //           this.tableApi();
    //         })
    //         .catch(err => {
    //           if (err.indexOf("") === -1) this.$message.warning(err);
    //         });
    //     })
    //     .catch(() => {
    //       this.$message({
    //         type: "info",
    //         message: ""
    //       });
    //     });
    // },
    // 
    returnSalesBtn() {
      let customer = this.multipleSelection[0];
      if (!customer.clueId) {
        this.$message({
          type: "error",
          message: ""
        });
      } else {
        this.isShowSales = true;
      }
    },
    // 
    handleEdit(index, row) {
      // console.log(index, row)
      this.customerId = row.id;
      this.isShowEdit = true;
    },
    // Dialog
    getSonSure() {
      this.getSonCancel();
      this.tableApi();
    },
    // Dialog
    getSonCancel() {
      this.isShowCreate = false;
      this.isShowClues = false;
      this.isShowSales = false;
      this.isShowPotential = false;
      this.isShowSend = false;
      this.isShowEdit = false;
    },
    // 
    handleView(index, row) {
      this.$router.push({ path: "customerdetails", query: { id: row.id } });
    }
  },
  mounted() {
    let showCol = JSON.parse(localStorage.getItem("customerTable"));
    if (showCol) this.showCol = showCol;
    // api
    API.getType().then(res => {
      let obj = { id: 0, name: "" };
      res.data.unshift(obj);
      this.customerTypeList = res.data;
    });
    // api
    API.getCustomerType().then(res => {
      let data = res.data;
      data.unshift({
        code: 0,
        in: null,
        val: ""
      });
      this.customerType = res.data;
    });
    // api
    API.getCustomerLevel().then(res => {
      let data = res.data;
      data.unshift({
        val: "",
        code: 0
      });
      this.customerLevel = data;
    });
    // api
    API.getCreationTime().then(res => {
      let data = res.data;
      data.splice(7);
      this.creationTime = res.data;
    });
    // api
    API.getRegion().then(res => {
      res.data.forEach(item => {
        item.value = item.id;
        if (item.children.length == "0") {
          item.children = "";
        } else {
          item.children.forEach(item1 => {
            item1.value = item1.id;
          });
        }
      });
      this.region = res.data;
    });
    // api
    API.getDepartments().then(res => {
      this.departmentsList = res.data;
    });
    // // 
    // API.getAllUser().then(res => {
    //   res.data.forEach(item => {
    //     item.value = item.name;
    //   });
    //   this.headList = res.data;
    // });
    // api
    API.getCustomerSource().then(res => {
      this.customerSourceList = res.data;
    });
    // table
  },
  activated() {
    this.tableApi();
  },
  computed: {
    selectIds() {
      let arr = [];
      let multipleSelection = this.multipleSelection;
      for (const item of multipleSelection) {
        arr.push(item.id);
      }
      return arr.join(",");
    }
  }
};
</script>

<style lang='scss' scoped>
.show-more-button {
  margin-bottom: 20px;
  text-align: center;
}

.el-form {
  .el-form-item {
    margin: 8px 0;
  }
}

.slide {
  transition: all 0.5s;
  overflow: hidden;
}

ul {
  list-style-type: none;
  padding: 0;
}
.list {
  li {
    display: inline-block;
    padding: 0 20px;
    cursor: pointer;
  }
}
.addclass {
  background: rgb(255, 242, 233);
  color: rgb(255, 178, 121);
}

.item {
  margin-bottom: 18px;
  margin-left: 20px;
}

.item-css {
  width: 100%;
}
.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}

.input-search button {
  margin-left: 30px;
}
</style>
