import fetch from '@/utils/fetch';


export default {
	// 
	findType(data) {
		return fetch({
			url: "businessType/findType",
			methods: "get",
			params: data
		})
	},
	// 
	addType(data) {
		return fetch({
			url: "businessType/addType",
			methods: "get",
			params: data
		})
	},
	// 
	delType(data) {
		return fetch({
			url: "businessType/delType",
			methods: "get",
			params: data
		})
	},
	// 
	addCompany(data) {
		return fetch({
			url: "user/addCompany",
			methods: "get",
			params: data
		})
	},
	// 
	getCompanyList(data) {
		return fetch({
			url: "user/getCompanyList",
			methods: "get",
			params: data
		})
	},
	// 
	updateType(data) {
		return fetch({
			url: "businessType/updateType",
			methods: "get",
			params: data
		})
	},
}