import fetch from '@/utils/fetch';
export default {
	// api
	getCustomerSource(data) {
		return fetch({
			url: `customer/channel`,
			method: 'get',
			params: data
		})
	},
	// 
	getChannelList(data) {
		return fetch({
			url: `channel/getChannelList`,
			method: 'get',
			params: data
		})
	},
	// 
	addChannel(data) {
		return fetch({
			url: `channel/addChannel`,
			method: 'post',
			params: data
		})
	},
	// 
	delChannel(data) {
		return fetch({
			url: "channel/delChannel",
			method: "get",
			params: data
		})
	},
	// 
	updateChannel(data) {
		return fetch({
			url: "channel/updateChannel",
			method: "get",
			params: data
		})
	},
	// 
	addChildrenChannel(data) {
		return fetch({
			url: "channel/addChildrenChannel",
			method: "post",
			params: data
		})
	},
	// 
	addPayAccount(data) {
		return fetch({
			url: "payAccount/addPayAccount",
			method: "post",
			params: data
		})
	},
	// 
	getPayAccountList(data) {
		return fetch({
			url: "payAccount/getPayAccountList",
			method: "post",
			params: data
		})
	},

}