<script>
import API from "@/api/customer";
import NewContact from "@/components/element/NewContact";
import NewContract from "@/components/element/NewContract";
import NewChance from "@/components/element/NewChance";
import FollowUpHistory from "@/components/element/FollowUpHistory"; // 
import Followup from "@/components/element/Followup"; // 
import Potential from "../components/Potential"; // 
import TabTask from "@/components/element/TabTask";
import TabAnnex from "@/components/element/TabAnnex";
import TabChance from "@/components/element/TabChance";
import TabContract from "@/components/element/TabContract";
import TabLinkman from "@/components/element/TabLinkman";

export default {
  name: "CustomerPanoramic",
  components: {
    NewContact,
    NewContract,
    NewChance,
    Potential,
    FollowUpHistory,
    Followup,
    TabLinkman,
    TabChance,
    TabTask,
    TabAnnex,
    TabContract
  },
  data() {
    return {
      isShowLinkman: false,
      isShowChance: false,
      isShowContract: false,
      isShowPotential: false,
      isShowFollow: false,
      panoramic: {}, // 
      head: {}, // 
      customerGrade: {}, // 
      channel: {}, // 
      customerData: {}, // 
      linkmanTableData: [], // 
      tableData: [], // 
      customerId: null
    };
  },
  methods: {
    // Dialog
    getSonSure() {
      this.getSonCancel();
    },
    // Dialog
    getSonCancel() {
      this.isShowLinkman = false;
      this.isShowChance = false;
      this.isShowContract = false;
      // console.log(this.isShow,'isShow')
    },
    addButton(name) {
      switch (name) {
        case "":
          this.isShowLinkman = true;
          break;
        case "":
          this.isShowChance = true;
          break;
        case "":
          this.isShowContract = true;
          break;
      }
    },
    handleCommand3(name) {
      switch (name) {
        case "":
          this.isShowPotential = true;
          break;
        case "":
          this.$confirm(
            "",
            "",
            {
              confirmButtonText: "",
              cancelButtonText: "",
              type: "warning"
            }
          )
            .then(() => {
              let data = {
                ids: this.selectIds
              };
              API.posttoPublic(data)
                .then(res => {
                  // console.log(res)
                  this.$message({
                    type: "success",
                    message: "!"
                  });
                  this.tableApi();
                })
                .catch(err => {
                  if (err.indexOf("") === -1) this.$message.warning(err);
                });
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: ""
              });
            });
          break;

        default:
          break;
      }
    },
    // 
    handleEdit1(index, row) {
      console.log(index, row);
    },
    // 
    handleView1(index, row) {
      console.log(index, row);
    },

    getContractPlanTable() {},
    getDetailData() {
      // api
      API.getCustomerDetails(this.customerId).then(res => {
        // console.log(res.data)
        let data = res.data;
        // 
        if (data.isWechat == 1) {
          data.isWechat = "";
        } else {
          data.isWechat = "";
        }
        // 
        if (data.isFace == 1) {
          data.isFace = "";
        } else {
          data.isFace = "";
        }
        this.panoramic = data;
        this.head = data.user;
        this.customerGrade = data.customerGrade;
        this.channel = data.channel;
      });
    },
    // tab 
    tabClick(tab, event) {
      let id = this.customerId;
      if (tab.label == "") {
        this.getDetailData();
      } else if (tab.label == "") {
        API.getCustomerInformationVoById(id).then(res => {
          this.customerData = res.data;
        });
      } else if (tab.label == "") {
        this.$refs.tabLinkman.getLinkmanTable();
      } else if (tab.label == "") {
        this.$refs.tabChance.getChanceTable();
      } else if (tab.label == "") {
        this.$refs.tabContract.getContractTable();
      } else if (tab.label == "") {
        this.$refs.tabTask.getTaskTable();
      } else if (tab.label == "") {
        this.$refs.tabAnnex.getAnnexTable();
      } else if (tab.label == "") {
        console.log("");
      }
    }
  },
  activated() {
    let id = this.$route.query.id;
    this.customerId = id;
    this.getDetailData();
  },
  beforeRouteLeave(to, from, next) {
    if (to.name == "" || to.name == "") {
      this.$destroy();
      next();
    } else {
      next();
    }
  }
};
</script>
