<template>
  <div>
    <el-form :model="form" label-width="120px" v-if="flag">
      <el-row>
        <el-col :span="24" class="list">
          <el-form-item label="">
            <ul>
              <li
                v-for="(item, index) in customerLevel"
                :key="index"
                @click="showStyle1(index, item.code)"
                :class="active1 == index ? 'addclass' : '' "
              >{{item.val}}</li>
            </ul>
          </el-form-item>
        </el-col>

        <el-col :span="24" class="list">
          <el-form-item label="">
            <ul>
              <li
                v-for="(item, index) in customerTypeList"
                :key="index"
                @click="showStyle2(index, item.id)"
                :class="serviceActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
            </ul>
          </el-form-item>
        </el-col>
        <el-col :span="24" class="list">
          <el-form-item label="">
            <ul>
              <li
                v-for="(item, index) in creationTime"
                :key="index"
                @click="timeChange(index, item.code)"
                :class="timeActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
              <li
                @click="showDefinedTime = true;timeActive = -1"
                class="list-li"
                v-if="!showDefinedTime"
              ></li>
              <li v-else>
                <el-date-picker
                  v-model="form.createTime"
                  @change="timeCustom"
                  type="daterange"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  range-separator=""
                  start-placeholder=""
                  end-placeholder=""
                ></el-date-picker>
              </li>
            </ul>
          </el-form-item>
        </el-col>

        <el-col :span="24">
          <el-col :span="7">
            <el-form-item label="">
              <el-select
                v-model="form.followName"
                @change="tableApi"
                clearable
                placeholder=""
                class="item-css"
                filterable
              >
                <el-option
                  v-for="(item, index) in headList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>

          <el-col :span="7">
            <el-form-item label="">
              <el-cascader
                :options="regionList"
                placeholder=""
                clearable
                v-model="form.regionArr"
                class="item-css"
                @change="region"
              ></el-cascader>
            </el-form-item>
          </el-col>
          <el-col :span="10">
            <el-form-item label="" class="input-search">
              <el-col :span="14">
                <el-input v-model="form.companyName" @clear="tableApi" placeholder="" clearable></el-input>
              </el-col>
              <el-col :span="6">
                <el-button type="primary" @click="tableApi()"></el-button>
              </el-col>
            </el-form-item>
          </el-col>
        </el-col>
      </el-row>
    </el-form>
    <!--  -->
    <!-- <el-row>
      <el-col :span="24" style="font-size:20px;text-align:center;">
        <i
          style="cursor: pointer;"
          :class="flag == true? 'el-icon-arrow-up' : 'el-icon-arrow-down'"
          @click="show"
        ></i>
      </el-col>
    </el-row>-->
    <!--  -->
    <el-row style="margin-top:20px;">
      <!--  -->
      <el-popover placement="top-start" width="300" trigger="click">
        <el-checkbox-group v-model="showCol" @change="colChange">
          <el-checkbox
            style="width:120px;line-height:25px"
            v-for="(item,index) in colOptions"
            :key="index"
            :label="item"
          ></el-checkbox>
        </el-checkbox-group>
        <el-button
          slot="reference"
          style="margin-right:10px;"
          type="primary"
          icon="el-icon-s-operation"
          plain
        ></el-button>
      </el-popover>
      <el-button
        type="success"
        icon="el-icon-document-copy"
        :disabled="!checkboxId.length"
        @click="isShowCustomer = true"
      ></el-button>
      <el-button
        type="danger"
        icon="el-icon-delete"
        :disabled="!checkboxId.length"
        @click="handleDelete"
      ></el-button>
    </el-row>
    <!--  -->
    <el-table
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      border
      stripe
      :fit="true"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" fixed></el-table-column>
      <el-table-column
        prop="companyName"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="serviceName"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column prop="telephone" v-if="showCol.indexOf('')>-1" label="" align="center"></el-table-column>
      <el-table-column
        prop="address"
        v-if="showCol.indexOf('')>-1"
        label=""
        show-overflow-tooltip
        align="center"
      ></el-table-column>
      <el-table-column
        prop="serviceName"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="channelName"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="contents"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="followTime"
        v-if="showCol.indexOf('')>-1"
        show-overflow-tooltip
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="createTime"
        v-if="showCol.indexOf('')>-1"
        show-overflow-tooltip
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="gradeStr"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="searchKeywords"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column label="" width="150" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            plain
            @click="handleEdit(scope.$index, scope.row)"
          ></el-button>
          <el-button size="mini" @click="handleView(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      class="itemPage"
      @size-change="sizeChangePage"
      @current-change="currentChangePage"
      :current-page="page"
      :page-sizes="[10, 20, 30, 40]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalSize"
    ></el-pagination>

    <!--  -->
    <move-client
      :checkboxId="checkboxId"
      :isShowCustomer="isShowCustomer"
      @closeDialogFather="isShowCustomer=false"
      @sureDialogFather="tableApi()"
    ></move-client>

    <edit-customer
      :isShowEdit="isShowEdit"
      :customerId="customerId"
      :customerLevel="customerLevel.slice(0,1)"
      :customerTypeList="customerTypeList"
      @sureDialogFather="tableApi()"
      @closeDialogFather="isShowEdit=false"
    ></edit-customer>
  </div>
</template>

<script>
// 
import API from "@/api/highseas";
import { constants } from "crypto";
import EditCustomer from "@/components/element/EditCustomer"; // 
import MoveClient from "./components/MoveClient"; // 
export default {
  name: "highSeas",
  components: {
    EditCustomer,
    MoveClient
  },
  data() {
    return {
      flag: true, // 
      customerLevel: [], // 
      active1: 0,
      timeActive: 0,
      serviceActive: 0,
      regionList: [], // 
      customerTypeList: [], // 
      creationTime: [], // 
      headList: [], // 
      isShowCustomer: false, // 
      showDefinedTime: false,
      form: {
        createTime: "", //
        followUser: "", //
        serviceId: "", //
        companyName: "", //
        regionArr: [] // 
      },
      showCol: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      colOptions: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      tableData: [], // tabel
      page: 1, //
      rows: 10, //
      totalSize: 0, //
      grade: null, // 
      province: null, // 
      city: null, // 
      service: null, // 
      codeTime: null, // 
      startDate: null, // 
      endDate: null, // 
      checkboxId: "", // 

      isShowEdit: false,
      customerId: "" // id
    };
  },
  methods: {
    colChange() {
      localStorage.setItem("highSeasTable", JSON.stringify(this.showCol));
    },
    // tableapi
    tableApi() {
      let form = this.form;
      form.regionArr = form.regionArr || [null, null];
      let params = {
        pageNum: this.page,
        pageSize: this.rows,
        grade: this.grade,
        province: form.regionArr[0],
        city: form.regionArr[1],
        service: this.service,
        timeCode: this.codeTime,
        startDate: this.startDate ? this.startDate : null,
        endDate: this.endDate ? this.endDate : null,
        createTime: this.createTime,
        followName: form.followName,
        // serviceId:form.serviceId,
        companyName: form.companyName
      };
      console.log(params);

      API.postList(params).then(res => {
        // console.log(res.data)
        this.totalSize = res.data.total; //
        res.data.list.forEach(item => {
          if (item.city == null) {
            item.address = item.province;
          } else {
            item.address = item.province + item.city;
          }
        });
        this.tableData = res.data.list;
      });
    },

    //
    sizeChangePage(val) {
      this.rows = val;
      this.tableApi();
    },
    //
    currentChangePage(val) {
      this.page = val;
      this.tableApi();
    },
    // 
    showStyle1(index, code) {
      this.active1 = index;
      this.grade = code;
      this.tableApi();
    },
    // 
    region(val) {
      this.province = val[0];
      this.city = val[1];
      this.tableApi();
    },
    // 
    showStyle2(index, code) {
      this.serviceActive = index;
      this.service = code;
      this.tableApi();
    },
    // 
    timeChange(index, code) {
      this.showDefinedTime = false;
      this.timeActive = index; // 
      this.startDate = this.endDate = null;
      this.codeTime = code;
      this.tableApi();
    },
    // 
    timeCustom(val) {
      if (val != null) {
        this.startDate = val[0];
        this.endDate = val[1];
        this.codeTime = null;
        this.tableApi();
      }
    },
    // // 
    // head(val) {
    //   this.followUser = val;
    //   this.tableApi();
    // },
    querySearchStr(queryString, cb) {
      var restaurants = this.headList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      cb(results);
    },
    // table
    handleSelectionChange(val) {
      let arr = [];
      val.map(ele => {
        arr.push(ele.id);
      });
      this.checkboxId = arr;
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    // 
    handleDelete() {
      let Params = { ids: this.checkboxId.join(",") };
      this.$confirm("", "", {
        distinguishCancelAndClose: true,
        confirmButtonText: "",
        cancelButtonText: ""
      }).then(() => {
        API.postdeleteSeaCustomer(Params).then(res => {
          this.$message({
            message: "",
            type: "success"
          });
          this.tableApi();
        });
      });
    },
    // 
    handleEdit(index, row) {
      this.customerId = row.id;
      this.isShowEdit = true;
    },
    // Dialog
    getSonSure() {
      this.isShowEdit = false;
    },

    // 
    handleView(index, row) {
      this.$router.push({ path: "highSeasdetails", query: { id: row.id } });
    }
  },
  activated() {
    // tabel
    this.tableApi();
  },
  mounted() {
    let showCol = JSON.parse(localStorage.getItem("highSeasTable"));
    if (showCol) this.showCol = showCol;

    // api
    API.getCustomerLevel().then(res => {
      let obj = { code: 0, val: "" };
      res.data.unshift(obj);
      this.customerLevel = res.data;
    });
    // api
    API.getRegion().then(res => {
      res.data.forEach(item => {
        item.value = item.id;
        if (item.children.length == "0") {
          item.children = "";
        } else {
          item.children.forEach(item1 => {
            item1.value = item1.id;
          });
        }
      });
      this.regionList = res.data;
    });
    // api
    API.getType().then(res => {
      let obj = { id: 0, name: "" };
      res.data.unshift(obj);
      this.customerTypeList = res.data;
    });
    // 
    API.getCreationTime().then(res => {
      // let obj = { code: 0, name: "" };
      // res.data.unshift(obj);
      res.data.splice(7);
      this.creationTime = res.data;
    });
    // 
    API.getAllUser().then(res => {
      res.data.map(ele => {
        ele.value = ele.name;
      });
      this.headList = res.data;
    });
  },
  watch: {}
};
</script>

<style lang='scss' scoped>
ul {
  list-style-type: none;
  padding: 0;
}
.list {
  li {
    display: inline-block;
    padding: 0 20px;
    cursor: pointer;
  }
}
.addclass {
  background: rgb(255, 242, 233);
  color: rgb(255, 178, 121);
}

.item {
  margin-bottom: 18px;
  margin-left: 20px;
}

.item-css {
  width: 100%;
}
.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}

.input-search button {
  margin-left: 30px;
}
</style>
