<template>
  <div>
    <el-row>
      <el-col :span="24">
        <el-col :span="8">
          <h1>{{message.name}}</h1>
        </el-col>
        <el-col :span="8">
          <h3>{{customer.companyName}}</h3>
        </el-col>
      </el-col>
    </el-row>
    <el-row>
      <el-dropdown trigger="click" class="el-dro" @command="handleCommand">
        <el-button type="primary" class="el-dropdown-link">
          {{commandName}}
          <i class="el-icon-arrow-down el-icon--right"></i>
        </el-button>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item
            v-for="(item, i) in contractStatusList"
            :key="i"
            :command="item.val"
            :val="item.code"
          >{{item.val}}</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <el-button
        type="primary"
        @click="isShowContractEdit=true"
        permi="'/contract/updateContract'"
      ></el-button>
      <el-button type="warning" @click="isShowPotential=true"></el-button>
      <el-button type="danger" @click="deleteContract"></el-button>
      <el-button type="success" @click="showSend"></el-button>
    </el-row>

    <el-tabs type="card" @tab-click="tabClick">
      <el-tab-pane label="" class="quanjing">
        <el-card class="box-card">
          <div>
            <div class="text"></div>
            <div class="text">
              <p></p>
              <p>{{message.name}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{customer.companyName}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.totalPrice}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.signDate}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.ladingBillTime}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.endTime}}</p>
            </div>
            <el-divider></el-divider>
            <div class="text"></div>
            <div class="text">
              <p></p>
              <p>{{message.name}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{paymentTotal}}</p>
            </div>
            <el-divider></el-divider>
            <div class="text"></div>
            <div class="text">
              <p></p>
              <p>{{message.statusStr}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.signNo}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{packageType.name}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.payNoStr}}</p>
            </div>
            <!-- <div class="text  ">()
              <p></p>
              <p>{{user.name}}</d</p>
            iv>-->
            <!-- <div class="text">
              <p></p>
              <el-button size="minddle" type="primary"></el-button>
            </div>-->
            <div class="text">
              <p></p>
              <p>{{message.nextFollowTime}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.remarks}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.fromName}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.telephone}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.intentionStr}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.receiptNo}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.isInvoice}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.incomeTypeStr}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.payment}}</p>
            </div>
            <!-- <div class="text  ">
              <p></p>
              <p>{{message.nextFollowTime}}</d</p>
            iv>
             <div class="text">
              <p></p>
              <p>{{message.companyName}}</p>
            </div>-->
            <div class="text">
              <p></p>
              <p>{{message.receiptNumber}}</p>
            </div>
            <!-- <div class="text">
              <p></p>
              <p>{{message.searchWords}}</p>
            </div>-->
            <el-divider></el-divider>
            <div class="text"></div>
            <div class="text">
              <p></p>
              <p>{{user.name}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.createUserName}}</p>
            </div>
            <!-- <div class="text  ">
              <p></p>
              <p>{{message.oldFollowUser}}</d</p>
            iv>-->
            <div class="text">
              <p></p>
              <p>{{depart.name}}</p>
            </div>
            <!-- <div class="text  ">
              <p></p>
              <p>{{message.oldFollowDepart}}</d</p>
            iv>-->
            <div class="text">
              <p></p>
              <p>{{message.createTime}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.updateTime}}</p>
            </div>
          </div>
        </el-card>
        <follow-up-history
          class="genjin"
          @writeFollow="isShowFollow=true"
          :customerId="customerId"
          :type="1"
          ref="followUpHistory"
        ></follow-up-history>
      </el-tab-pane>
      <el-tab-pane label="">
        <el-card>
          <div class="returned-title">
            <span>{{paymentPriceTop.plan.planTotalPrice}}</span>
            <span>{{paymentPriceTop.plan.alreadyTotalPrice}}</span>
            <span>{{paymentPriceTop.plan.notTotalPrice}}</span>
            <span>{{paymentPriceTop.plan.overTimeTotalPrice}}</span>
            <span v-show="!HavepaymentPrice.length">
              <el-button
                type="warning"
                v-permi="'/payment/addContractPaymentPlan'"
                @click="isNewReceivableplan=true"
              ></el-button>
            </span>
          </div>
        </el-card>
        <el-table
          :data="HavepaymentPrice"
          tooltip-effect="dark"
          style="width: 100%;"
          border
          :fit="true"
        >
          <el-table-column prop="opeDate" label="" align="center">
            <template slot-scope="scope">{{scope.row.opeDate.slice(0,10)}}</template>
          </el-table-column>
          <!-- <el-table-column prop="opeDate" label="" align="center"></el-table-column> -->
          <el-table-column prop="price" label="" align="center"></el-table-column>
          <el-table-column prop="userName" label="" align="center"></el-table-column>
          <!-- <el-table-column prop="returnStatus" label="" align="center"></el-table-column> -->
          <el-table-column prop="realPay" label="" align="center"></el-table-column>
          <el-table-column prop="remarks" label="" align="center">
            <template slot-scope="scope">{{scope.row.isOver?"":""}}</template>
          </el-table-column>
          <el-table-column label="" width="100" align="center" fixed="right">
            <template slot-scope="scope">
              <!-- <el-button
                size="mini"
                :disabled="Boolean(scope.row.isOver)"
                @click="editPlan(scope.$index, scope.row)"
                type="primary"
                plain
              ></el-button> -->
              <el-button
                size="mini"
                @click="handleDelete(scope.$index, scope.row, '')"
                type="danger"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="">
        <el-card>
          <div class="returned-title">
            <span>{{paymentPriceTop.record.totalPrice}}</span>
            <span>{{paymentPriceTop.record.alreadyTotalPrice}}</span>
            <span>{{paymentPriceTop.record.notTotalPrice}}</span>
            <!-- <span>{{paymentPriceTop.record.overTimeTotalPrice}}</span> -->
            <el-button type="warning"
              v-permi="'/payment/addRealContractPayment'"
             @click="isNewReceivablerecord=true"></el-button>
          </div>
        </el-card>

        <el-table
          :data="NopaymentPrice"
          tooltip-effect="dark"
          style="width: 100%;"
          border
          :fit="true"
        >
          <el-table-column prop="opeDate" label="" align="center"></el-table-column>
          <el-table-column prop="price" label="" align="center"></el-table-column>
          <el-table-column prop="payNo" label="" align="center"></el-table-column>
          <el-table-column prop="payType" label="" align="center"></el-table-column>
          <el-table-column prop="paymentUser" label="" align="center"></el-table-column>
          <el-table-column prop="remarks" label="" align="center"></el-table-column>
          <el-table-column label="" width="150" align="center" fixed="right">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="primary"
                plain
                v-permi="'/payment/updateContractPaymentPlan'"
                @click="editRecord(scope.$index, scope.row)"
              ></el-button>
              <!-- <el-button size="mini" @click="handleView(scope.$index, scope.row)"></el-button> -->
              <el-button
                size="mini"
                @click="handleDelete(scope.$index, scope.row,'')"
                type="danger"
              ></el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="">
        <tab-task ref="tabTask" :customerId="customerId"></tab-task>
      </el-tab-pane>
      <el-tab-pane label="">
        <tab-annex ref="tabAnnex" :customerId="customerId"></tab-annex>
      </el-tab-pane>
      <!-- <el-tab-pane label="">
        <el-table
          :data="tableData"
          tooltip-effect="dark"
          style="width: 100%;margin-top: 20px;"
          border
          :fit="true"
        >
          <el-table-column prop="name" label="" align="center"></el-table-column>
          <el-table-column prop="name" label="" align="center"></el-table-column>
          <el-table-column prop="name" label="" align="center"></el-table-column>
          <el-table-column prop="name" label="" align="center"></el-table-column>
        </el-table>
      </el-tab-pane>-->
    </el-tabs>
    <new-receivablerecord
      :isNewReceivablerecord="isNewReceivablerecord"
      :detailData="addRecordData"
      @sureDialogFather="getSonSure;getContractRecordTable();getContractPlanTable()"
      @closeDialogFather="getSonCancel"
    ></new-receivablerecord>
    <NewContractEdit
      :isShowContractEdit="isShowContractEdit"
      :contractId="contractId"
      :contractStatusList="contractStatusList"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewContractEdit>
    <Potential
      :isShowPotential="isShowPotential"
      :contractId="contractId"
      @sureDialogFather="$router.go(-1)"
      @closeDialogFather="getSonCancel"
    ></Potential>
    <NewSend
      :isShowSend="isShowSend"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewSend>

    <edit-receivableplan
      :isEditReceivableplan="isEditReceivableplan"
      :editPlanData="editPlanData"
      @sureDialogFather="getSonSure;getContractPlanTable()"
      @closeDialogFather="getSonCancel"
    ></edit-receivableplan>

    <new-receivableplan
      :contractDetail="contractDetail"
      :isNewReceivableplan="isNewReceivableplan"
      @sureDialogFather="getSonSure;getContractPlanTable()"
      @closeDialogFather="getSonCancel"
    ></new-receivableplan>
    <Followup
      :isShowFollow="isShowFollow"
      :customerId="customerId"
      @sureDialogFather="$refs.followUpHistory.getFollowData()"
      @closeDialogFather="getSonCancel"
    ></Followup>
    <edit-receivablerecord
      :isEditReceivablerecord="isEditReceivablerecord"
      :editRecordData="editRecordData"
      @sureDialogFather="getSonSure;getContractRecordTable();"
      @closeDialogFather="getSonCancel"
    ></edit-receivablerecord>
  </div>
</template>

<script>
import API from "@/api/contract";
import EditReceivablerecord from "../components/EditReceivablerecord"; // 
import NewReceivablerecord from "../components/NewReceivablerecord"; // 
import EditReceivableplan from "../components/EditReceivableplan"; // 
import NewReceivableplan from "../components/NewReceivableplan"; // 
import Potential from "../components/Potential"; // 

import TabTask from "@/components/element/TabTask";
import NewSend from "@/components/element/NewSend"; // 
import TabAnnex from "@/components/element/TabAnnex";
import Followup from "@/components/element/Followup"; // 
import NewContractEdit from "@/components/element/EditContract"; // 
import FollowUpHistory from "@/components/element/FollowUpHistory"; // 

export default {
  name: "contactdetails",
  components: {
    NewReceivablerecord,
    NewContractEdit,
    FollowUpHistory,
    TabAnnex,
    EditReceivableplan,
    Potential,
    NewSend,
    TabTask,
    NewReceivableplan,
    Followup,
    EditReceivablerecord
  },
  data() {
    return {
      contractStatusList: [], // 
      commandName: "",
      isNewReceivablerecord: false,
      isShowContractEdit: false,
      isShowPotential: false,
      isShowSend: false,
      isShowFollow: false,
      isNewReceivableplan: false,
      isEditReceivablerecord: false,
      editRecordData: "", // id
      contractId: "", // id
      customerId: null, // id
      paymentTotal: "", // 
      message: {}, // 
      customer: {},
      packageType: {},
      channel: {},
      user: {},
      addRecordData: {}, // 
      depart: {},
      paymentPriceTop: {
        // 
        plan: {}, // 
        record: {} // 
      },
      isEditReceivableplan: false, // 
      editPlanData: {}, // 
      NopaymentPrice: [], // 
      HavepaymentPrice: [], // 
      tableData: [],
      contractDetail: {} // 
    };
  },
  methods: {
    // 
    handleCommand(command, val) {
      let data = {
        id: this.contractId,
        status: val.$attrs.val
      };
      API.postchangeContractStatus(data).then(res => {
        this.$message({
          type: "success",
          message: "!"
        });
        this.commandName = command;
      });
    },
    // 
    deleteContract() {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          API.postdeleteContract({ ids: this.contractId })
            .then(res => {
              this.$message({
                type: "success",
                message: "!"
              });
              this.$router.push({ path: "contract" });
            })
            .catch(err => {
              if (err.indexOf("") === -1) this.$message.warning(err);
            });
        })
        .catch(() => {
          this.$message({
            type: "error",
            message: ""
          });
        });
    },
    // 
    showSend() {
      this.isShowSend = true;
    },
    // 
    showFollow() {
      this.isShowFollow = true;
    },

    editPlan(index, row) {
      this.editPlanData = {
        customerId: this.customerId,
        departId: row.departId,
        userName: row.userName,
        contractId: Number(this.contractId),
        opeDate: row.opeDate,
        price: row.price,
        id: row.id
      };
      this.isEditReceivableplan = true;
    },

    // 
    editRecord(index, row) {
      this.editRecordData = {
        opeDate: row.opeDate,
        customerId: this.customerId,
        payType: row.payTypeId,
        price: row.price,
        contractId: Number(this.contractId),
        remarks: row.remarks,
        userName: row.paymentUser,
        planpayId: row.planpayId,
        departId: row.departId,
        isOver: row.isOver,
        payNo: row.payNoId,
        id: row.id
      };

      this.isEditReceivablerecord = true;
    },
    // 
    handleDelete(index, row, name) {
      this.$confirm("" + name, "" + name, {
        distinguishCancelAndClose: true,
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(() => {
          API.postdeletePaymentPlans({
            ids: row.id
          }).then(res => {
            this.$message({
              message: "",
              type: "success"
            });
            if (name === "") {
              this.getContractPlanTable();
            } else {
              this.getContractRecordTable();
            }
          });
        })
        .catch(() => {});
    },
    // 
    handleView(index, row) {
      this.$router.push({
        path: "receivableRecorddetails",
        query: { id: row.id }
      });
    },
    // Dialog
    getSonSure() {
      this.getSonCancel();
      this.getDetailData();
    },
    // Dialog 
    getSonCancel() {
      this.isNewReceivablerecord = false;
      this.isShowContractEdit = false;
      this.isShowPotential = false;
      this.isShowSend = false;
      this.isShowFollow = false;
      this.isNewReceivableplan = false;
      this.isEditReceivablerecord = false;
      this.isEditReceivableplan = false;
    },
    // tab 
    tabClick(tab) {
      if (tab.label == "") {
      } else if (tab.label == "") {
        this.getContractPlanTable();
      } else if (tab.label == "") {
        this.getContractRecordTable();
      } else if (tab.label == "") {
        this.$refs.tabTask.getTaskTable();
      } else if (tab.label == "") {
        this.$refs.tabAnnex.getAnnexTable();
      } else if (tab.label == "") {
        // console.log('')
      }
    },
    getContractPlanTable() {
      API.getfindContractPaymentPlans(this.contractId).then(res => {
        let data = res.data;
        if (!data) {
          this.HavepaymentPrice = [];
          this.paymentPriceTop.plan = {
            planTotalPrice: null,
            notTotalPrice: null,
            alreadyTotalPrice: null,
            overTimeTotalPrice: null
          };
        } else {
          this.paymentPriceTop.plan = {
            planTotalPrice: data.planTotalPrice,
            notTotalPrice: data.notTotalPrice,
            alreadyTotalPrice: data.alreadyTotalPrice,
            overTimeTotalPrice: data.overTimeTotalPrice
          };
          this.HavepaymentPrice = data.payments;
        }
      });
    },
    getContractRecordTable() {
      API.getfindContractPayment(this.contractId).then(res => {
        let data = res.data;
        if (!data) {
          this.NopaymentPrice = [];
          this.paymentPriceTop.record = {
            alreadyTotalPrice: null,
            notTotalPrice: null,
            totalPrice: null,
            overTimeTotalPrice: null
          };
        } else {
          this.NopaymentPrice = data.contractPaymentVos;
          this.paymentPriceTop.record = {
            alreadyTotalPrice: data.alreadyTotalPrice,
            notTotalPrice: data.notTotalPrice,
            totalPrice: data.totalPrice,
            overTimeTotalPrice: data.overTimeTotalPrice
          };
        }
      });
    },
    getDetailData() {
      API.getContractDetails(this.contractId).then(res => {
        let data = res.data;
        this.message = data;
        this.channel = data.channel || {};
        this.customer = data.customer || {};
        this.packageType = data.packageType || {};
        this.user = data.user;
        this.depart = data.depart || {};
        this.customerId = data.customerId;
        this.contractDetail = {
          customerId: data.customer.companyName,
          customerIdNum: data.customerId,
          totalPrice: data.totalPrice,
          payment: data.payment,
          signDate: data.signDate,
          contractId: Number(this.contractId),
          id: this.customerId,
          residue: data.totalPrice - this.paymentTotal
        };
        this.addRecordData = {
          customerId: data.customerId,
          customerName: data.customer.companyName,
          contractId: Number(this.contractId)
        };
      });
    }
  },
  mounted() {
    this.contractId = this.$route.query.id;
    this.paymentTotal = this.$route.query.paymentTotal;
    // 
    this.getDetailData();
    // 
    API.getContractStatus().then(res => {
      // console.log(res);
      let statusList = res.data;
      API.getfindContractStatusById(this.contractId).then(res => {
        statusList.forEach(item => {
          if (res.data == item.code) {
            this.commandName = item.val;
          }
        });
        this.contractStatusList = statusList;
      });
    });
  }
};
</script>
<style lang='scss' scoped>
.el-dropdown-link {
  cursor: pointer;
}
.returned-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.el-icon-arrow-down {
  font-size: 12px;
}
.el-dro {
  margin-right: 10px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.text {
  font-size: 14px;
  line-height: 40px;
  display: flex;
  & > :first-child {
    width: 110px;
  }
  & > p:last-child {
    flex: 1;
    word-break: break-all;
    word-wrap: break-word;
  }
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.box-card {
  width: 48%;
  margin-top: 20px;
}
.genjin {
  position: absolute;
  top: 0;
  right: 0;
  width: 48%;
}
.quanjing {
  position: relative;
}
span {
  font-size: 18px;
}
</style>