<template>
  <div>
    <el-dialog
      title=""
      :close-on-click-modal="false"
      :visible.sync="dialogVisible"
      @close="closeDialog"
    >
      <el-form :model="form" :rules="rules" label-width="120px" ref="form">
        <el-form-item label="" prop="name">
          <el-input v-model="form.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="customerId">
          <el-select
            v-model="form.customerId"
            @change="customerChange"
            :disabled="Boolean(customerId)"
            placeholder=""
            class="item-css"
            filterable
          >
            <el-option
              v-for="(item, index) in restaurants"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="linkmanId">
          <el-select
            v-model="form.linkmanId"
            :no-data-text="form.customerId?'':''"
            placeholder=""  
          >
            <el-option
              v-for="(item, i) in LinkmenList"
              :key="i"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="serviceId">
          <el-select v-model="form.serviceId" placeholder="" >
            <el-option
              v-for="(item, i) in BusinessTypes"
              :key="i"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="form.expectPrice" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=" ">
          <el-date-picker
            v-model="form.expectDate"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="stage">
          <el-select v-model="form.stage" placeholder="" clearable>
            <el-option v-for="(item, i) in SaleStage" :key="i" :label="item.val" :value="item.code"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-select v-model="form.type" placeholder="" clearable>
            <el-option
              v-for="(item, i) in ChanceType"
              :key="i"
              :label="item.val"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="form.getTime"
            :picker-options="pickerOptions"
            type="date"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="source">
          <el-select v-model="form.source" placeholder="" clearable>
            <el-option v-for="(item, i) in channel" :key="i" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
            v-model="form.nextTime"
            type="datetime"
            value-format="yyyy-MM-dd HH:mm:ss"
            placeholder=""
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="" prop="followDepart">
          <el-select
            v-model="form.followDepart"
            placeholder=""
            clearable
            @change="departChange($event)"
          >
            <el-option
              v-for="(item, index) in departmentsList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="followUser">
          <el-select v-model="form.followUser" @change="userChange" filterable placeholder="">
            <el-option
              v-for="(item,index) in headList"
              :key="index"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="">
          <el-input v-model="form.probability" placeholder=""></el-input>
        </el-form-item>-->
        <el-form-item label="">
          <el-input
            v-model="form.remarks"
            style="width:60%;"
            type="textarea"
            show-word-limit
            maxlength="100"
            :autosize="{ minRows: 4, maxRows: 10}"
            placeholder=""
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="sureDialog" type="success"></el-button>
        <el-button @click="closeDialog"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// 
import API from "@/api/chance";
export default {
  name: "NewChance",
  props: {
    isShowChance: {
      type: Boolean,
      default: false
    },
    customerId: {
      type: Number | String,
      default: ""
    },
    customerName: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now(); //-8.64e7
        }
      },
      rules: {
        name: [{ required: true, message: "", trigger: "blur" }],
        customerId: [
          { required: true, message: "", trigger: "change" }
        ],
        linkmanId: [
          { required: true, message: "", trigger: "change" }
        ],
        serviceId: [
          { required: true, message: "", trigger: "change" }
        ],
        stage: [
          { required: true, message: "", trigger: "change" }
        ],
        type: [
          { required: true, message: "", trigger: "change" }
        ],
        source: [
          { required: true, message: "", trigger: "change" }
        ],
        followDepart: [
          { required: true, message: "", trigger: "change" }
        ],
        followUser: [
          { required: true, message: "", trigger: "change" }
        ]
      },
      dialogVisible: this.isShowChance,
      restaurants: [], // 
      LinkmenList: [], // 
      BusinessTypes: [], // 
      SaleStage: [], // 
      ChanceType: [], // 
      channel: [], // 
      departmentsList: [], // 
      headList: [], // 
      form: {
        name: "", // 
        customerId: "", // id
        linkmanId: "", // id
        serviceId: "", // id
        expectPrice: "", // 
        expectDate: "", // 
        stage: "", // id
        type: "", // id
        getTime: "", // 
        source: "", // id
        nextTime: "", // 
        followDepart: "", // 
        followUser: "", // 
        // probability: "", // 
        remarks: "" // 
      }
    };
  },
  methods: {
    // 
    closeDialog() {
      this.dialogVisible = false;

      this.$emit("closeDialogFather", this.dialogVisible);
    },
    // 
    clearFollowUser() {
      if (this.form.followDepart) {
        this.departChange(this.form.followDepart);
      }
    },
    // 
    sureDialog() {
      this.$refs.form.validate(valid => {
        if (!valid) return;
        let form = this.form;
        console.log(form);
        let params = {
          name: form.name,
          customerId: form.customerId,
          linkmanId: form.linkmanId,
          serviceId: form.serviceId,
          expectPrice: form.expectPrice,
          expectDate: form.expectDate,
          stage: form.stage,
          type: form.type,
          getTime: form.getTime,
          source: form.source,
          nextTime: form.nextTime,
          followDepart: form.followDepart,
          followUser: form.followUser,
          // probability: form.probability,
          remarks: form.remarks
        };
        console.log(params);

        API.postaddChance(params).then(res => {
          // console.log(res)
          this.$message({
            message: "",
            type: "success"
          });
          this.$refs.form.resetFields();
          this.$emit("sureDialogFather");
        });
      });
    },
    // 
    customerChange(val) {
      this.form.linkmanId = null;
      API.getCustomerLinkman(val).then(res => {
        this.LinkmenList = res.data;
      });
    },
    // 
    departChange(flag) {
      if (flag !== "no_change") this.form.followUser = null;
      let val = this.form.followDepart;
      if (val) {
        API.getHead(val).then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          res.data.forEach(item => {
            item.value = item.name;
          });
          this.headList = res.data;
        });
      }
    },
    // 
    userChange(val) {
      let departId = this.headList.find(item => {
        return item.id === val;
      }).departId;
      this.form.followDepart = departId;
      this.departChange("no_change");
    }
  },
  watch: {
    isShowChance(value) {
      this.dialogVisible = this.isShowChance;
      if (!value) return;
      let userInfo = this.$store.state.userInfo;
      this.form.followUser = userInfo.id;
      this.form.followDepart = userInfo.departId;
      // 
      API.getCustomerByCompanyName().then(res => {
        res.data.forEach(item => {
          item.value = item.name;
        });
        this.restaurants = res.data;
      });
      if (this.customerId) {
        this.form.customerId = this.customerId;
        API.getCustomerLinkman(this.customerId).then(res => {
          this.LinkmenList = res.data;
        });
      }
      // 
      API.getType().then(res => {
        this.BusinessTypes = res.data;
      });
      // 
      API.getSaleStage().then(res => {
        this.SaleStage = res.data;
      });
      // 
      API.getChanceType().then(res => {
        this.ChanceType = res.data;
      });
      // 
      API.getchannel().then(res => {
        this.channel = res.data;
      });
      // 
      API.getDepartments().then(res => {
        this.departmentsList = res.data;
      });
      // 
      API.getAllUser().then(res => {
        res.data.forEach(item => {
          item.value = item.name;
        });
        this.headList = res.data;
      });
    }
  }
};
</script>
<style scoped lang="scss">
/deep/.el-dialog {
  width: 40%;
  .el-input__inner {
    width: auto;
  }
  // .el-input:not(.el-input--suffix) {
  //   width: 50%;
  // }
}
</style>