<template>
  <div>
    <!-- <el-button type="primary" @click="outerVisible = true"></el-button> -->
    <el-dialog title="" :visible.sync="outerVisible" width="40%" @close="closeDialog">
      <div class="item">
        1
        <el-link type="success" @click="downfile"></el-link>
      </div>
      <div class="item"></div>
      <div class="item">1</div>
      <div class="item">2</div>
      <div class="item">310000</div>
      <div class="item">2</div>
      <el-upload
        class="upload-demo"
        ref="upload"
        :on-success="uploadSuccess"
        :on-error="uploadError"
        :action="action"
        :file-list="fileList"
        :auto-upload="false"
      >
        <el-button class="item" type="primary" icon="el-icon-plus"></el-button>
      </el-upload>

      <div slot="footer" class="dialog-footer">
        <el-button @click="sureDialog" type="primary"></el-button>
        <el-button @click="closeDialog"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
// 
import API from "@/api/customer";
export default {
  name: "UploadClient",
  props: ["isShowClues"],
  data() {
    return {
      outerVisible: this.isShowClues,
      action: this.$store.state.baseURL + "/customer/batchUploadCustomer", // 
      fileList: [] //
    };
  },
  methods: {
    uploadSuccess(res) {
      if (res.status !== 200) {
        this.$message({
          type: "info",
          message: res.msg
        });
        return;
      }
      this.fileList = [];
      this.$message({
        type: "success",
        message: ""
      });
      this.$emit("sureDialogFather");
      this.closeDialog();
    },
    uploadError(res) {
      res = res.toString().slice(7);
      let msg = JSON.parse(res).message;
      this.$message({
        type: "error",
        message: "" + msg
      });
    },
    // 
    downfile() {
      window.location.href = this.$store.state.baseURL + "customer/downFile";
    },
    // 
    sureDialog() {
      this.$refs.upload.submit();
    },
    // 
    closeDialog() {
      this.outerVisible = false;
      this.$emit("closeDialogFather");
    }
  },
  watch: {
    isShowClues() {
      this.outerVisible = this.isShowClues;
    }
  }
};
</script>
<style lang='scss' scoped>
.item {
  margin-bottom: 18px;
  margin-left: 20px;
}
</style>