<template>
  <div>
    <el-form :model="form" label-width="120px">
      <el-row>
        <el-row :span="24" class="list">
          <el-form-item label="">
            <ul>
              <li
                v-for="(item, index) in contractStatusList"
                :key="index"
                @click="showStyle(index, item.code)"
                :class="active == index ? 'addclass' : '' "
              >{{item.val}}</li>
            </ul>
          </el-form-item>
        </el-row>
        <el-row :span="24" class="list">
          <el-form-item label="">
            <ul>
              <li
                v-for="(item, index) in statusList"
                :key="index"
                @click="showStyle2(index, item.code)"
                :class="active2 == index ? 'addclass' : '' "
              >{{item.val}}</li>
            </ul>
          </el-form-item>
        </el-row>
        <el-row :span="24" class="list">
          <el-form-item label="">
            <ul>
              <li
                v-for="(item, index) in timeCodeList"
                :key="index"
                @click="timeChange(index, item.code)"
                :class="timeActive == index ? 'addclass' : '' "
              >{{item.name}}</li>

              <li
                @click="showDefinedTime=true;timeActive = -1"
                class="list-li"
                v-if="!showDefinedTime"
              ></li>
              <li v-else>
                <el-date-picker
                  v-model="form.createTime"
                  @change="timeCustom"
                  type="daterange"
                  value-format="yyyy-MM-dd"
                  range-separator=""
                  start-placeholder=""
                  end-placeholder=""
                ></el-date-picker>
              </li>
            </ul>
          </el-form-item>
        </el-row>

        <div class="slide" :style="{'height':showMore?'60px':0}">
          <el-row>
            <el-col :span="7">
              <el-form-item label="">
                <el-select
                  v-model="form.departId"
                  placeholder=""
                  clearable
                  class="item-css"
                  @change="depart($event)"
                >
                  <el-option
                    v-for="(item, index) in departmentsList"
                    :key="index"
                    :label="item.name"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="7">
              <el-form-item label="">
                <el-select
                  v-model="form.userId"
                  placeholder=""
                  clearable
                  class="item-css"
                  @change="tableApi"
                >
                  <el-option
                    v-for="(item, index) in followUserList"
                    :key="index"
                    :label="item.name"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>

            <el-col :span="7">
              <el-form-item label="">
                <el-input
                  v-model="form.name"
                  @keydown.enter.native="tableApi"
                  placeholder=""
                  clearable
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="2">
              <el-button
                style="margin-left:20px;margin-top:7px;"
                type="primary"
                @click="tableApi()"
              ></el-button>
            </el-col>
          </el-row>
        </div>
        <p class="show-more-button">
          <el-button
            @click="showMore=!showMore"
            plain
            :icon="showMore?'el-icon-caret-top':'el-icon-caret-bottom'"
            type="primary"
            circle
            size="mini"
          ></el-button>
        </p>
      </el-row>
    </el-form>

    <el-row style="margin-top:20px;">
      <!--  -->
      <el-popover placement="top-start" width="300" trigger="click">
        <el-checkbox-group v-model="showCol" @change="colChange">
          <el-checkbox
            style="width:120px;line-height:25px"
            v-for="(item,index) in colOptions"
            :key="index"
            :label="item"
          ></el-checkbox>
        </el-checkbox-group>
        <el-button
          slot="reference"
          style="margin-right:10px;"
          type="primary"
          icon="el-icon-s-operation"
          plain
        ></el-button>
      </el-popover>
      <el-button
        type="primary"
        icon="el-icon-folder-add"
        v-permi="'/contract/add'"
        @click="isShowContract=true"
      ></el-button>
      <el-button
        type="danger"
        icon="el-icon-delete"
        :disabled="!checkboxId.length"
        @click="deleteContract"
      ></el-button>
      <el-button type="success" icon="el-icon-message" @click="exportTable" class="item"></el-button>
    </el-row>
    <el-table
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      border
      :fit="true"
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" fixed></el-table-column>
      <el-table-column
        prop="customer.companyName"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column prop="name" v-if="showCol.indexOf('')>-1" label="" align="center"></el-table-column>
      <el-table-column
        prop="signDate"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="payStatusStr"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column prop="user.name" v-if="showCol.indexOf('')>-1" label="" align="center"></el-table-column>
      <el-table-column
        prop="totalPrice"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="paymentTotal"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column v-if="showCol.indexOf('')>-1" label="" align="center">
        <template
          slot-scope="scope"
        >{{scope.row.clueCreateTime&&scope.row.clueCreateTime.slice(0,11)}}</template>
      </el-table-column>
      <el-table-column
        prop="businessType.name"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column label="" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            plain
            permi="'/contract/updateContract'"
            @click="handleEdit(scope.$index, scope.row)"
          ></el-button>
          <el-button size="mini" @click="handleView(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      class="itemPage"
      @size-change="sizeChangePage"
      @current-change="currentChangePage"
      :current-page="page"
      :page-sizes="[10, 20, 30, 40]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalSize"
    ></el-pagination>
    <new-contract
      :isShowContract="isShowContract"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-contract>
    <edit-contract
      :isShowContractEdit="isShowContractEdit"
      :contractStatusList="contractStatusList"
      :contractId="contractId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-contract>
  </div>
</template>

<script>
// 
import API from "@/api/contract";
import NewContract from "@/components/element/NewContract"; // 
import EditContract from "@/components/element/EditContract"; // 
export default {
  name: "contract",
  components: {
    NewContract,
    EditContract
  },
  data() {
    return {
      contractStatusList: [], // 
      active: 0,
      showMore: false, // 
      statusList: [], // 
      active2: 0,
      timeCodeList: [], // 
      timeActive: 0,
      departmentsList: [], // 
      followUserList: [], // 
      isShowContract: false,
      isShowContractEdit: false,
      contractId: "", // id
      form: {
        createTime: [],
        departId: "",
        userId: "",
        name: ""
      },
      showCol: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      colOptions: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      tableData: [],
      showDefinedTime: false, // 
      page: 1, //
      rows: 10, //
      totalSize: 0, //
      status: null, // 
      paymentStatus: null, // 
      timeCode: null, // 
      checkboxId: [] // 
    };
  },
  methods: {
    exportTable() {
      let form = this.form;
      let startSignDate, endSignDate;
      if (form.createTime) {
        startSignDate = form.createTime[0];
        endSignDate = form.createTime[1];
      }
      let params = {
        pageNum: this.page,
        pageSize: this.rows,
        status: this.status,
        paymentStatus: this.paymentStatus,
        timeCode: this.timeCode,
        startSignDate,
        endSignDate,
        departId: form.departId,
        userId: form.userId,
        name: form.name
      };
      API.exportContractExc(params)
        .then(res => {
          console.log(res);

          var blob = new Blob([res], {
            type: "application/vnd.ms-excel;charset=utf-8"
          }); //vnd.ms-excel
          var downloadElement = document.createElement("a");
          var href = window.URL.createObjectURL(blob); //
          downloadElement.href = href;
          document.body.appendChild(downloadElement);
          downloadElement.setAttribute("download", ".xls");
          downloadElement.click(); //
          document.body.removeChild(downloadElement); //
          window.URL.revokeObjectURL(href); //blob
        })
        .catch(err => {
          console.error(err);
        });
    },
    colChange() {
      localStorage.setItem("contractTable", JSON.stringify(this.showCol));
    },
    // tableapi
    tableApi() {
      let form = this.form;
      let startSignDate, endSignDate;
      if (form.createTime) {
        startSignDate = form.createTime[0];
        endSignDate = form.createTime[1];
      }
      let params = {
        pageNum: this.page,
        pageSize: this.rows,
        status: this.status,
        paymentStatus: this.paymentStatus,
        timeCode: this.timeCode,
        startSignDate,
        endSignDate,
        departId: form.departId,
        userId: form.userId,
        name: form.name
      };
      API.postList(params).then(res => {
        console.log(res.data);
        let data = res.data;
        this.totalSize = data.total; //
        for (const item of data.list) {
          item.signDate = item.signDate.slice(0, 10);
        }
        this.tableData = data.list;
      });
    },
    //
    sizeChangePage(val) {
      this.rows = val;
      this.tableApi(this.rows);
    },
    //
    currentChangePage(val) {
      this.page = val;
      this.tableApi(this.page);
    },
    // 
    showStyle(index, code) {
      this.active = index; //
      this.status = code;
      this.tableApi(this.status);
    },
    // 
    showStyle2(index, code) {
      this.active2 = index;
      this.paymentStatus = code;
      this.tableApi(this.paymentStatus);
    },
    // 
    timeChange(index, code) {
      this.showDefinedTime = false;
      this.form.createTime = [];
      this.timeActive = index;
      this.timeCode = code;
      this.tableApi(this.timeCode);
    },
    // 
    timeCustom(val) {
      this.timeCode = null;
      this.tableApi();
    },
    // 
    depart(val) {
      this.form.userId = "";
      this.tableApi();
      if (val) {
        API.getHead(val).then(res => {
          // res.data.forEach(item => {
          //   item.value = item.name;
          // });
          this.followUserList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          // res.data.forEach(item => {
          //   item.value = item.name;
          // });
          this.followUserList = res.data;
        });
      }
    },
    // querySearch(queryString, cb) {
    //   var restaurants = this.followUserList;
    //   var results = queryString
    //     ? restaurants.filter(this.createFilter(queryString))
    //     : restaurants;
    //   //  callback 
    //   cb(results);
    // },
    // createFilter(queryString) {
    //   return restaurant => {
    //     return (
    //       restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
    //       0
    //     );
    //   };
    // },
    // id
    handleSelect(item) {
      this.userId = item.id;
      this.tableApi(this.userId);
      this.userId = null;
    },
    // table
    handleSelectionChange(val) {
      let arr = [];
      val.map(ele => {
        arr.push(ele.id);
      });
      this.checkboxId = arr;
    },
    // 
    deleteContract() {
      this.$confirm("", "", {
        distinguishCancelAndClose: true,
        confirmButtonText: "",
        cancelButtonText: ""
      }).then(() => {
        let params = {
          ids: this.checkboxId.join(",")
        };
        API.postdeleteContract(params)
          .then(res => {
            this.$message({
              message: "",
              type: "success"
            });
            this.tableApi();
          })
          .catch(err => {
            if (err.indexOf("") === -1) this.$message.warning(err);
          });
      });
    },
    // Dialog
    getSonSure() {
      this.isShowContract = false;
      this.isShowContractEdit = false;
      this.tableApi();
    },
    // Dialog
    getSonCancel(val) {
      this.isShowContract = val;
      this.isShowContractEdit = val;
    },
    // 
    handleEdit(index, row) {
      this.contractId = row.id;
      this.isShowContractEdit = true;
      // console.log(index, row)
    },
    // 
    handleView(index, row) {
      this.$router.push({
        path: "contractdetails",
        query: { id: row.id, paymentTotal: row.paymentTotal }
      });
    }
  },
  activated() {
    // 
    this.tableApi();
  },
  mounted() {
    let showCol = JSON.parse(localStorage.getItem("contractTable"));
    if (showCol) this.showCol = showCol;
    // 
    API.getContractStatus().then(res => {
      let obj = { code: 0, val: "" };
      res.data.unshift(obj);
      this.contractStatusList = res.data;
    });
    // 
    API.getPaymentStatus().then(res => {
      let obj = { code: 0, val: "" };
      res.data.unshift(obj);
      this.statusList = res.data;
    });
    // 
    API.getCustomerTimeCode().then(res => {
      // let obj = { code: 0, name: "" };
      // res.data.unshift(obj);
      res.data.splice(2, 1);
      res.data.splice(3, 1);
      res.data.splice(4, 1);
      this.timeCodeList = res.data;
    });
    // api
    API.getDepartments().then(res => {
      this.departmentsList = res.data;
    });
    // 
    API.getAllUser().then(res => {
      // res.data.forEach(item => {
      //   item.value = item.name;
      // });
      this.followUserList = res.data;
    });
  }
};
</script>
<style lang='scss' scoped>
ul {
  list-style-type: none;
  padding: 0;
}
.item-css {
  width: 100%;
}

.show-more-button {
  margin-bottom: 20px;
  text-align: center;
}

.el-form {
  .el-form-item {
    margin: 8px 0;
  }
}

.slide {
  transition: all 0.5s;
  overflow: hidden;
}

.list {
  li {
    display: inline-block;
    padding: 0 20px;
    cursor: pointer;
  }
}

.addclass {
  background: rgb(255, 242, 233);
  color: rgb(255, 178, 121);
}
.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}
</style>