<template>
  <div>
    <el-form :model="form" label-width="120px">
      <el-row>
        <el-col :span="24" class="list">
          <el-form-item label="">
            <ul>
              <li
                v-for="(item, index) in stageList"
                :key="index"
                @click="stageChange(index, item.code)"
                :class="stageActive == index ? 'addclass' : '' "
              >{{item.val}}</li>
            </ul>
          </el-form-item>
        </el-col>
        <el-col :span="24" class="list">
          <el-form-item label="">
            <ul>
              <li
                v-for="(item, index) in timeCodeList"
                :key="index"
                @click="dateChange(index, item.code)"
                :class="dateActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
              <!-- <li
                @click="showDefinedTime = true;dateActive = -1"
                class="list-li"
                v-if="!showDefinedTime"
              ></li>
              <li v-else>
                <el-date-picker
                  v-model="form.createTime"
                  @change="timeCustom"
                  type="daterange"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  range-separator=""
                  start-placeholder=""
                  end-placeholder=""
                ></el-date-picker>
              </li>-->
            </ul>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-col :span="7">
            <el-form-item label="">
              <el-select
                v-model="form.followDepart"
                placeholder=""
                clearable
                class="item-css"
                @change="depart($event)"
              >
                <el-option
                  v-for="(item, index) in departmentsList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="7">
            <el-form-item label="">
              <el-select
                v-model="form.followUser"
                placeholder=""
                clearable
                :no-data-text="form.followDepart?'':''"
                class="item-css"
                @change="tableApi()"
              >
                <el-option
                  v-for="(item, index) in headList"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>
          </el-col>

          <el-col :span="8">
            <el-form-item label="">
              <el-input v-model="channame" class="input-with-select">
                <el-select v-model="valid" slot="prepend" placeholder="" style="width:110px;">
                  <el-option label="" value></el-option>
                  <el-option label="" value="1"></el-option>
                  <el-option label="" value="2"></el-option>
                </el-select>
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="2">
            <el-button style="margin-left:20px;" type="primary" @click="onSubmit"></el-button>
          </el-col>
        </el-col>
        <el-col :span="24"></el-col>
      </el-row>
    </el-form>
    <!-- <el-row>
      <el-col :span="24" style="font-size:20px;text-align:center;">
        <i
          style="cursor: pointer;"
          :class="flag == true? 'el-icon-arrow-up' : 'el-icon-arrow-down'"
          @click="show"
        ></i>
      </el-col>
    </el-row>-->

    <el-row style="margin-top:20px;">
      <!--  -->
      <el-popover placement="top-start" width="300" trigger="click">
        <el-checkbox-group v-model="showCol" @change="colChange">
          <el-checkbox
            style="width:120px;line-height:25px"
            v-for="(item,index) in colOptions"
            :key="index"
            :label="item"
          ></el-checkbox>
        </el-checkbox-group>
        <el-button
          slot="reference"
          style="margin-right:10px;"
          type="primary"
          icon="el-icon-s-operation"
          plain
        ></el-button>
      </el-popover>

      <el-button
        type="primary"
        icon="el-icon-folder-add"
        v-permi="'/customer/addChance'"
        @click="isShowChance=true"
      ></el-button>
      <el-button
        type="danger"
        icon="el-icon-delete"
        :disabled="!checkboxId.length"
        @click="deleteChance"
      ></el-button>
      <el-button type="success" icon="el-icon-message" @click="showSend"></el-button>
    </el-row>
    <el-table
      :data="tableData"
      tooltip-effect="dark"
      style="width: 100%;margin-top: 20px;"
      border
      @selection-change="handleSelectionChange"
    >
      <el-table-column type="selection" width="55" align="center" fixed></el-table-column>
      <el-table-column prop="name" v-if="showCol.indexOf('')>-1" label="" align="center"></el-table-column>
      <el-table-column
        prop="companyName"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="expectPrice"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="expectDate"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="stageStr"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <!-- <el-table-column prop="probability" v-if="showCol.indexOf('')>-1" label="" align="center"></el-table-column> -->
      <el-table-column
        prop="followContents"
        show-overflow-tooltip
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="followCreateTime"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column
        prop="nextTime"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
      ></el-table-column>
      <el-table-column label="" width="150" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            plain
            v-permi="'/chance/updateChance'"
            @click="handleEdit(scope.$index, scope.row)"
          ></el-button>
          <el-button size="mini" @click="handleView(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      class="itemPage"
      @size-change="sizeChangePage"
      @current-change="currentChangePage"
      :current-page="page"
      :page-sizes="[10, 20, 30, 40]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="totalSize"
    ></el-pagination>
    <new-chance
      :isShowChance="isShowChance"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-chance>
    <new-send
      :isShowSend="isShowSend"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-send>
    <edit-chance
      :isShowChanceEdit="isShowChanceEdit"
      :opportunityId="opportunityId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-chance>
  </div>
</template>

<script>
// 
import API from "@/api/chance";
import NewChance from "@/components/element/NewChance"; // 
import NewSend from "@/components/element/NewSend"; // 
import EditChance from "@/components/element/EditChance"; // 
import { constants } from "crypto";
export default {
  name: "opportunity",
  components: {
    NewChance,
    NewSend,
    EditChance
  },
  data() {
    return {
      stageList: [], // 
      stageActive: 0,
      dateActive: 0,
      timeCodeList: [], // 
      isShowChance: false,
      isShowSend: false,
      showDefinedTime: false, // 
      isShowChanceEdit: false,
      opportunityId: "", // id
      departmentsList: [], // 
      headList: [], // 
      channame: "",
      names: "",
      tableData: [],
      valid: "",
      form: {
        followDepart: "",
        followUser: "",
        createTime: []
      },
      page: 1, //
      rows: 10, //
      totalSize: 0, //
      stage: null, // code
      timeCode: null, // code
      followUser: null, // 
      companyName: null, // 
      name: null, // 
      showCol: [
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      colOptions: [
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      checkboxId: "" // 
    };
  },
  methods: {
    colChange() {
      localStorage.setItem("opportunityTable", JSON.stringify(this.showCol));
    },
    // tableapi
    tableApi() {
      let form = this.form;
      let data = {
        pageNum: this.page,
        pageSize: this.rows,
        stage: this.stage,
        timeCode: this.timeCode,
        startTime: form.createTime[0],
        endTime: form.createTime[1],
        followDepart: form.followDepart,
        followUser: form.followUser,
        companyName: form.companyName,
        name: form.name
      };
      API.postList(data).then(res => {
        // console.log(res.data)
        this.totalSize = res.data.total; //
        this.tableData = res.data.list;
      });
    },
    //
    sizeChangePage(val) {
      this.rows = val;
      this.tableApi(this.rows);
    },
    //
    currentChangePage(val) {
      this.page = val;
      this.tableApi(this.page);
    },
    // 
    stageChange(index, code) {
      this.stageActive = index; //
      this.stage = code;
      this.tableApi(this.stage);
    },
    // 
    dateChange(index, code) {
      this.dateActive = index;
      this.showDefinedTime = false;

      this.form.createTime = [];
      this.timeCode = code;
      this.tableApi(this.timeCode);
    },
    // 
    depart(val) {
      this.form.followDepart = val;
      this.form.followUser = "";
      this.tableApi();
      if (val) {
        API.getHead(val).then(res => {
          // res.data.forEach(item => {
          //   item.value = item.name;
          // });
          this.headList = res.data;
        });
      } else {
        API.getAllUser().then(res => {
          // res.data.forEach(item => {
          //   item.value = item.name;
          // });
          this.headList = res.data;
        });
      }
    },
    timeCustom() {
      this.dateActive = null;
      this.form.timeCode = null;
      this.tableApi();
    },
    querySearch(queryString, cb) {
      var restaurants = this.headList;
      var results = queryString
        ? restaurants.filter(this.createFilter(queryString))
        : restaurants;
      //  callback 
      cb(results);
    },
    createFilter(queryString) {
      return restaurant => {
        return (
          restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) ===
          0
        );
      };
    },
    // // id
    // handleSelect(item) {
    //   this.followUser = item.id;
    //   this.tableApi(this.followUser);
    //   if (this.followUser != null) {
    //     API.getDepartByUserId(item.id).then(res => {
    //       this.departmentsList = res.data;
    //     });
    //   } else {
    //     API.getDepartments().then(res => {
    //       this.departmentsList = res.data;
    //     });
    //   }
    // },
    // handleBlurStr(val) {
    //   if (this.form.followUser == "") {
    //     API.getDepartments().then(res => {
    //       this.departmentsList = res.data;
    //     });
    //   }
    // },
    // 
    onSubmit() {
      this.form.companyName = this.form.name = "";
      if (this.valid == "1") {
        this.form.companyName = this.channame;
        this.tableApi();
      } else {
        this.form.name = this.channame;
        this.tableApi();
      }
    },

    // table
    handleSelectionChange(val) {
      let arr = [];
      val.map(ele => {
        arr.push(ele.id);
      });
      this.checkboxId = arr;
    },
    // 
    deleteChance() {
      this.$confirm("", "", {
        distinguishCancelAndClose: true,
        confirmButtonText: "",
        cancelButtonText: ""
      }).then(() => {
        let Params = { ids: this.checkboxId.join(",") };
        API.postdeleteChance(Params).then(res => {
          this.$message({
            message: "",
            type: "success"
          });
        });
        this.tableApi();
      });
    },
    // 
    showSend() {
      this.isShowSend = true;
    },
    // Dialog
    getSonSure() {
      this.isShowSend = false;
      this.isShowChance = false;
      this.isShowChanceEdit = false;
      this.tableApi();
    },
    // Dialog
    getSonCancel(val) {
      this.isShowSend = val;
      this.isShowChance = val;
      this.isShowChanceEdit = val;
    },
    // 
    handleEdit(index, row) {
      this.opportunityId = row.id;
      this.isShowChanceEdit = true;
    },
    // 
    handleView(index, row) {
      this.$router.push({ path: "opportunitydetails", query: { id: row.id } });
      // console.log(index, row)
    }
  },
  mounted() {
    let showCol = JSON.parse(localStorage.getItem("opportunityTable"));
    if (showCol) this.showCol = showCol;
    // 
    this.tableApi();
    // 
    API.getSaleStage().then(res => {
      let obj = { code: 0, val: "" };
      res.data.unshift(obj);
      this.stageList = res.data;
    });
    // 
    API.getCustomerTimeCode().then(res => {
      // let obj = { code: 0, name: "" };
      // res.data.unshift(obj);
      // res.data.splice(2, 1);
      // res.data.splice(3, 1);
      // res.data.splice(4, 1);
      this.timeCodeList = res.data;
    });
    // api
    API.getDepartments().then(res => {
      this.departmentsList = res.data;
    });
    // 
    API.getAllUser().then(res => {
      // res.data.forEach(item => {
      //   item.value = item.name;
      // });
      // this.headList = res.data;
    });
  }
};
</script>
<style lang='scss' scoped>
ul {
  list-style-type: none;
  padding: 0;
}
.list {
  li {
    display: inline-block;
    padding: 0 20px;
    cursor: pointer;
  }
}
.item-css {
  width: 100%;
}
.addclass {
  background: rgb(255, 242, 233);
  color: rgb(255, 178, 121);
}
.itemPage {
  margin-top: 30px;
  margin-right: 10px;
  float: right;
}
</style>