<template>
  <div>
    <el-row>
      <el-form label-width="100px">
        <el-col :span="24">
          <el-form-item label="">
            <ul>
              <li
                v-for="(item, index) in complaintType"
                :key="index"
                @click="typeChange(index, item.code)"
                :class="typeActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
            </ul>
          </el-form-item>
        </el-col>
        <el-col :span="24">
          <el-form-item label=" ">
            <ul>
              <li
                v-for="(item, index) in disposeStatus"
                :key="index"
                @click="statusChange(index, item.code)"
                :class="statusActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
            </ul>
          </el-form-item>
        </el-col>
        <el-col :span="24" class="list">
          <el-form-item label="">
            <ul>
              <li
                v-for="(item, index) in creationTime"
                :key="index"
                @click="changeCreateTime(index, item.code)"
                :class="timeActive == index ? 'addclass' : '' "
              >{{item.name}}</li>
              <li
                @click="showDefinedDate = true;timeActive = -1"
                class="list-li"
                v-if="!showDefinedDate"
              ></li>
              <li v-else>
                <el-date-picker
                  v-model="chooseData.createTime"
                  @change="timeCustom"
                  type="daterange"
                  value-format="yyyy-MM-dd HH:mm:ss"
                  range-separator=""
                  start-placeholder=""
                  end-placeholder=""
                ></el-date-picker>
              </li>
            </ul>
          </el-form-item>
        </el-col>
      </el-form>
    </el-row>
    <el-row>
      <!--  -->
      <el-popover placement="top-start" width="300" trigger="click">
        <el-checkbox-group v-model="showCol" @change="colChange">
          <el-checkbox
            style="width:120px;line-height:25px"
            v-for="(item,index) in colOptions"
            :key="index"
            :label="item"
          ></el-checkbox>
        </el-checkbox-group>
        <el-button
          slot="reference"
          style="margin-right:10px;"
          type="primary"
          icon="el-icon-s-operation"
          plain
        ></el-button>
      </el-popover>
      <el-button
        type="primary"
        @click="isAddComplain=true"
        v-permi="'/aftersales/addAdvise'"
        icon="el-icon-document-add"
      ></el-button>
      <el-button
        type="warning"
        @click="handleStatus(0)"
        :disabled="!checkboxId.length"
        icon="el-icon-close"
      ></el-button>
      <el-button
        type="success"
        @click="handleStatus(1)"
        :disabled="!checkboxId.length"
        icon="el-icon-check"
      ></el-button>
      <el-button
        type="danger"
        @click="delComplain"
        :disabled="!checkboxId.length"
        icon="el-icon-delete"
      ></el-button>
    </el-row>

    <el-table
      :data="tableData"
      tooltip-effect="dark"
      @selection-change="handleSelectionChange"
      style="width: 100%;margin-top: 20px;"
      border
      stripe
    >
      <el-table-column type="selection" align="center" fixed></el-table-column>
      <el-table-column prop="companyName" v-if="showCol.indexOf('')>-1" label="" align="center"></el-table-column>
      <!-- <el-table-column prop="name" v-if="showCol.indexOf('')>-1" label="" align="center"></el-table-column> -->
      <el-table-column prop="linkName" v-if="showCol.indexOf('')>-1" label="" align="center"></el-table-column>
      <el-table-column prop="linkTel" v-if="showCol.indexOf('')>-1" label="" align="center"></el-table-column>
      <el-table-column
        prop="text"
        v-if="showCol.indexOf('')>-1"
        label=""
        width="250"
        align="center"
      ></el-table-column>
      <el-table-column
        prop="followDepartName"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
        header-align="center"
      ></el-table-column>
      <el-table-column
        prop="followUserName"
        v-if="showCol.indexOf('')>-1"
        label=""
        align="center"
        header-align="center"
      ></el-table-column>
      <el-table-column v-if="showCol.indexOf('')>-1" label="" align="center">
        <template slot-scope="scope">
          <span
            :style="{color:scope.row.statusName===''?'#67C23A':'#f78989'}"
          >{{scope.row.statusName}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="typeName" v-if="showCol.indexOf('')>-1" label="" align="center"></el-table-column>
      <el-table-column
        prop="createTime"
        v-if="showCol.indexOf('')>-1"
        label=""
        width="200"
        align="center"
      ></el-table-column>
      <el-table-column label="" width="200" align="center" fixed="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            :disabled="scope.row.statusName===''"
            :type="scope.row.statusName===''?'':'primary'"
            plain
            v-permi="'/aftersales/addAdviseFollow'"
            @click="isAddHandleReault=true;complainId=scope.row.id"
          ></el-button>
          <el-button size="mini" @click="isExamineResult=true;complainId=scope.row.id"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      class="itemPage"
      style="text-align:right;margin-top: 20px;"
      @size-change="sizeChangePage"
      @current-change="currentChangePage"
      :current-page="pageObj.pageNum"
      :page-sizes="[10, 20, 30, 40]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pageObj.total"
    ></el-pagination>
    <!--  -->
    <add-complain
      :isAddComplain="isAddComplain"
      @closeDialogFather="isAddComplain=false"
      @sureDialogFather="getTableData();isAddComplain=false"
    ></add-complain>
    <!--  -->
    <add-handle-result
      :isAddHandleReault="isAddHandleReault"
      :complainId="complainId"
      @closeDialogFather="isAddHandleReault=false"
      @sureDialogFather="getTableData();isAddHandleReault=false"
    ></add-handle-result>
    <!--  -->
    <examine-result
      :isExamineResult="isExamineResult"
      :complainId="complainId"
      @closeDialogFather="isExamineResult=false"
    ></examine-result>
  </div>
</template>

<script>
import API from "@/api/after";
import AddComplain from "../components/AddComplain";
import AddHandleResult from "../components/AddHandleResult";
import ExamineResult from "../components/ExamineResult";
// 
export default {
  name: "complaintAdvice",
  components: {
    AddComplain,
    AddHandleResult,
    ExamineResult
  },
  data() {
    return {
      showCol: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      colOptions: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      isAddHandleReault: false, // 
      isExamineResult: false, // 
      complainId: null, // id
      complaintType: [
        { name: "", code: null },
        { name: "", code: 0 },
        { name: "", code: 1 }
      ],
      disposeStatus: [
        { name: "", code: null },
        { name: "", code: 1 },
        { name: "", code: 0 }
      ],
      creationTime: [],
      timeActive: 0,
      statusActive: 0,
      typeActive: 0,
      chooseData: {
        typeCode: null,
        statusCode: null,
        timeCode: null,
        createTime: []
      },
      pageObj: {
        pageNum: 1,
        pageSize: 10,
        total: null
      },
      checkboxId: [],
      isAddComplain: false,
      showDefinedDate: false,
      tableData: []
    };
  },
  methods: {
    colChange() {
      localStorage.setItem("complaintTable", JSON.stringify(this.showCol));
    },
    delComplain() {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          let params = {
            ids: this.checkboxId.join(",")
          };
          API.deleteAdvise(params).then(res => {
            this.$message.success("");
            this.getTableData();
          });
        })
        .catch(() => {
          this.$message({
            message: "",
            type: "info"
          });
        });
    },
    handleStatus(status) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          let params = {
            ids: this.checkboxId.join(","),
            status
          };
          API.updateAdviseStatus(params).then(res => {
            this.$message.success("");
            this.getTableData();
          });
        })
        .catch(() => {
          this.$message({
            message: "",
            type: "info"
          });
        });
    },
    // table
    handleSelectionChange(val) {
      let arr = [];
      val.map(ele => {
        arr.push(ele.id);
      });
      this.checkboxId = arr;
    },
    // 
    timeCustom(val) {
      if (val != null) {
        this.chooseData.codeTime = null;
        this.getTableData();
      }
    },
    sizeChangePage(e) {
      this.pageObj.pageSize = e;
      this.getTableData();
    },
    currentChangePage(e) {
      this.pageObj.pageNum = e;
      this.getTableData();
    },
    //  
    statusChange(index, code) {
      this.chooseData.statusCode = code;
      this.statusActive = index;
      this.getTableData();
    },
    typeChange(index, code) {
      this.chooseData.typeCode = code;
      this.typeActive = index;
      this.getTableData();
    },
    // 
    changeCreateTime(index, code) {
      this.chooseData.createTime = [];
      this.showDefinedDate = false;
      this.timeActive = index; //
      this.chooseData.codeTime = code;
      this.getTableData();
    },
    getTableData() {
      let pageObj = this.pageObj;
      let chooseData = this.chooseData;
      let params = {
        pageSize: pageObj.pageSize,
        pageNum: pageObj.pageNum,
        type: chooseData.typeCode,
        status: chooseData.statusCode,
        startTime: chooseData.createTime[0],
        endTime: chooseData.createTime[1],
        timeCode: chooseData.codeTime
      };
      API.getAdviseList(params).then(res => {
        let data = res.data;
        this.pageObj.total = data.total;
        this.tableData = data.list;
      });
    }
  },
  mounted() {
    let showCol = JSON.parse(localStorage.getItem("complaintTable"));
    if (showCol) this.showCol = showCol;
    // api
    API.getCreationTime().then(res => {
      let data = res.data;
      data.splice(7);
      this.creationTime = res.data;
    });
    this.getTableData();
  }
};
</script>
<style lang='scss' scoped>
ul {
  list-style-type: none;
  padding: 0;
}
.el-form-item li {
  display: inline-block;
  padding: 0 20px;
  cursor: pointer;
}

.addclass {
  background: rgb(255, 242, 233);
  color: rgb(255, 178, 121);
}
</style>