// 

import fetch from '@/utils/fetch';
import Qs from 'qs'

export default {
    // get 
    // 
    getSaleStage(data) {
        return fetch({
            url: `chance/findSaleStage`,
            method: 'get',
            params: data
        })
    },
    // 
    getCustomerTimeCode(data) {
        return fetch({
            url: `customer/findCustomerTimeCode`,
            method: 'get',
            params: data
        })
    },
    // api
    getDepartments(data) {
        return fetch({
            url: `customer/findAllDepart`,
            method: 'get',
            params: data
        })
    },
    // id
    getCustomerLinkman(data) {
        return fetch({
            url: `customer/findCustomerLinkman/${data}`,
            method: 'get'
        })
    },

    // id
    getCustomerFileById(data) {
        return fetch({
            url: `customer/findCustomerFileById/${data}`,
            method: 'get'
        })
    },

    //
    getFollowList(data) {
        return fetch({
            url: `/follow/getFollowList`,
            method: 'get',
            params: data
        })
    },
    // 
    getAllUser(data) {
        return fetch({
            url: `customer/findAllUser`,
            method: 'get',
            params: data
        })
    },

    // 
    customerFrom(data) {
        return fetch({
            url: `clue/customerFrom`,
            method: 'get',
            params: data
        })
    },

    // id
    getHead(data) {
        return fetch({
            url: `customer/findAllDepartUser/${data}`,
            method: 'get'
        })
    },
    // id
    getDepartByUserId(data) {
        return fetch({
            url: `customer/findDepartByUserId/${data}`,
            method: 'get'
        })
    },
    // id
    getChanceDetails(data) {
        return fetch({
            url: `chance/findChanceDetails/${data}`,
            method: 'get'
        })
    },
    //  
    getCustomerByCompanyName(data) {
        return fetch({
            url: `customer/findCustomerByCompanyName`,
            method: 'get',
            params: data
        })
    },
    // 
    getAllLinkmen(data) {
        return fetch({
            url: `chance/findAllLinkmen`,
            method: 'get',
            params: data
        })
    },
    // 
    getType(data) {
        return fetch({
            url: `customer/service/type`,
            method: 'get',
            params: data
        })
    },
    // 
    getChanceType(data) {
        return fetch({
            url: `customer/findChanceType`,
            method: 'get',
            params: data
        })
    },
    // 
    getchannel(data) {
        return fetch({
            url: `customer/channel`,
            method: 'get',
            params: data
        })
    },
    // id
    findChanceById(data) {
        return fetch({
            url: `chance/findChanceById/${data}`,
            method: 'get'
        })
    },
    // post 
    // api
    postList(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `chance/findChance?${data1}`,
            method: 'post'
        })
    },
    // id
    postdeleteChance(data) {
        let data1 = Qs.stringify(data)
        return fetch({
            url: `chance/deleteChance?${data1}`,
            method: 'post'
        })
    },
    // 
    postaddChance(data) {
        return fetch({
            url: `customer/addChance`,
            method: 'post',
            params: data
        })
    },
    // 
    postupdateChance(data) {
        return fetch({
            url: `chance/updateChance`,
            method: 'post',
            params: data
        })
    },
}