<template>
  <!--  -->
  <div>
    <el-row>
      <el-col :span="24">
        <h1>{{detailData.companyName}}</h1>
      </el-col>
    </el-row>
    <el-row class="el-dro">
      <el-button type="primary" v-permi="'/payment/updateContractPaymentPlan'" @click="isEditReceivablerecord = true"></el-button>
      <el-button type="danger" @click="deleteRecord"></el-button>
    </el-row>

    <el-tabs type="card" @tab-click="handleClick">
      <el-tab-pane label="">
        <el-card class="box-card">
          <div class="text item">
            <div class="text item"></div>
            <div class="text item">{{detailData.opeDate}}</div>
            <div class="text item">{{detailData.price}}</div>
            <div class="text item">{{detailData.companyName}}</div>
            <div class="text item">{{detailData.contractName}}</div>
            <!-- <div class="text item">{{detailData.notPrice}}</div> -->
            <div class="text item">{{detailData.isOver==1?"":""}}</div>
            <el-divider></el-divider>
            <div class="text item"></div>
            <div class="text item">{{detailData.payNo}}</div>
            <div class="text item">{{detailData.payType}}</div>
            <!-- <div class="text item">{{detailData.returnStatus}}</div> -->
            <div class="text item">{{detailData.days}}</div>
            <div class="text item">{{detailData.userName}}</div>
            <div class="text item">{{detailData.remarks}}</div>
            <el-divider></el-divider>
            <div class="text item"></div>
            <div class="text item">{{detailData.createTime}}</div>
            <div class="text item">{{detailData.updateTime}}</div>
            <div class="text item">{{detailData.createUserName}}</div>
          </div>
        </el-card>
        <follow-up-history
          class="genjin"
          ref="followUpHistory"
          @writeFollow="isShowFollow=true"
          :customerId="customerId"
          :type="1"
        ></follow-up-history>
      </el-tab-pane>
      <el-tab-pane label="">
        <tab-task ref="tabTask" :customerId="customerId"></tab-task>
      </el-tab-pane>
      <el-tab-pane label="">
        <tab-annex ref="tabAnnex" :customerId="customerId"></tab-annex>
      </el-tab-pane>
    </el-tabs>
    <edit-receivablerecord
      :isEditReceivablerecord="isEditReceivablerecord"
      :editRecordData="detailData"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-receivablerecord>

    <Followup
      :isShowFollow="isShowFollow"
      :customerId="customerId"
      @sureDialogFather="$refs.followUpHistory.getFollowData()"
      @closeDialogFather="getSonCancel"
    ></Followup>
  </div>
</template>

<script>
// 
import API from "@/api/payment";
import EditReceivablerecord from "../components/EditReceivablerecord"; // 
import FollowUpHistory from "@/components/element/FollowUpHistory"; // 
import Followup from "@/components/element/Followup"; // 
import TabAnnex from "@/components/element/TabAnnex";
import TabTask from "@/components/element/TabTask";

export default {
  name: "receivablePlandetails",
  components: {
    EditReceivablerecord,
    FollowUpHistory,
    Followup,
    TabTask,
    TabAnnex
  },
  data() {
    return {
      isEditReceivablerecord: false,
      detailData: {},
      isShowFollow: false, // 
      customerId: null,
      file: [], // 
      isShowUpload: false // 
    };
  },
  methods: {
    // 
    deleteRecord() {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          API.postdeletePaymentPlans({ ids: this.paymentId }).then(res => {
            if (res.status == 200) {
              this.$message({
                type: "success",
                message: "!"
              });
              this.$router.go(-1);
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "error",
            message: ""
          });
        });
    },
    // Dialog
    getSonSure() {
      this.getSonCancel();
      this.getDetailData();
    },
    // Dialog 
    getSonCancel(val) {
      this.isEditReceivablerecord = false;
      this.UploadAnnex = false;
      this.isShowFollow = false;
    },
    // tab 
    handleClick(tab, event) {
      if (tab.label == "") {
        this.getDetailData();
      } else if (tab.label == "") {
        this.$refs.tabTask.getTaskTable();
      } else if (tab.label == "") {
        this.$refs.tabAnnex.getAnnexTable();
      }
    },

    getDetailData() {
      API.getPaymentById(this.paymentId).then(res => {
        console.log(res.data);
        let data = res.data;
        this.contractId = data.contractId;
        this.customerId = data.customerId;
        switch (Number(data.returnStatus)) {
          case 1:
            data.returnStatus = "";
            break;
          case 2:
            data.returnStatus = "";
            break;
          case 3:
            data.returnStatus = "";
            break;
          default:
            data.returnStatus = "";
            break;
        }
        this.detailData = res.data;
      });
    }
  },
  mounted() {
    this.paymentId = this.$route.query.id;
    this.getDetailData();
  }
};
</script>
<style lang='scss' scoped>
.el-icon-arrow-down {
  font-size: 12px;
}
.el-dro {
  margin-top: 20px;
  margin-bottom: 20px;
}

.text {
  font-size: 14px;
}
.item {
  margin-bottom: 18px;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.genjin {
  position: absolute;
  top: 0;
  right: 0;

  width: 48%;
}
.box-card {
  width: 48%;
}
</style>