<template>
  <div>
    <el-row>
      <el-col :span="24">
        <el-col :span="6">
          <h1>{{message.name}}</h1>
        </el-col>
        <el-col :span="8">
          <h3>{{message.companyName}}</h3>
        </el-col>
      </el-col>
    </el-row>
    <el-row class="el-dro">
      <el-button type="primary" v-permi="'/chance/updateChance'" @click="isShowChanceEdit=true"></el-button>
      <el-button type="danger" @click="deleteChance"></el-button>
      <el-button type="warning" @click="showSend"></el-button>
    </el-row>

    <el-tabs type="card" @tab-click="handleClick">
      <el-tab-pane label="" class="quanjing">
        <el-card class="box-card">
          <div>
            <div class="text"></div>
            <div class="text">
              <p></p>
              <p>{{message.name}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.companyName}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.expectPrice}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.expectDate}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.stageStr}}</p>
            </div>
            <el-divider></el-divider>
            <div class="text"></div>
            <div class="text">
              <p></p>
              <p>{{message.typeStr}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.expectDate}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.sourceName}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.nextTime}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.remarks}}</p>
            </div>
            <el-divider></el-divider>
            <div class="text"></div>
            <div class="text">
              <p></p>
              <p>{{message.userName}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.departName}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.createUser}}</p>
            </div>
            <!-- <div>
              <p></p>
              <p>{{message.oldFollowUser}}</p>
              </div>
            <div>
              <p></p>
              <p>{{message.oldFollowDepart}}</p>
            </div>-->
            <div class="text">
              <p></p>
              <p>{{message.createTime}}</p>
            </div>
            <div class="text">
              <p></p>
              <p>{{message.updateTime}}</p>
            </div>
          </div>
        </el-card>
        <follow-up-history
          class="genjin box-card"
          @writeFollow="isShowFollow= true"
          :customerId="customerId"
          :type="1"
          ref="followUpHistory"
        ></follow-up-history>
      </el-tab-pane>
      <el-tab-pane label="">
        <tab-task ref="tabTask" :customerId="customerId"></tab-task>
      </el-tab-pane>
      <el-tab-pane label="">
        <tab-annex ref="tabAnnex" :customerId="customerId"></tab-annex>
      </el-tab-pane>

      <!-- <el-tab-pane label="">
        <el-table
          :data="tableData"
          tooltip-effect="dark"
          style="width: 100%;margin-top: 20px;"
          border
          :fit="true"
        >
          <el-table-column prop="name" label="" width align="center"></el-table-column>
          <el-table-column prop="name" label="" width align="center"></el-table-column>
          <el-table-column prop="name" label="" width align="center"></el-table-column>
          <el-table-column prop="name" label="" width align="center"></el-table-column>
        </el-table>
      </el-tab-pane>-->
    </el-tabs>
    <edit-chance
      :isShowChanceEdit="isShowChanceEdit"
      :customerId="customerId"
      :opportunityId="opportunityId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-chance>
    <NewSend
      :isShowSend="isShowSend"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewSend>
    <Followup
      :isShowFollow="isShowFollow"
      :customerId="customerId"
      @sureDialogFather="$refs.followUpHistory.getFollowData()"
      @closeDialogFather="getSonCancel"
    ></Followup>
  </div>
</template>

<script>
import API from "@/api/chance";
import EditChance from "@/components/element/EditChance"; // 
import FollowUpHistory from "@/components/element/FollowUpHistory"; // 
import NewSend from "@/components/element/NewSend"; // 
import TabAnnex from "@/components/element/TabAnnex";
import TabTask from "@/components/element/TabTask";
import Followup from "@/components/element/Followup"; // 
export default {
  name: "opportunitydetails",
  components: {
    EditChance,
    NewSend,
    FollowUpHistory,
    TabTask,
    TabAnnex,
    Followup
  },
  data() {
    return {
      isShowChanceEdit: false,
      isShowSend: false,
      isShowFollow: false,
      opportunityId: "", // id
      customerId: null, // id
      message: {}, // 
      tableData: [],
      followList: [] // 
    };
  },
  methods: {
    handleClick(tab, event) {
      if (tab.label == "") {
        this.getDetailData();
      } else if (tab.label == "") {
        this.$refs.tabTask.getTaskTable();
      } else if (tab.label == "") {
        this.$refs.tabAnnex.getAnnexTable();
      } else if (tab.label == "") {
        // console.log('')
      }
    },
    // 
    deleteChance() {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: ""
      }).then(() => {
        API.postdeleteChance({ ids: this.opportunityId }).then(res => {
          this.$message({
            message: "",
            type: "success"
          });
          this.$router.go(-1);
        });
      });
    },
    // 
    showSend() {
      this.isShowSend = true;
    },
    // 
    showFollow() {
      this.isShowFollow = true;
    },
    // Dialog
    getSonSure() {
      this.isShowChanceEdit = false;
      this.isShowSend = false;
      this.isShowFollow = false;
      this.getDetailData();
    },
    // Dialog
    getSonCancel() {
      this.isShowChanceEdit = false;
      this.isShowSend = false;
      this.isShowFollow = false;
    },
    getDetailData() {
      API.getChanceDetails(this.opportunityId).then(res => {
        console.log(res.data);
        this.message = res.data;
        this.customerId = res.data.customerId;
        // this.followList = res.data.follows;
      });
    }
  },
  mounted() {
    this.opportunityId = this.$route.query.id;
    this.getDetailData();
  }
};
</script>
<style lang='scss' scoped>
.el-dropdown-link {
  cursor: pointer;
  // color: #409EFF;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.el-dro {
  margin-top: 20px;
  margin-bottom: 20px;
}

.text {
  font-size: 14px;
  line-height: 40px;
  display: flex;
  & > :first-child {
    width: 100px;
  }
  & > :last-child {
    flex: 1;
    word-break: break-all;
    word-wrap: break-word;
  }
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.box-card {
  width: 48%;
  margin-top: 20px;
}
.genjin {
  position: absolute;
  top: 0;
  right: 0;
}
.quanjing {
  position: relative;
}
span {
  font-size: 18px;
}
</style>