<template>
    <div>
        <el-dialog title="" :visible.sync="outerVisible" width="40%" @close="closeDialog">
            <el-form :model="form">
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-checkbox v-model="form.checked"></el-checkbox>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-select v-model="form.value" placeholder="">
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="" :label-width="formLabelWidth">
                    <el-input type="textarea" placeholder="" v-model="form.textarea" maxlength="280" show-word-limit :autosize="{ minRows: 4, maxRows: 12}" ></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="sureDialog" type="success"></el-button>
                <el-button @click="closeDialog"></el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
// 
export default {
    name:'newsend',
    props: {
        isShowSend:{
            type: Boolean,
            default: false
        }
    },
    data() {
        return {
            outerVisible: this.isShowSend,
            formLabelWidth: '110px',
            form: {
                checked: true,
                value: '',
                textarea: ''
            },
        };
    },
    methods: {
        // 
        closeDialog(){
            this.outerVisible = false
            this.$emit('closeDialogFather',this.outerVisible);
        },
        // 
        sureDialog(){
            this.$message({
                message: '',
                type: 'success'
            },1000);
            this.$emit('sureDialogFather');
        },
    },
    mounted() {
    },
    watch: {
        isShowSend () {
            this.outerVisible = this.isShowSend;
        }
    }
}
</script>
<style lang='scss' scoped>

</style>