<template>
  <!--  -->
  <div>
    <el-row>
      <el-col :span="24">
        <h1>{{detailData.contractName}}</h1>
      </el-col>
    </el-row>
    <el-row class="el-dro">
      <!-- <el-button
        icon="el-icon-s-operation"
        v-permi="'/payment/updateContractPaymentPlan'"
        @click="isEditReceivableplan=true"
        :disabled="detailData.isOver===''"
      ></el-button>-->
      <el-button
        type="primary"
        v-permi="'/payment/addContractPaymentPlan'"
        @click="isNewReceivablerecord=true"
      ></el-button>
    </el-row>

    <el-tabs type="card" @tab-click="handleClick">
      <el-tab-pane label="">
        <el-card class="box-card">
          <div class="text item">
            <div></div>
            <div>
              <p></p>
              <p>{{detailData.opeDate&&detailData.opeDate.slice(0,11)}}</p>
            </div>
            <div>
              <p></p>
              <p>{{detailData.price}}</p>
            </div>
            <div>
              <p></p>
              <p>{{detailData.contractName}}</p>
            </div>
            <div>
              <p></p>
              <p>{{detailData.customerName}}</p>
            </div>
            <div>
              <p></p>
              <p>{{detailData.remarks}}</p>
            </div>
            <el-divider></el-divider>
            <div></div>
            <div>
              <p></p>
              <p>{{detailData.isOver}}</p>
            </div>
            <div>
              <p></p>
              <p>{{detailData.overDays}}</p>
            </div>
            <div>
              <p></p>
              <p>{{detailData.price}}</p>
            </div>
            <div>
              <p></p>
              <p>{{detailData.notPrice}}</p>
            </div>
            <el-divider></el-divider>
            <div></div>
            <div>
              <p></p>
              <p>{{detailData.createTime}}</p>
            </div>
            <div>
              <p></p>
              <p>{{detailData.updateTime}}</p>
            </div>
            <div>
              <p></p>
              <p>{{detailData.createUserName}}</p>
            </div>
          </div>
        </el-card>
        <follow-up-history
          class="genjin"
          ref="followUpHistory"
          @writeFollow="isShowFollow=true"
          :customerId="detailData.customerId"
          :type="1"
        ></follow-up-history>
      </el-tab-pane>
      <el-tab-pane label="">
        <tab-task ref="tabTask" :customerId="detailData.customerId"></tab-task>
      </el-tab-pane>
      <el-tab-pane label="">
        <tab-annex ref="tabAnnex" :customerId="detailData.customerId"></tab-annex>
      </el-tab-pane>
    </el-tabs>

    <edit-receivableplan
      :isEditReceivableplan="isEditReceivableplan"
      @sureDialogFather="getSonSure"
      :editPlanData="detailData"
      @closeDialogFather="getSonCancel"
    ></edit-receivableplan>
    <new-receivablerecord
      :isNewReceivablerecord="isNewReceivablerecord"
      :detailData="detailData"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></new-receivablerecord>

    <Followup
      :isShowFollow="isShowFollow"
      :customerId="detailData.customerId"
      @sureDialogFather="$refs.followUpHistory.getFollowData()"
      @closeDialogFather="getSonCancel"
    ></Followup>
  </div>
</template>

<script>
// 
import API from "@/api/payment";
import * as filters from "@/filters/index";
import EditReceivableplan from "../components/EditReceivableplan"; // 
import Followup from "@/components/element/Followup"; // 
import TabAnnex from "@/components/element/TabAnnex";
import TabTask from "@/components/element/TabTask";
import FollowUpHistory from "@/components/element/FollowUpHistory"; // 
import NewReceivablerecord from "../components/NewReceivablerecord"; // 
export default {
  name: "receivablePlandetails",
  components: {
    EditReceivableplan,
    NewReceivablerecord,
    FollowUpHistory,
    Followup,
    TabTask,
    TabAnnex
  },
  data() {
    return {
      isShowFollow: false,
      followList: [],
      isEditReceivableplan: false,
      isNewReceivablerecord: false,
      detailData: {} // 
    };
  },
  methods: {
    // Dialog
    getSonSure() {
      this.getSonCancel();
      this.getDetailData();
    },
    // tab 
    handleClick(tab, event) {
      this.getDetailData();
      if (tab.label == "") {
      } else if (tab.label == "") {
        this.$refs.tabTask.getTaskTable();
      } else if (tab.label == "") {
        this.$refs.tabAnnex.getAnnexTable();
      }
    },

    // Dialog 
    getSonCancel() {
      this.isShowFollow = false;
      this.isEditReceivableplan = false;
      this.isNewReceivablerecord = false;
    },
    getDetailData() {
      API.getPaymentPlanById({ id: this.$route.query.id }).then(res => {
        let data = res.data;
        data.isOver = data.isOver == 1 ? "" : "";
        data.overDays = data.overDays || "";
        // this.followList = data.follows
        this.detailData = data;
        console.log(this.detailData);
      });
    }
  },
  mounted() {
    this.getDetailData();
  }
};
</script>
<style lang='scss' scoped>
.el-icon-arrow-down {
  font-size: 12px;
}
.el-dro {
  margin-top: 20px;
  margin-bottom: 20px;
}

.text {
  font-size: 14px;
  div {
    margin-bottom: 18px;
    display: flex;
    & > :first-child {
      width: 100px;
      & > :last-child {
        flex: 1;
        word-break: break-all;
        word-wrap: break-word;
      }
    }
  }
}

.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.box-card {
  width: 48%;
}
.genjin {
  position: absolute;
  top: 0;
  right: 0;
  width: 48%;
}
</style>