<template>
  <div>
    <el-row>
      <el-col :span="24">
        <el-col :span="4">
          <h1>{{message.name}}</h1>
        </el-col>
        <el-col :span="8">
          <h3>{{message.customerName}}</h3>
        </el-col>
      </el-col>
    </el-row>
    <el-row class="el-dro">
      <el-button
        type="success"
        @click="isShowLinkmanEdit=true"
        v-permi="'/linkman/updateLinkman'"
      ></el-button>
      <el-button type="danger" @click="deleteLinkman"></el-button>
      <el-button type="warning" @click="showSend"></el-button>
    </el-row>

    <el-tabs type="card" @tab-click="handleClick">
      <el-tab-pane label="" class="quanjing">
        <el-card class="box-card">
          <div class="text item">
            <div></div>
            <div>
              <p></p>
              <p>{{message.name}}</p>
            </div>
            <div>
              <p></p>
              <p>{{message.customerName}}</p>
            </div>
            <div>
              <p></p>
              <p>{{message.linkmanDepart}}</p>
            </div>
            <div>
              <p></p>
              <p>{{message.job}}</p>
            </div>
            <el-divider></el-divider>
            <div></div>
            <div>
              <p></p>
              <p>{{message.telephone}}</p>
            </div>
            <div>
              <p></p>
              <p>{{message.mobile}}</p>
            </div>
            <div>
              <p></p>
              <p>{{message.weChat}}</p>
            </div>
            <div>
              <p>QQ</p>
              <p>{{message.qq}}</p>
            </div>
            <div>
              <p></p>
              <p>{{message.email}}</p>
            </div>
            <div>
              <p></p>
              <p>{{message.province}}{{message.city}}</p>
            </div>
            <div>
              <p></p>
              <p>{{message.address}}</p>
            </div>
            <el-divider></el-divider>
            <div></div>
            <div>
              <p></p>
              <p>{{message.sexStr}}</p>
            </div>
            <div>
              <p></p>
              <p>{{message.birthday}}</p>
            </div>
            <div>
              <p></p>
              <p>{{message.remarks}}</p>
            </div>
            <el-divider></el-divider>
            <div></div>
            <div>
              <p></p>
              <p>{{message.name}}</p>
            </div>
            <div>
              <p></p>
              <p>{{message.createTime}}</p>
            </div>
            <div>
              <p></p>
              <p>{{message.updateTime}}</p>
            </div>
          </div>
        </el-card>
        <follow-up-history
          class="genjin box-card"
          @writeFollow="isShowFollow= true"
          :customerId="customerId"
          :type="1"
          ref="followUpHistory"
        ></follow-up-history>
      </el-tab-pane>

      <el-tab-pane label="">
        <tab-task ref="tabTask" :customerId="customerId"></tab-task>
      </el-tab-pane>

      <el-tab-pane label="">
        <tab-annex ref="tabAnnex" :customerId="customerId"></tab-annex>
      </el-tab-pane>
      <!-- <el-tab-pane label="">
        <el-table :data="tableData" tooltip-effect="dark" style="width: 100%;" border :fit="true">
          <el-table-column prop="name" label="" width align="center"></el-table-column>
          <el-table-column prop="name" label="" width align="center"></el-table-column>
          <el-table-column prop="name" label="" width align="center"></el-table-column>
          <el-table-column prop="name" label="" width align="center"></el-table-column>
        </el-table>
      </!-->
    </el-tabs>
    <edit-contacts
      :isShowLinkmanEdit="isShowLinkmanEdit"
      :linkmanId="linkmanId"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></edit-contacts>
    <NewSend
      :isShowSend="isShowSend"
      @sureDialogFather="getSonSure"
      @closeDialogFather="getSonCancel"
    ></NewSend>
    <Followup
      :isShowFollow="isShowFollow"
      :customerId="customerId"
      @sureDialogFather="$refs.followUpHistory.getFollowData();"
      @closeDialogFather="getSonCancel"
    ></Followup>
  </div>
</template>	

<script>
import API from "@/api/linkman";
import TabAnnex from "@/components/element/TabAnnex";
import TabTask from "@/components/element/TabTask";
import EditContacts from "@/components/element/EditContacts"; // 
import NewSend from "@/components/element/NewSend"; // 
import Followup from "@/components/element/Followup"; // 
import FollowUpHistory from "@/components/element/FollowUpHistory"; // 

export default {
  name: "linkmandetails",
  components: {
    EditContacts,
    TabTask,
    NewSend,
    TabAnnex,
    Followup,
    FollowUpHistory
  },
  data() {
    return {
      isShowLinkmanEdit: false,
      isShowSend: false,
      isShowFollow: false,
      linkmanId: "", // id
      customerId: null, // id
      message: {}, // 
      tableData: []
    };
  },
  methods: {
    // 
    deleteLinkman() {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: ""
      }).then(() => {
        API.postdeleteLinkman({ ids: this.linkmanId }).then(res => {
          this.$router.go(-1);
          this.$message({
            message: "",
            type: "success"
          });
        });
      });
    },
    handleUp() {},
    // 
    showSend() {
      this.isShowSend = true;
    },
    // 
    showFollow() {
      this.isShowFollow = true;
    },
    // Dialog
    getSonSure() {
      this.getSonCancel();
      this.getDetailData();
    },
    // Dialog 
    getSonCancel() {
      this.isShowLinkmanEdit = false;
      this.isShowSend = false;
      this.isShowFollow = false;
    },
    // tab 
    handleClick(tab, event) {
      if (tab.label == "") {
        this.getDetailData();
      } else if (tab.label == "") {
        this.$refs.tabTask.getTaskTable();
      } else if (tab.label == "") {
        this.$refs.tabAnnex.getAnnexTable();
      } else if (tab.label == "") {
        // console.log('')
      }
    },
    getDetailData() {
      API.getLinkmanDetails(this.linkmanId).then(res => {
        this.message = res.data;
        this.customerId = res.data.customerId;
      });
    }
  },
  mounted() {
    this.linkmanId = this.$route.query.id;
    this.getDetailData();
  }
};
</script>
<style lang='scss' scoped>
.el-dropdown-link {
  cursor: pointer;
}
.el-icon-arrow-down {
  font-size: 12px;
}
.el-dro {
  margin-top: 20px;
  margin-bottom: 20px;
}

.text {
  font-size: 14px;
  div {
    margin-bottom: 18px;
    display: flex;
    & > :first-child {
      width: 100px;
      & > :last-child {
        flex: 1;
        word-break: break-all;
        word-wrap: break-word;
      }
    }
  }
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.box-card {
  width: 48%;
}
.genjin {
  position: absolute;
  top: 0;
  right: 0;
}
.quanjing {
  position: relative;
}
span {
  font-size: 18px;
}
</style>